import { Expression, OrWrapper, AndWrapper, LogicalNode } from "./Expression";
import { HierarchyType } from "../../common";

// @ts-ignore
export type Operation = (value) => boolean;
// @ts-ignore
export type wrapperOperation = (operations: Operation[]) => (value) => boolean;

export interface HierarchyQuery {
    $ENTITYID: number[];
    $SCOPE: "Descendants" | "DescendantsAndSelf" | "Root";
    $HIERARCHYTYPE: HierarchyType;
    $UNIQUEIDENTIFIERS: string[];
    result?: number[];
}

function unwrapTopLevelOperators(object: any) {
    for (let [token, right] of Object.entries(object)) {
        token = token.startsWith("$") ? token.toUpperCase() : token;
        if (right instanceof Array) {
            switch (token) {
                case "$OR":
                    return new OrWrapper(parseArray(right));
                case "$AND":
                    return new AndWrapper(parseArray(right));
                // case "$NOT":
                default:
                    return object;
            }
        } else {
            return;
        }
    }
}

export function parse(object: any): LogicalNode {
    if (Object.values(object)[0] instanceof Array) {
        return unwrapTopLevelOperators(object);
    } else {
        return new LogicalNode(parseObject(object));
    }
}

export function parseObject(object: any) {
    const expressions: Expression[] = [];

    for (const [token, right] of Object.entries(object)) {
        const expression = parseToken(token, right);

        if (right instanceof Array) {
            const childExpressions = parseArray(right);
            expression.combineExpressions(childExpressions);
        } else if (isObject(right)) {
            const childExpressions = parseObject(right);
            expression.combineExpressions(childExpressions);
        }
        expressions.push(expression);
    }

    return expressions;
}

export function parseHierarchy(object: any) {
    let newObject = {};

    for (const key of Object.keys(object)) {
        const uppercaseKey = key.toUpperCase();
        const value = object[key];
        switch (uppercaseKey) {
            case "$UNIQUEIDENTIFIERS":
            case "$ENTITYID":
                // @ts-ignore
                newObject[uppercaseKey] = value instanceof Array ? value : [value];
                break;
            default:
                // @ts-ignore
                newObject[uppercaseKey] = value;
                break;
        }
    }

    if (!("$AND" in newObject)) {
        newObject = { $AND: [newObject] };
    }

    return newObject;
}

function parseArray(array: any[]) {
    const childExpressions = [];
    for (const item of array) {
        childExpressions.push(...parseObject(item));
    }
    return childExpressions;
}

function parseToken(token: string, right: any) {
    token = token.startsWith("$") ? token.toUpperCase() : token;

    switch (token) {
        //LOGICAL
        case "$OR":
        case "$AND":
        case "$NOT":
            return parseLogical(token, right);
        //COMPARISON
        case "$GT":
        case "$GTE":
        case "$EQ":
        case "$LT":
        case "$LTE":
            return comparison(token, right);
        //TEXT
        case "$REGEX":
        case "$TEXT":
            break;
        default:
            return parseSelector(token, right);
    }
}

function parseLogical(token: string, right: any) {
    if (right instanceof Array) {
        return new Expression({
            token,
            wrapperOperation: getLogicalWrapper(token)
        });
    } else {
        throw new Error("TODO");
    }
}

function getLogicalWrapper(key: string) {
    switch (key) {
        case "$OR":
            // @ts-ignore
            return (operations: Operation[]) => value =>
                operations.reduce((prev, op) => prev || op(value), false);
        case "$AND":
            // @ts-ignore
            return (operations: Operation[]) => value =>
                operations.reduce((prev, op) => prev && op(value), true);
    }
}

function parseSelector(token: string, right: any) {
    const operation = isPrimitive(right) ? parseSelectorPrimitive(right) : undefined;

    return new Expression({
        token,
        operation,
        isSelector: true
    });
}

function parseSelectorPrimitive(value: String | Number) {
    // @ts-ignore
    return inputValue => inputValue === value;
}

function getComparisonFunction(key: string, value: number | string) {
    switch (key) {
        case "$GT":
            // @ts-ignore
            return input => input > value;
        case "$GTE":
            // @ts-ignore
            return input => input >= value;
        case "$EQ":
            // @ts-ignore
            return input => input === value;
        case "$LT":
            // @ts-ignore
            return input => input < value;
        case "$LTE":
            // @ts-ignore
            return input => input <= value;
        default:
            throw new Error(`Missing comparison operator ${key}.`);
    }
}

function comparison(token: string, right: any) {
    if (!isNaN(right) || right instanceof Date) {
        return new Expression({
            token,
            operation: getComparisonFunction(token, right)
        });
    } else {
        throw new Error(`Comparison operators cannot be used with ${typeof right}`);
    }
}

function isPrimitive(value: any) {
    return typeof value === "string" || value instanceof String || !isNaN(value);
}

function isObject(obj: any) {
    return obj instanceof Object && Object.keys(obj).length > 0;
}
