import {
    GeometryBuilder,
    GeometryData, normalLength
} from "./GeometryBuilder";

import { Trimble } from "../../TrimBim_generated";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import {Ray, Vector3, Triangle, Frustum, Plane} from "../ThreeMath";
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { HitPoint } from "../HitPoint";
import { TrimbimGeometryType } from "../../common";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

export class MeshGeometryBuilder extends GeometryBuilder {
    geometryType = TrimbimGeometryType.TriangleMesh;
    readonly snapGeometryType = GeometryType.MESH;
    protected buffers: GeometryData[] = [];

    triangleMeshes: (n: number) => Fbs.TriangleMesh | Fbs.TriangleMesh8 | Fbs.TexturedTriangleMesh | Fbs.TexturedTriangleMesh8 = this.geometry.triangleMeshes.bind(this.geometry);

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    public getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const geometry = this.buffers[geometryIndex];

        if (!geometry.positions && buildIfEmpty) {
            const mesh = this.triangleMeshes(geometryIndex);
            this.build(mesh, geometry);
        }

        return [geometry];
    }

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.triangleMeshesLength(); i < l; i++) {
            const mesh = this.geometry.triangleMeshes(i);
            this.buffers.push(this.calculateBufferSizes(mesh));
        }
    }

    calculateBufferSizes(
        mesh:
            | Fbs.TriangleMesh
            | Fbs.TriangleMesh8
            | Fbs.TexturedTriangleMesh
            | Fbs.TexturedTriangleMesh8
    ): GeometryData {
        const indicesLength = mesh.indicesLength();

        if (indicesLength > 65535) {
            throw new Error("Indices buffer larger than 65535");
        }

        if (mesh instanceof Fbs.TriangleMesh || mesh instanceof Fbs.TriangleMesh8) {
            return {
                objectType: ObjectType.MESH,
                indicesLength: indicesLength,
                positionsLength: mesh.verticesLength() * 3,
                normalsLength: mesh.verticesLength() * 3,
                uvsLength: 0,
                pointSizesLength: 0,
            };
        } else if (
            mesh instanceof Fbs.TexturedTriangleMesh ||
            mesh instanceof Fbs.TexturedTriangleMesh8
        ) {
            return {
                objectType: ObjectType.MESH,
                indicesLength: indicesLength,
                positionsLength: mesh.verticesLength() * 3,
                normalsLength: mesh.verticesLength() * 3,
                uvsLength: indicesLength * 2,
                pointSizesLength: 0
            };
        }
    }

    /*
     * Build base meshes from meshes
     */
    build(
        mesh:
            Fbs.TriangleMesh
            | Fbs.TriangleMesh8,
        bufferGeometry: GeometryData
    ): void {
        const pos = new Fbs.Vec3f();
        const normal = new Fbs.Vec3f();
        const vertex = createIndexedVertex(this.geometryType);

        const verticesLength = mesh.verticesLength();
        const indices = mesh.indicesArray();
        const vertices = new Float32Array(mesh.verticesLength() * 3);
        const normals = new Int16Array(mesh.verticesLength() * 3);

        for (let i = 0; i < verticesLength; i++) {
            mesh.vertices(i, vertex);
            mesh.positions(vertex.positionIndex(), pos);
            mesh.normals(vertex.normalIndex(), normal);

            vertices[i * 3    ] = pos.x();
            vertices[i * 3 + 1] = pos.y();
            vertices[i * 3 + 2] = pos.z();

            normals[i * 3    ] = normal.x() * normalLength;
            normals[i * 3 + 1] = normal.y() * normalLength;
            normals[i * 3 + 2] = normal.z() * normalLength;
        }

        bufferGeometry.indices = indices;
        bufferGeometry.positions = vertices;
        bufferGeometry.normals = normals;
        bufferGeometry.uvs = new Float32Array(0);
    }

    buildTexured(
        mesh: Fbs.TexturedTriangleMesh | Fbs.TexturedTriangleMesh8,
        type: TrimbimGeometryType,
        bufferGeometry: GeometryData
    ): GeometryData {
        const pos = new Fbs.Vec3f();
        const normal = new Fbs.Vec3f();
        const uv = new Fbs.UV();
        const vertex = createIndexedVertex(type);

        const verticesLength = mesh.verticesLength();
        const indices = mesh.indicesArray();
        const vertices = new Float32Array(mesh.verticesLength() * 3);
        const normals = new Int16Array(mesh.verticesLength() * 3);
        const uvs = new Float32Array(mesh.verticesLength() * 2);

        for (let i = 0; i < verticesLength; i++) {
            mesh.vertices(i, vertex);
            mesh.positions(vertex.positionIndex(), pos);
            mesh.normals(vertex.normalIndex(), normal);
            mesh.uvs(vertex.positionIndex(), uv);

            vertices[i * 3] = pos.x();
            vertices[i * 3 + 1] = pos.y();
            vertices[i * 3 + 2] = pos.z();

            normals[i * 3] = normal.x() * normalLength;
            normals[i * 3 + 1] = normal.y() * normalLength;
            normals[i * 3 + 2] = normal.z() * normalLength;

            uvs[i * 2] = uv.u();
            uvs[i * 2 + 1] = uv.v();
        }

        bufferGeometry.indices = indices;
        bufferGeometry.positions = vertices;
        bufferGeometry.normals = normals;
        bufferGeometry.uvs = uvs;

        return bufferGeometry;
    }

    getVertex = (() => {
        const pos = new Fbs.Vec3f();
        let vertex: Fbs.IndexedVertex;

        return (mesh: Fbs.TexturedTriangleMesh | Fbs.TexturedTriangleMesh8 | Fbs.TriangleMesh | Fbs.TriangleMesh8, type: TrimbimGeometryType, i: number, out: Vector3): void => {
            const index = mesh.indices(i);
            if (!vertex) vertex = createIndexedVertex(type);
            mesh.vertices(index, vertex);
            mesh.positions(vertex.positionIndex(), pos);
            out.set(pos.x(), pos.y(), pos.z());
        };
    })();

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickFace(geometryIndex, ray);
    }

    pickFace = (() => {
        const vecA = new Vector3();
        const vecB = new Vector3();
        const vecC = new Vector3();
        const triangle = new Triangle();

        const target = new Vector3();

        return (meshIndex: number, ray: Ray): HitPoint[] => {
            const mesh = getMesh(meshIndex, this.geometryType, this.geometry);
            const indicesLength = mesh.indicesLength();
            const result: HitPoint[] = [];

            for (let i = 0; i < indicesLength; i += 3) {
                this.getVertex(mesh, this.geometryType, i, vecA);
                this.getVertex(mesh, this.geometryType, i + 1, vecB);
                this.getVertex(mesh, this.geometryType, i + 2, vecC);

                if (ray.intersectTriangle(vecA, vecB, vecC, false, target)) {
                    triangle.set(vecA, vecB, vecC);
                    result.push({
                        point: target.clone(),
                        normal: triangle.getNormal(new Vector3())
                    });
                }
            }
            return result;
        };
    })();

    pickLine = (() => {
        const vecA = new Vector3();
        const vecB = new Vector3();
        const vecC = new Vector3();
        const point = new Vector3();
        const plane = new Plane();

        return (meshIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            const mesh = getMesh(meshIndex, this.geometryType, this.geometry);
            const indicesLength = mesh.indicesLength();

            const result: HitPoint[] = [];

            const addPoint = (v1: Vector3, v2: Vector3, normal: Vector3): void => {
                if (frustum.containsPoint(point))
                    result.push({point: point.clone(), normal: normal.clone(), snapLineStart: v1.clone(), snapLineEnd: v2.clone()})
            };

            for (let i = 0; i < indicesLength; i += 3) {
                this.getVertex(mesh, this.geometryType, i, vecA);
                this.getVertex(mesh, this.geometryType, i + 1, vecB);
                this.getVertex(mesh, this.geometryType, i + 2, vecC);
                plane.setFromCoplanarPoints(vecA, vecB, vecC);

                ray.distanceSqToSegment(vecA, vecB, undefined, point); addPoint(vecA, vecB, plane.normal);
                ray.distanceSqToSegment(vecA, vecC, undefined, point); addPoint(vecA, vecC, plane.normal);
                ray.distanceSqToSegment(vecB, vecC, undefined, point); addPoint(vecB, vecC, plane.normal);
            }

            return result;
        };
    })();

    pickPoint = (() => {
        const pos = new Fbs.Vec3f();
        const position = new Vector3();

        return (meshIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            const vertex = createIndexedVertex(this.geometryType);
            const mesh = getMesh(meshIndex, this.geometryType, this.geometry);

            const result: HitPoint[] = [];
            for (let i = 0; i < mesh.verticesLength(); i++) {
                mesh.vertices(i, vertex);
                mesh.positions(vertex.positionIndex(), pos);
                position.set(pos.x(), pos.y(), pos.z());

                if (frustum.containsPoint(position))
                    result.push({point: position.clone()})
            }

            return result;
        };
    })();
}

function createIndexedVertex(type: TrimbimGeometryType): Fbs.IndexedVertex {
    if (type === TrimbimGeometryType.TriangleMesh8 || type === TrimbimGeometryType.TexturedTriangleMesh8) {
        return new Fbs.IndexedVertex8();
    } else if (type === TrimbimGeometryType.TriangleMesh || type === TrimbimGeometryType.TexturedTriangleMesh) {
        return new Fbs.IndexedVertex();
    } else {
        throw new Error("Unkown index type for mesh");
    }
}

export function getMesh(idx: number, type: TrimbimGeometryType, geometry: Fbs.ModelGeometry): Fbs.TexturedTriangleMesh | Fbs.TexturedTriangleMesh8 | Fbs.TriangleMesh | Fbs.TriangleMesh8 {
    if (type === TrimbimGeometryType.TriangleMesh8) {
        return geometry.triangleMeshes8(idx);
    } else if (type === TrimbimGeometryType.TriangleMesh) {
        return geometry.triangleMeshes(idx);
    } else if (type === TrimbimGeometryType.TexturedTriangleMesh8) {
        return geometry.texturedTriangleMeshes8(idx);
    } else if (type === TrimbimGeometryType.TexturedTriangleMesh) {
        return geometry.texturedTriangleMeshes(idx);
    } else {
        throw new Error(`Unknown Mesh type: ${type}`);
    }
}

export class Mesh8GeometryBuilder extends MeshGeometryBuilder {
    readonly geometryType = TrimbimGeometryType.TriangleMesh8;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.triangleMeshes = geometry.triangleMeshes8.bind(geometry);
        this.prepareModelTypes();
    }

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.triangleMeshes8Length(); i < l; i++) {
            const mesh = this.geometry.triangleMeshes8(i);
            this.buffers.push(this.calculateBufferSizes(mesh));
        }
    }
}

export class MeshTexturedGeometryBuilder extends MeshGeometryBuilder {
    readonly geometryType = TrimbimGeometryType.TexturedTriangleMesh;
    readonly producedType = ObjectType.MESH;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.triangleMeshes = geometry.texturedTriangleMeshes.bind(geometry);
        this.prepareModelTypes();
    }

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.texturedTriangleMeshesLength(); i < l; i++) {
            const mesh = this.geometry.texturedTriangleMeshes(i);
            this.buffers.push(
                this.calculateBufferSizes(mesh)
            );
        }
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const geometry = this.buffers[geometryIndex];
        if (!geometry.positions && buildIfEmpty) {
            const mesh = this.triangleMeshes(geometryIndex) as Fbs.TexturedTriangleMesh;
            this.buffers[geometryIndex] = this.buildTexured(mesh, this.geometryType, geometry);
        }
        return [geometry];
    }
}

export class Mesh8TexturedGeometryBuilder extends MeshGeometryBuilder {
    readonly geometryType = TrimbimGeometryType.TexturedTriangleMesh8;
    readonly producedType = ObjectType.MESH;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.triangleMeshes = geometry.texturedTriangleMeshes8.bind(geometry);
        this.prepareModelTypes();
    }

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.texturedTriangleMeshes8Length(); i < l; i++) {
            const mesh = this.geometry.texturedTriangleMeshes8(i);
            this.buffers.push(this.calculateBufferSizes(mesh));
        }
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        let object: GeometryData = this.buffers[geometryIndex];
        if (!object.positions && buildIfEmpty) {
            const mesh = this.triangleMeshes(geometryIndex) as Fbs.TexturedTriangleMesh8;
            this.buffers[geometryIndex] = this.buildTexured(mesh, this.geometryType, object);
        }
        return [object];
    }
}
