import { ClusterManager } from "../ClusterManager";
import { InstanceManager } from "../InstanceManager";
import { GeometryManager } from "../GeometryManager";

import { Trimble } from "../../TrimBim_generated";
import { Box3, Frustum, Vector3 } from "../ThreeMath";
import { ActionType } from "../../Model/ModelBuilderTypes";
import { LodManager } from "../LodManager";

export class ModelBuilderWorker {
    private geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry;
    private enabled = true;

    constructor(
        private modelId: string,
        private cm: ClusterManager,
        private im: InstanceManager,
        private geom: GeometryManager,
        private lodManager: LodManager,
        public model: Trimble.Technology.TrimBim.Fbs.Model
    ) {
        this.geometry = model.geometry();
        this.startGeometryUpdateLoop();
    }

    public buildGeometryForCamera(frustum: Frustum, position: Vector3): void {
        this.setCameraForGeometryUpdate(frustum, position);
    }

    private cameraFrustum: Frustum;
    private cameraPosition: Vector3;
    private clustersProcessedForCurrentCamera: number = 0;
    private updateFinishedSent: boolean = false;

    private startGeometryUpdateLoop(): void {
        const aabb = new Box3();
        let index = 0;

        // use setTimeout instead of normal loop to allow messages to be received in the middle
        const processClusters = () => {
            if (this.clustersProcessedForCurrentCamera < this.cm.clusterLUT.size && this.cameraPosition) {
                const clusters = Array.from(this.cm.clusterLUT.values());
                const start = performance.now();
                for (let i = 0; i < clusters.length && (performance.now() - start < 4 || i < 5); i++) {
                    const cluster = clusters[index];
                    aabb.min.set(cluster.min[0], cluster.min[1], cluster.min[2]);
                    aabb.max.set(cluster.max[0], cluster.max[1], cluster.max[2]);
                    if (this.cameraFrustum.intersectsBox(aabb))
                        cluster.buildGeometry(this.getLodLevel(this.cameraPosition, aabb));
                    else
                        cluster.removeGeometry();
                    index++;
                    if (index >= clusters.length) index = 0;
                    this.clustersProcessedForCurrentCamera++;
                }
            }
            if (this.clustersProcessedForCurrentCamera >= this.cm.clusterLUT.size && !this.updateFinishedSent) {
                this.cm.messenger.send({actionType: ActionType.UPDATE_FINISHED, modelId: this.modelId});
                this.updateFinishedSent = true;
            }
            if (this.enabled)
                setTimeout(processClusters, this.clustersProcessedForCurrentCamera < this.cm.clusterLUT.size ? 0 : 100);
        };
        processClusters();
    }

    forceUpdate(): void {
        this.updateFinishedSent = false;
        this.clustersProcessedForCurrentCamera = 0;
        this.cm.messenger.removePendingUpdateFinishedAction();
    }

    dispose(): void {
        this.enabled = false;
    }

    private setCameraForGeometryUpdate(frustum: Frustum, position: Vector3): void {
        this.cameraFrustum = frustum;
        this.cameraPosition = position;
        this.updateFinishedSent = false;
        this.clustersProcessedForCurrentCamera = 0;
        this.cm.messenger.removePendingUpdateFinishedAction();
    }

    private getLodLevel(loc: Vector3, aabb: Box3): number {
        const distance = aabb.distanceToPoint(loc);
        return this.lodManager.getLevel(distance);
    }
}
