import { GeometryData } from "./GeometryBuilders/GeometryBuilder";
import incrementBatchCount from "./BatchCounter";
import {
    ActionType,
    BatchState,
    IAdd,
    IRemove,
    ObjectType
} from "../Model/ModelBuilderTypes";
import {Box3, Matrix4, TypedArray} from "./ThreeMath";
import { InstanceManager } from "./InstanceManager";
import { GeometryManager } from "./GeometryManager";
import { LodManager } from "./LodManager";
import { GeometryUpdateMessenger } from "./GeometryUpdateMessenger";
import { IText } from "../common";

const placement = new Matrix4();

export class BatchCluster {
    public min: Float64Array;
    public max: Float64Array;

    constructor(protected modelId: string, public id: Int32Array) {
    }

    protected createLocalBoundingBox(): Box3 {
        const boundingBox = new Box3();
        boundingBox.max.set(this.max[0] - this.min[0], this.max[1] - this.min[1], this.max[2] - this.min[2]);
        return boundingBox;
    }

    static createBatchPrototype(geometry: GeometryData, instanceId: number): BatchPrototype {
        return {
            indicesLength: geometry.indicesLength,
            verticesLength: geometry.positionsLength,
            normalsLength: geometry.normalsLength,
            uvsLength: geometry.uvsLength,
            pointSizesLength: geometry.pointSizesLength,
            instances: [instanceId],
            indexOffset: [0],
            indexBuffer32bit: geometry.positionsLength > 65535,
            id: incrementBatchCount(),
            geometryBuilt: false,
            state: {
                visible: [{ start: 0, count: Infinity }],
                selected: []
            } as BatchState,
            text: geometry.text,
        };
    }

    static addToPrototype(prototype: BatchPrototype, geometry: GeometryData, instanceId: number): void {
        const offset = prototype.indicesLength !== 0 ? prototype.indicesLength : (prototype.verticesLength / 3);
        prototype.indexOffset.push(offset);
        prototype.indicesLength += geometry.indicesLength;
        prototype.verticesLength += geometry.positionsLength;
        prototype.normalsLength += geometry.normalsLength;
        prototype.uvsLength += geometry.uvsLength;
        prototype.pointSizesLength += geometry.pointSizesLength;
        if (!prototype.instances.includes(instanceId))
            prototype.instances.push(instanceId);
    }

    static addObjectToFreePrototype(
        prototypes: BatchPrototype[],
        geometry: GeometryData,
        instanceId: number
    ): BatchPrototype {
        let prototype = this.findFreeBatchPrototype(prototypes, geometry);

        if (prototype) {
            BatchCluster.addToPrototype(prototype, geometry, instanceId);
        } else {
            prototype = BatchCluster.createBatchPrototype(geometry, instanceId);
            prototypes.push(prototype);
        }

        return prototype;
    }

    static findFreeBatchPrototype(prototypeBatches: BatchPrototype[], geometry: GeometryData): BatchPrototype {
        for (let i = prototypeBatches.length - 1; i >= 0; i--) {
            const batch = prototypeBatches[i];
            const newVertexLength = batch.verticesLength + geometry.positionsLength;
            const maxVertexLength = batch.indexBuffer32bit ? 2147483647 : 65535;
            if (newVertexLength <= maxVertexLength)
                return prototypeBatches[i];
        }
    }
}

export interface BatchPrototype {
    indicesLength: number;
    verticesLength: number;
    normalsLength: number;
    uvsLength: number;
    pointSizesLength: number;
    instances: number[];
    indexOffset: number[];
    indexBuffer32bit: boolean;
    id: number;
    geometryBuilt: boolean;
    state: BatchState;
    text?: IText;
}

export class GeometryCluster extends BatchCluster {
    protected lodLevelToBatches: Map<number, BatchPrototype[]>;

    constructor(
        modelId: string,
        public id: Int32Array,
        protected im: InstanceManager,
        protected geom: GeometryManager,
        protected messenger: GeometryUpdateMessenger,
        protected lodManager: LodManager
    ) {
        super(modelId, id);
        this.lodLevelToBatches = new Map();
        this.lodLevelToBatches.set(0, []);
    }

    get materialId(): number {
        return this.id[3];
    }

    get type(): ObjectType {
        return this.id[4];
    }

    get textChecksum(): number {
        return this.id[5];
    }

    public belongsToCluster(geometry: GeometryData): boolean {
        if (geometry.objectType !== this.type) return false;
        if (!this.textChecksum && !geometry.text) return true;
        if (!this.textChecksum || !geometry.text) return false;
        return this.textChecksum === geometry.text.checksum;
    }

    getBatches(lodLevel: number): BatchPrototype[] {
        if (this.lodLevelToBatches.has(lodLevel)) {
            return this.lodLevelToBatches.get(lodLevel);
        } else {
            const batches: BatchPrototype[] = [];
            this.lodLevelToBatches.set(lodLevel, batches);
            return batches;
        }
    }

    getInstanceIds(): number[] {
        let instanceIds: number[] = [];
        for (const oldBatches of this.lodLevelToBatches.values()) {
            for (const batch of oldBatches) {
                instanceIds = instanceIds.concat(batch.instances);
            }
        }
        return instanceIds;
    }

    removeGeometry(): void {
        for (const batches of this.lodLevelToBatches.values()) {
            for (const batch of batches)
                this.removeBatchGeometry(batch);
        }
    }

    protected removeBatchGeometry(prototype: BatchPrototype): void {
        if (!prototype.geometryBuilt) return;

        const action = {
            actionType: ActionType.REMOVAL,
            modelId: this.modelId,
            batchId: prototype.id,
            userData: { id: prototype.id }
        } as IRemove;
        this.messenger.send(action);
        prototype.geometryBuilt = false;
    }

    insertObject(
        geometry: GeometryData,
        instanceId: number,
        objectSize: number,
    ): BatchPrototype {
        for (let i = this.lodManager.getLevelsCount() - 1; i >= 0; i--) {
            if (objectSize >= this.lodManager.getMinObjectSize(i)) {
                const prototypes = this.getBatches(i);
                return BatchCluster.addObjectToFreePrototype(prototypes, geometry, instanceId);
            }
        }
    }

    clearBatches(): void {
        this.lodLevelToBatches.clear();
        this.lodLevelToBatches.set(0, []);
    }

    buildGeometry(lodLevel: number): void {
        const lodCount = this.lodManager.getLevelsCount();

        for (let i = 0; i < lodCount; i++) {
            const newBatches = this.getBatches(i);

            if (i >= lodLevel) {
                for (const prototype of newBatches) {
                    if (prototype.geometryBuilt || prototype.instances.length === 0)
                        continue;

                    const geometry = this.buildBatchGeometry(prototype, this.type, this.min);
                    const action = {
                        actionType: ActionType.ADD,
                        modelId: this.modelId,              // What's this?
                        objectType: geometry.objectType,
                        batchId: prototype.id,
                        indices: geometry.indices,
                        positions: geometry.positions,
                        normals: geometry.normals,
                        uvs: geometry.uvs,
                        pointSizes: geometry.pointSizes,
                        userData: {
                            id: prototype.id,
                            clusterId: this.id.toString(),
                        },
                        materialId: this.materialId,
                        position: this.min,
                        state: prototype.state,
                        boundingBox: this.createLocalBoundingBox(),
                        text: geometry.text,
                    } as IAdd;
                    this.messenger.send(action);
                    prototype.geometryBuilt = true;
                }
            } else {
                for (const prototype of newBatches)
                    this.removeBatchGeometry(prototype);
            }
        }
    }

    protected buildBatchGeometry(prototype: BatchPrototype, objectType: ObjectType, offset: Float64Array): GeometryData {

        const geometry: GeometryData = {
            objectType: objectType,
            indices: prototype.indexBuffer32bit ? new Uint32Array(prototype.indicesLength) : new Uint16Array(prototype.indicesLength),
            positions: new Float32Array(prototype.verticesLength),
            normals: new Int16Array(prototype.normalsLength),
            uvs: new Float32Array(prototype.uvsLength),
            pointSizes: new Float32Array(prototype.pointSizesLength),
            indexPointer: 0,
            positionPointer: 0,
            normalPointer: 0,
            uvPointer: 0,
            pointSizePointer: 0,
            indicesLength: prototype.indicesLength,
            positionsLength: prototype.verticesLength,
            normalsLength: prototype.normalsLength,
            uvsLength: prototype.uvsLength,
            pointSizesLength: prototype.pointSizesLength,
            text: prototype.text,
        };

        for (const id of prototype.instances) {
            this.im.getInstanceTransform(id, placement);
            placement.elements[12] -= offset[0];
            placement.elements[13] -= offset[1];
            placement.elements[14] -= offset[2];

            const geometries = this.geom.getGeometries(id, true);
            for (const newGeometry of geometries) {
                if (this.belongsToCluster(newGeometry))
                    this.mergeGeometry(geometry, placement, newGeometry);
            }
        }

        return geometry;
    }

    protected mergeGeometry(
        batch: GeometryData,
        instancePlacement: Matrix4,
        instanceGeometry: GeometryData
    ): void {
        const start = batch.indexPointer;
        const offset = batch.positionPointer / 3;
        const length = instanceGeometry.indicesLength;

        for (let i = 0, j = start; i < length; i++, j++) {
            batch.indices[j] = instanceGeometry.indices[i] + offset;
        }

        affineTransformVector3(
            instancePlacement,
            instanceGeometry.positions,
            batch.positions,
            batch.positionPointer,
            instanceGeometry.positions.length
        );

        if (instanceGeometry.normals) {
            applyRotationAndScaleToVector3(
                instancePlacement,
                instanceGeometry.normals,
                batch.normals,
                batch.normalPointer,
                instanceGeometry.normalsLength
            );
        }

        batch.indexPointer += instanceGeometry.indicesLength;
        batch.positionPointer += instanceGeometry.positionsLength;
        batch.normalPointer += instanceGeometry.normalsLength;

        if (instanceGeometry.uvs) {
            batch.uvs.set(instanceGeometry.uvs, batch.uvPointer);
            batch.uvPointer += instanceGeometry.uvs.length;
        }
        if (instanceGeometry.pointSizes) {
            batch.pointSizes.set(instanceGeometry.pointSizes, batch.pointSizePointer);
            batch.pointSizePointer += instanceGeometry.pointSizes.length;
        }

        if (instanceGeometry.materialId) batch.materialId = instanceGeometry.materialId;
        batch.text = instanceGeometry.text;
    }
}

function affineTransformVector3( matrix: Matrix4, source: TypedArray, dst: TypedArray, dstOffset: number = 0, length: number ): void {
    const e = matrix.elements;
    for ( let i = 0; i < length; i += 3) {
        const x = source[ i ];
        const y = source[ i + 1 ];
        const z = source[ i + 2 ];
        dst[ i + dstOffset] =      ( e[ 0 ] * x + e[ 4 ] * y + e[ 8  ] * z + e[ 12 ] );
        dst[ i + dstOffset + 1 ] = ( e[ 1 ] * x + e[ 5 ] * y + e[ 9  ] * z + e[ 13 ] );
        dst[ i + dstOffset + 2 ] = ( e[ 2 ] * x + e[ 6 ] * y + e[ 10 ] * z + e[ 14 ] );
    }
}

function applyRotationAndScaleToVector3( matrix: Matrix4, source: TypedArray, dst: TypedArray, dstOffset: number = 0, length: number ): void {
    const e = matrix.elements;
    for ( let i = 0; i < length; i += 3) {
        const x = source[ i ];
        const y = source[ i + 1 ];
        const z = source[ i + 2 ];
        dst[ i + dstOffset ] =     ( e[ 0 ] * x + e[ 4 ] * y + e[ 8 ]  * z);
        dst[ i + dstOffset + 1 ] = ( e[ 1 ] * x + e[ 5 ] * y + e[ 9 ]  * z);
        dst[ i + dstOffset + 2 ] = ( e[ 2 ] * x + e[ 6 ] * y + e[ 10 ] * z);
    }
}

export class PointCluster extends GeometryCluster {
    insertObject(
        geometry: GeometryData,
        instanceId: number,
        objectSize: number,
    ): BatchPrototype {
        const prototypes = this.getBatches(2);
        return BatchCluster.addObjectToFreePrototype(prototypes, geometry, instanceId);
    }
}
