import { GeometryData, GeometryBuilder, } from "./GeometryBuilder";
import { HitPoint } from "../HitPoint";
import { Trimble } from "../../TrimBim_generated";
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { TextStyle, TrimbimGeometryType } from "../../common";
import { Frustum, Ray, Vector3Const } from "../ThreeMath";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { createIText } from "../Utils";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

abstract class BillboardGeometryBuilder extends GeometryBuilder{
    abstract readonly geometryType: TrimbimGeometryType;

    abstract build(
        point: Fbs.TexturedBillboard | Fbs.BillboardText,
        bufferGeometry: GeometryData
    ): GeometryData;

    abstract calculateBufferSizes(mesh: any, producedType: ObjectType): GeometryData;

    abstract getGeometries(geometryIndex: number, buildIfEmpty: boolean): GeometryData[];

    prepareModelTypes(): void {
    }

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickPoint(geometryIndex, ray, frustum);
    }

    pickFace(geometryIndex: number, ray: Ray): HitPoint[] {
        return;
    }

    pickLine(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return;
    }

    pickPoint(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return frustum.containsPoint(Vector3Const.zero) ? [{point: Vector3Const.zero.clone()}] : undefined;
    }
}


export class TexturedBillboardGeometryBuilder extends BillboardGeometryBuilder {
    readonly geometryType = TrimbimGeometryType.TexturedBillboard;
    readonly snapGeometryType = GeometryType.POINT;

    build(
        point: Fbs.TexturedBillboard,
        bufferGeometry: GeometryData
    ): GeometryData {
        bufferGeometry.positions = new Float32Array(bufferGeometry.positionsLength);
        bufferGeometry.uvs = new Float32Array(bufferGeometry.uvsLength);
        bufferGeometry.pointSizes = new Float32Array(bufferGeometry.pointSizesLength);

        bufferGeometry.uvs[0] = point.leftUpUV().x();
        bufferGeometry.uvs[1] = point.leftUpUV().y();
        bufferGeometry.uvs[2] = point.rightDownUV().x();
        bufferGeometry.uvs[3] = point.rightDownUV().y();

        bufferGeometry.pointSizes[0] = point.leftUpXY().x();
        bufferGeometry.pointSizes[1] = point.leftUpXY().y();
        bufferGeometry.pointSizes[2] = point.rightDownXY().x();
        bufferGeometry.pointSizes[3] = point.rightDownXY().y();

        return bufferGeometry;
    }

    calculateBufferSizes(mesh: any, objectType: ObjectType): GeometryData {
        return {
            objectType: objectType,
            indicesLength: 0,
            positionsLength: 3,
            normalsLength: 0,
            uvsLength: 4,
            pointSizesLength: 4,
            indexPointer: 0,
            positionPointer: 0
        };
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const billboard = this.geometry.texturedBillboards(geometryIndex);
        const producedType = billboard.IsInWorldUnits() ? ObjectType.ATTENUATED_POINT : ObjectType.POINT;

        const geometry = this.calculateBufferSizes(billboard, producedType);
        if (buildIfEmpty)
            this.build(billboard, geometry);

        return [geometry];
    }

}

export class BillboardTextGeometryBuilder extends BillboardGeometryBuilder {
    readonly geometryType = TrimbimGeometryType.BillboardText;

    build(point: Fbs.BillboardText, bufferGeometry: GeometryData): GeometryData {
        bufferGeometry.positions = new Float32Array(bufferGeometry.positionsLength);
        bufferGeometry.uvs = new Float32Array(bufferGeometry.uvsLength);

        bufferGeometry.uvs[0] = 0;
        bufferGeometry.uvs[1] = 0;
        bufferGeometry.uvs[2] = 1;
        bufferGeometry.uvs[3] = 1;

        return bufferGeometry;
    }

    calculateBufferSizes(mesh: any, objectType: ObjectType): GeometryData {
        return {
            objectType: objectType,
            indicesLength: 0,
            positionsLength: 3,
            normalsLength: 0,
            uvsLength: 4,
            pointSizesLength: 0,
            indexPointer: 0,
            positionPointer: 0
        };
    }

    getGeometries = (() => {
        const trbTextStyle = new Fbs.TextStyle();

        return (geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] => {
            const billboardText = this.geometry.billboardTexts(geometryIndex);
            const producedType = ObjectType.POINT;

            const geometry = this.calculateBufferSizes(billboardText, producedType);

            // Text and textStyle have to be defined already to avoid consolidation of different
            // BillboardTexts with the same material:
            const textStyleId = billboardText.textStyleId();
            this.geometry.textStyles(textStyleId, trbTextStyle);
            const textStyle = {
                font: trbTextStyle.font() || "sans-serif",
                size: trbTextStyle.size() || 14,
                alignment: trbTextStyle.alignment() || Fbs.AlignmentType.LeftTop,
                horizontalAlign: trbTextStyle.horizontalAlign() || 0,
                verticalAlign: trbTextStyle.verticalAlign() || 0,
            } as TextStyle;
            geometry.text = createIText(billboardText.text(), textStyle);

            if (buildIfEmpty)
                this.build(billboardText, geometry);

            return [geometry];
        }
    })();

}
