import { Trimble } from "../TrimBim_generated";
import { Box3, Vector3, Matrix4 } from "./ThreeMath";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { LogicalNode } from "./QueryHandling/Expression";

export class InstanceManager {
    entityToInstance: Map<number, number[]>;
    boundingBoxes: Float64Array;
    geometry: Fbs.ModelGeometry;

    worldBoundingbox: Box3;

    constructor(private _model: Fbs.Model) {
        this.geometry = _model.geometry();
        this.entityToInstance = new Map();
        this.analyzeInstances();
    }

    private analyzeInstances(): void {
        this.worldBoundingbox = new Box3();
        this.boundingBoxes = new Float64Array(this.geometry.instancesLength() * 6);

        const instancesLength = this.geometry.instancesLength();
        const instance = new Fbs.GeometryInstance();
        const definition = new Fbs.GeometryDefinition();

        for (let i = 0; i < instancesLength; i++) {
            this.geometry.instances(i, instance);
            this.geometry.definitions(instance.definitionId(), definition);

            const entityId = instance.entityId();

            this.entityToInstance.has(entityId)
                ? this.entityToInstance.get(entityId).push(i)
                : this.entityToInstance.set(entityId, [i]);

            this.getInstanceAABB(i, this.boundingBoxes, i * 6);
            this.updateWorldBoundingBox(i, this.boundingBoxes, i * 6);
        }
    }

    public updateWorldBoundingBox(i: number, array: Float64Array, offset: number = 0): void {
        if (i === 0) {
            this.worldBoundingbox.min.x = array[offset];
            this.worldBoundingbox.min.y = array[offset + 1];
            this.worldBoundingbox.min.z = array[offset + 2];

            this.worldBoundingbox.max.x = array[offset + 3];
            this.worldBoundingbox.max.y = array[offset + 4];
            this.worldBoundingbox.max.z = array[offset + 5];
        } else {
            this.worldBoundingbox.min.x =
                this.worldBoundingbox.min.x < array[offset] ? this.worldBoundingbox.min.x : array[offset];
            this.worldBoundingbox.min.y =
                this.worldBoundingbox.min.y < array[offset + 1]
                    ? this.worldBoundingbox.min.y
                    : array[offset + 1];
            this.worldBoundingbox.min.z =
                this.worldBoundingbox.min.z < array[offset + 2]
                    ? this.worldBoundingbox.min.z
                    : array[offset + 2];

            this.worldBoundingbox.max.x =
                this.worldBoundingbox.max.x > array[offset + 3]
                    ? this.worldBoundingbox.max.x
                    : array[offset + 3];
            this.worldBoundingbox.max.y =
                this.worldBoundingbox.max.y > array[offset + 4]
                    ? this.worldBoundingbox.max.y
                    : array[offset + 4];
            this.worldBoundingbox.max.z =
                this.worldBoundingbox.max.z > array[offset + 5]
                    ? this.worldBoundingbox.max.z
                    : array[offset + 5];
        }
    }

    getAABBFromIds = (() => {
        const box2 = new Box3();
        const tmp: any = new Float64Array(6);

        return (entityIds: number[], box: Box3): void => {
            entityIds.forEach(entityId => {
                if (this.entityToInstance.has(entityId)) {
                    this.entityToInstance.get(entityId).forEach(instanceId => {
                        this.getInstanceAABB(instanceId, tmp);
                        box2.min.fromArray(tmp, 0);
                        box2.max.fromArray(tmp, 3);

                        box.union(box2);
                    });
                }
            });
        };
    })();

    getInstanceAABB = (() => {
        const xAxis = new Vector3();
        const yAxis = new Vector3();
        const zAxis = new Vector3();

        let xMax: Vector3;
        const xMin = new Vector3();
        const yMin = new Vector3();
        let yMax: Vector3;
        const zMin = new Vector3();
        let zMax: Vector3;

        let min: Vector3;
        let max: Vector3;

        const instance = new Fbs.GeometryInstance();
        const definition = new Fbs.GeometryDefinition();
        const aabb = new Fbs.AABB3f();
        const aabbMin = new Fbs.Vec3f();
        const aabbMax = new Fbs.Vec3f();

        const placement = new Matrix4();

        return (i: number, array: Float64Array, offset = 0) => {
            this.geometry.instances(i, instance);
            this.geometry.definitions(instance.definitionId(), definition);
            definition.boundingBox(aabb);
            aabb.min(aabbMin);
            aabb.max(aabbMax);

            this.getInstanceTransform(i, placement);

            xAxis.set(placement.elements[0], placement.elements[1], placement.elements[2]);

            yAxis.set(placement.elements[4], placement.elements[5], placement.elements[6]);

            zAxis.set(placement.elements[8], placement.elements[9], placement.elements[10]);

            xMin.copy(xAxis).multiplyScalar(aabbMin.x());
            xMax = xAxis.multiplyScalar(aabbMax.x());

            yMin.copy(yAxis).multiplyScalar(aabbMin.y());
            yMax = yAxis.multiplyScalar(aabbMax.y());

            zMin.copy(zAxis).multiplyScalar(aabbMin.z());
            zMax = zAxis.multiplyScalar(aabbMax.z());

            min = xMin
                .clone()
                .min(xMax)
                .add(yMin.clone().min(yMax))
                .add(zMin.clone().min(zMax));
            max = xMin
                .max(xMax)
                .add(yMin.max(yMax))
                .add(zMin.max(zMax));

            array[offset] = min.x + placement.elements[12];
            array[offset + 1] = min.y + placement.elements[13];
            array[offset + 2] = min.z + placement.elements[14];

            array[offset + 3] = max.x + placement.elements[12];
            array[offset + 4] = max.y + placement.elements[13];
            array[offset + 5] = max.z + placement.elements[14];
        };
    })();

    getInstanceTransform = (() => {
        const instance = new Fbs.GeometryInstance();

        return (instanceId: number, transform: Matrix4) => {
            this.geometry.instances(instanceId, instance);
            return this.getTransform(instance, transform)
        }
    })();

    private toMatrix4 = (() => {
        const origin = new Vector3();
        const xAxis = new Vector3();
        const yAxis = new Vector3();
        const zAxis = new Vector3();

        const vec3d = new Fbs.Vec3d();
        const vec3f = new Fbs.Vec3f();

        return (localPlacement: Fbs.Placement3, out: Matrix4) => {
            localPlacement.origin(vec3d);
            origin.set(vec3d.x(), vec3d.y(), vec3d.z());
            localPlacement.xAxis(vec3f);
            xAxis.set(vec3f.x(), vec3f.y(), vec3f.z());
            localPlacement.yAxis(vec3f);
            yAxis.set(vec3f.x(), vec3f.y(), vec3f.z());
            zAxis.crossVectors(xAxis, yAxis);

            out.set(
                xAxis.x,
                yAxis.x,
                zAxis.x,
                origin.x,
                xAxis.y,
                yAxis.y,
                zAxis.y,
                origin.y,
                xAxis.z,
                yAxis.z,
                zAxis.z,
                origin.z,
                0,
                0,
                0,
                1
            );
        };
    })();

    getTransform = (() => {
        const local = new Matrix4();
        const global = new Matrix4();

        const localPlacement = new Fbs.Placement3();
        const globalPlacement = new Fbs.Placement3();
        const entity = new Fbs.Entity();

        return (instance: Fbs.GeometryInstance, transform: Matrix4) => {
            this.geometry.localPlacements(instance.localPlacementId(), localPlacement);
            this.toMatrix4(localPlacement, local);

            this._model.entities().entities(instance.entityId(), entity);
            entity.globalPlacement(globalPlacement);
            this.toMatrix4(globalPlacement, global);

            transform.multiplyMatrices(global, local);
        };
    })();

    filterDefinition(entityIds: Set<number>, node: LogicalNode): number[] {
        const instance = new Fbs.GeometryInstance();
        const definition = new Fbs.GeometryDefinition();
        const result: number[] = [];

        for (const entityId of entityIds) {
            const instanceIds = this.entityToInstance.get(entityId);
            if (instanceIds && instanceIds.length > 0) {
                this.geometry.instances(instanceIds[0], instance);
                this.geometry.definitions(instance.definitionId(), definition);
                if (node.expressions[0] && node.expressions[0].token === "geometryType" && node.expressions[0].operations[0](definition.type()))
                    result.push(entityId);
            }
        }

        return result;
    }
}
