import { GeometryBuilder, GeometryData, normalLength } from "./GeometryBuilder";
import { Trimble } from "../../TrimBim_generated";
import {
    intersectCylinder,
    pickOnArc,
    pickOnPolyline,
    pickOnSegment,
    forEachSegment,
    forEachArc,
    forEachPolyline,
    get3DArcSectionsCount
} from "../Pickers";
import { Frustum, Quaternion, Ray, Vector3, Vector3Const } from "../ThreeMath";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { HitPoint } from "../HitPoint";
import Directrix = Trimble.Technology.TrimBim.Fbs.Directrix;
import { TrimbimGeometryType } from "../../common";
import { createCrossSections } from "./LinePrimitiveBuilder";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

interface InterConnectionDetail {
    position: Vector3;
    type: Fbs.SegmentType;
    isStartSegment?: boolean;
    isEndSegment?: boolean;
}

export class SweptDiskSolidGeometryBuilder extends GeometryBuilder {
    readonly geometryType = TrimbimGeometryType.SweptDiskSolid;
    readonly snapGeometryType = GeometryType.MESH;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    private getCrossSectionsPointCount(radius: number, amplifier: number = 200): number {
        // Cross sections point depends on cross section radius
        const count = Math.round(radius * amplifier);
        return Math.min(Math.max(count, 5), 30);
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const geometry = this.buffers[geometryIndex];

        if (!geometry.positions && buildIfEmpty) {
            const line = this.geometry.sweptDiskSolids(geometryIndex);
            this.build(line, geometry);
        }

        return [geometry];
    }

    public prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.sweptDiskSolidsLength(); i < l; i++) {
            const line = this.geometry.sweptDiskSolids(i);
            this.buffers.push(this.calculateBufferSizes(line));
        }
    }

    private directrix = new Directrix();
    calculateBufferSizes(sds: Fbs.SweptDiskSolidContainer): GeometryData {
        let linesCount = 0;
        let arcsCount = 0;
        let linesOfPolylinesCount = 0;
        let lineCrossSectionPointCount = 0;
        let arcCrossSectionPointCount = 0;
        let polylineCrossSectionPointCount = 0;
        let arcStartEndCrossSectionPointCount = 0;

        for (let i = 0, l = sds.directricesLength(); i < l; i++) {
            sds.directrices(i, this.directrix);
            const radius = sds.radius(i);
            const crossSectionPointCount = this.getCrossSectionsPointCount(radius);
            linesCount += this.directrix.lineSegmentsLength();
            arcsCount += this.directrix.arcsLength();
            arcStartEndCrossSectionPointCount += this.directrix.arcsLength() * crossSectionPointCount * 2;

            for (let j = 0; j < this.directrix.polyLinesLength(); j++) {
                linesOfPolylinesCount += this.directrix.polyLines(j).pointsLength() - 1;
                polylineCrossSectionPointCount += crossSectionPointCount * (this.directrix.polyLines(j).pointsLength() - 1) * 2;
            }
            for (let a = 0; a < this.directrix.arcsLength(); a++) {
                this.directrix.arcs(a, this.arc);
                arcCrossSectionPointCount += crossSectionPointCount * get3DArcSectionsCount(this.arc);
            }
            for (let b = 0; b < this.directrix.lineSegmentsLength(); b++) {
                lineCrossSectionPointCount += 2 * crossSectionPointCount;
            }
        }

        const verticesLength = (lineCrossSectionPointCount + arcCrossSectionPointCount + polylineCrossSectionPointCount) * 3;
        //Crosss section indices length
        const indicesLength0 = (lineCrossSectionPointCount - linesCount * 2 * 2) * 3 + (arcStartEndCrossSectionPointCount - arcsCount * 2 * 2) * 3 + (polylineCrossSectionPointCount - linesOfPolylinesCount * 2 * 2) * 3;
        //Connect cross section indices length
        const indicesLength1 = lineCrossSectionPointCount / 2 * 6 + (arcCrossSectionPointCount - arcStartEndCrossSectionPointCount / 2) * 6 + polylineCrossSectionPointCount / 2 * 6;
        //Interconnection indices length
        const indicesLength2 = arcStartEndCrossSectionPointCount * 6;
        const indicesLength = indicesLength0 + indicesLength1 + indicesLength2;

        return {
            objectType: ObjectType.MESH,
            indicesLength: indicesLength,
            positionsLength: verticesLength,
            normalsLength: verticesLength,
            uvsLength: 0,
            pointSizesLength: 0,
            indexPointer: 0,
            positionPointer: 0,
            normalPointer: 0
        };
    }

    build(
        sds: Fbs.SweptDiskSolidContainer,
        buffer: GeometryData
    ): void {
        buffer.positions = new Float32Array(buffer.positionsLength);
        buffer.normals = new Int16Array(buffer.normalsLength);
        if (buffer.positionsLength > 65535)
            buffer.indices = new Uint32Array(buffer.indicesLength);
        else
            buffer.indices = new Uint16Array(buffer.indicesLength);

        const interConnectionDetail = {} as InterConnectionDetail;
        for (let i = 0, l = sds.directricesLength(); i < l; i++) {
            const radius = sds.radius(i);
            const directrix = sds.directrices(i, this.directrix);
            const crossSectionPointCount = this.getCrossSectionsPointCount(radius);

            for (let j = 0; j < directrix.indexesLength(); j++) {
                if (j === 0) interConnectionDetail.isStartSegment = true;
                if (j === directrix.indexesLength() - 1) interConnectionDetail.isEndSegment = true;

                const type = directrix.types(j);
                const index = directrix.indexes(j);
                switch (type) {
                    case Fbs.SegmentType.LineSegment: this.buildLineSegment(directrix.lineSegments(index), radius, buffer, crossSectionPointCount, interConnectionDetail); break;
                    case Fbs.SegmentType.PolyLine: this.buildPolyLine(directrix.polyLines(index), radius, buffer, crossSectionPointCount, interConnectionDetail); break;
                    case Fbs.SegmentType.Arc: this.buildArc(directrix.arcs(index), radius, buffer, crossSectionPointCount, interConnectionDetail); break;
                }

                if (interConnectionDetail.isStartSegment) interConnectionDetail.isStartSegment = !interConnectionDetail.isStartSegment;
                if (interConnectionDetail.isEndSegment) interConnectionDetail.isEndSegment = !interConnectionDetail.isEndSegment;
            }

        }
    }

    private buildPolyLine(polyline: Fbs.Polyline3f, radius: number, buffer: GeometryData, crossSectionPointCount: number, interConnectionDetail: InterConnectionDetail): void {
        for (let j = 1, length = polyline.pointsLength(); j < length; j++) {
            this.lineStart.set(polyline.points(j - 1).x(), polyline.points(j - 1).y(), polyline.points(j - 1).z());
            this.lineEnd.set(polyline.points(j).x(), polyline.points(j).y(), polyline.points(j).z());
            this.tmp.copy(this.lineEnd).sub(this.lineStart).normalize();
            this.lineQuaternion.setFromUnitVectors(Vector3Const.up, this.tmp);

            this.buildCrossSection(this.lineStart, radius, this.lineQuaternion, buffer, crossSectionPointCount);
            //first line part
            j === 1 ? this.connectFirstCrossSection(buffer, crossSectionPointCount, interConnectionDetail, this.lineStart, Fbs.SegmentType.PolyLine) : this.buildCrossSectionIndices(buffer, crossSectionPointCount);
            this.buildCrossSection(this.lineEnd, radius, this.lineQuaternion, buffer, crossSectionPointCount);
            if (j !== (length - 1) || interConnectionDetail.isEndSegment)
                this.buildCrossSectionIndices(buffer, crossSectionPointCount, true);
            else {
                interConnectionDetail.position = this.lineEnd;
                interConnectionDetail.type = Fbs.SegmentType.PolyLine;
            }
            this.connectCrossSections(buffer, crossSectionPointCount);
        }
    }

    private connectFirstCrossSection(buffer: GeometryData, crossSectionPointCount: number, interConnectionDetail: InterConnectionDetail, firstCrossSectionPosition: Vector3, type:Fbs.SegmentType): void {
        if (interConnectionDetail.isStartSegment) this.buildCrossSectionIndices(buffer, crossSectionPointCount);
        else if (!isMatchType(interConnectionDetail.type,type) || (!IsConnected(interConnectionDetail.position, firstCrossSectionPosition))) {
            this.buildCrossSectionIndices(buffer, crossSectionPointCount, true, true);
            this.buildCrossSectionIndices(buffer, crossSectionPointCount);
        }
        else {
            this.connectCrossSections(buffer, crossSectionPointCount)
        }
    }

    private buildStartCrossSection(point: Vector3, nextPoint: Vector3, radius: number, buffer: GeometryData, crossSectionPointCount: number): void {
        this.tmp.copy(nextPoint).sub(point).normalize();
        this.lineQuaternion.setFromUnitVectors(Vector3Const.up, this.tmp);
        this.buildCrossSection(point, radius, this.lineQuaternion, buffer, crossSectionPointCount);
    }

    private buildMidCrossSection(prevPoint: Vector3, point: Vector3, nextPoint: Vector3, radius: number, buffer: GeometryData, crossSectionPointCount: number): void {
        this.tmp.copy(nextPoint).sub(prevPoint).normalize();
        this.lineQuaternion.setFromUnitVectors(Vector3Const.up, this.tmp);
        this.buildCrossSection(point, radius, this.lineQuaternion, buffer, crossSectionPointCount);
        this.connectCrossSections(buffer, crossSectionPointCount);
    }

    private buildEndCrossSection(point: Vector3, nextPoint: Vector3, radius: number, buffer: GeometryData, crossSectionPointCount: number): void {
        this.tmp.copy(nextPoint).sub(point).normalize();
        this.lineQuaternion.setFromUnitVectors(Vector3Const.up, this.tmp);
        this.buildCrossSection(nextPoint, radius, this.lineQuaternion, buffer, crossSectionPointCount);
    }

    private arcCrossSections: Vector3[] = [];
    private arc = new Trimble.Technology.TrimBim.Fbs.Arc3f();
    private buildArc(arc: Fbs.Arc3f, radius: number, buffer: GeometryData, crossSectionPointCount: number, interConnectionDetail: InterConnectionDetail): void {
        const arcCrossSectionCount = get3DArcSectionsCount(arc);
        this.arcCrossSections = createCrossSections(arc, arcCrossSectionCount);

        this.buildStartCrossSection(this.arcCrossSections[0], this.arcCrossSections[1], radius, buffer, crossSectionPointCount);
        this.connectFirstCrossSection(buffer,crossSectionPointCount,interConnectionDetail,this.arcCrossSections[0],Fbs.SegmentType.Arc);
        for (let j = 0; j < arcCrossSectionCount - 2; j++) {
            this.buildMidCrossSection(this.arcCrossSections[j], this.arcCrossSections[j + 1], this.arcCrossSections[j + 2], radius, buffer, crossSectionPointCount);
        }
        this.buildEndCrossSection(this.arcCrossSections[arcCrossSectionCount - 2], this.arcCrossSections[arcCrossSectionCount - 1], radius, buffer, crossSectionPointCount);
        if (interConnectionDetail.isEndSegment)
            this.buildCrossSectionIndices(buffer, crossSectionPointCount, true);
        else {
            interConnectionDetail.position = this.arcCrossSections[arcCrossSectionCount - 1];
            interConnectionDetail.type = Fbs.SegmentType.Arc;
        }
        this.connectCrossSections(buffer, crossSectionPointCount);
    }

    private lineStart = new Vector3();
    private lineEnd = new Vector3();
    private lineQuaternion = new Quaternion();
    private buildLineSegment(segment: Fbs.Line3d | Fbs.Line3f, radius: number, buffer: GeometryData, crossSectionPointCount: number, interConnectionDetail: InterConnectionDetail): void {
        const start = segment.start();
        const end = segment.end();
        this.lineStart.set(start.x(), start.y(), start.z());
        this.lineEnd.set(end.x(), end.y(), end.z());
        this.tmp.copy(this.lineEnd).sub(this.lineStart).normalize();
        this.lineQuaternion.setFromUnitVectors(Vector3Const.up, this.tmp);

        this.buildCrossSection(this.lineStart, radius, this.lineQuaternion, buffer, crossSectionPointCount);
        this.connectFirstCrossSection(buffer,crossSectionPointCount, interConnectionDetail, this.lineStart, Fbs.SegmentType.LineSegment);

        this.buildCrossSection(this.lineEnd, radius, this.lineQuaternion, buffer, crossSectionPointCount);
        if (interConnectionDetail.isEndSegment)
            this.buildCrossSectionIndices(buffer, crossSectionPointCount, true);
        else {
            interConnectionDetail.position = this.lineEnd;
            interConnectionDetail.type = Fbs.SegmentType.LineSegment;
        }
        this.connectCrossSections(buffer, crossSectionPointCount);
    }

    private crossSectionPoints: Vector3[];
    private tmp = new Vector3();
    private buildCrossSection(position: Vector3, radius: number, quaternion: Quaternion, buffer: GeometryData, crossSectionPointCount: number): void {
        if (!this.crossSectionPoints || this.crossSectionPoints.length !== crossSectionPointCount) {
            this.crossSectionPoints = [];
            for (let i = 0; i < crossSectionPointCount; i++) {
                const a = 2 * Math.PI * i / crossSectionPointCount;
                this.crossSectionPoints.push(new Vector3(Math.sin(a), Math.cos(a), 0));
            }
        }

        for (let i = 0; i < crossSectionPointCount; i++) {
            this.tmp.copy(this.crossSectionPoints[i]).multiplyScalar(radius).applyQuaternion(quaternion).add(position);
            buffer.positions[buffer.positionPointer] = this.tmp.x;
            buffer.positions[buffer.positionPointer + 1] = this.tmp.y;
            buffer.positions[buffer.positionPointer + 2] = this.tmp.z;
            buffer.positionPointer += 3;

            this.tmp.copy(this.crossSectionPoints[i]).applyQuaternion(quaternion);
            buffer.normals[buffer.normalPointer] = this.tmp.x * normalLength;
            buffer.normals[buffer.normalPointer + 1] = this.tmp.y * normalLength;
            buffer.normals[buffer.normalPointer + 2] = this.tmp.z * normalLength;
            buffer.normalPointer += 3;
        }
    }

    private buildCrossSectionIndices(buffer: GeometryData, crossSectionPointCount: number, invertFaces: boolean = false, isPreviousCrossSection: boolean = false): void {
        for (let i = 0; i < crossSectionPointCount - 2; i++) {
            buffer.indices[buffer.indexPointer] = buffer.positionPointer / 3 - crossSectionPointCount * (isPreviousCrossSection ? 2 : 1);
            buffer.indices[buffer.indexPointer + 1] = buffer.positionPointer / 3 + i + (invertFaces ? 2 : 1) - crossSectionPointCount * (isPreviousCrossSection ? 2 : 1);
            buffer.indices[buffer.indexPointer + 2] = buffer.positionPointer / 3 + i + (invertFaces ? 1 : 2) - crossSectionPointCount * (isPreviousCrossSection ? 2 : 1);
            buffer.indexPointer += 3;
        }
    }

    private connectCrossSections(buffer: GeometryData, crossSectionPointCount: number): void {
        const vertexPointer1 = buffer.positionPointer / 3 - crossSectionPointCount;
        const vertexPointer2 = vertexPointer1 - crossSectionPointCount;
        for (let i = 0; i < crossSectionPointCount; i++) {
            const nexti = (i + 1) % crossSectionPointCount;

            buffer.indices[buffer.indexPointer] = vertexPointer1 + i;
            buffer.indices[buffer.indexPointer + 1] = vertexPointer1 + nexti;
            buffer.indices[buffer.indexPointer + 2] = vertexPointer2 + i;
            buffer.indexPointer += 3;

            buffer.indices[buffer.indexPointer] = vertexPointer2 + i;
            buffer.indices[buffer.indexPointer + 1] = vertexPointer1 + nexti;
            buffer.indices[buffer.indexPointer + 2] = vertexPointer2 + nexti;
            buffer.indexPointer += 3;
        }
    }

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickFace(geometryIndex, ray, frustum, false);
    }

    pickFace = (() => {
        const hits: HitPoint[] = [];
        const sds = new Fbs.SweptDiskSolidContainer();
        const directrix = new Fbs.Directrix();

        return (geometryIndex: number, ray: Ray, frustum?: Frustum, snapped: boolean = true): HitPoint[] => {
            hits.length = 0;
            this.geometry.sweptDiskSolids(geometryIndex, sds);

            for (let i = 0, l = sds.directricesLength(); i < l; i++) {
                sds.directrices(i, directrix);
                const radius = sds.radius(i);

                // do not snap to rebar surface if it is thin (dont block the center line snap)
                if (snapped && radius < 0.02) continue;

                forEachSegment(directrix, (start: Vector3, end: Vector3) => {
                    hits.push(...intersectCylinder(start, end, ray, radius));
                });

                forEachArc(directrix, (arcStart: Vector3, arcMids: Vector3[], arcEnd: Vector3) => {
                    hits.push(...intersectCylinder(arcStart, arcMids[0], ray, radius));
                    arcMids.forEach((mid, index) => {
                        if (index > 0) hits.push(...intersectCylinder(arcMids[index - 1], mid, ray, radius));
                    });
                    hits.push(...intersectCylinder(arcMids[arcMids.length - 1], arcEnd, ray, radius));
                }, get3DArcSectionsCount);

                forEachPolyline(directrix, (polyPoint: Vector3, nextPolyPoint: Vector3) => {
                    hits.push(...intersectCylinder(polyPoint, nextPolyPoint, ray, radius));
                });
            }

            return hits;
        };
    })();

    pickLine = (() => {
        const hits: HitPoint[] = [];
        const sds = new Fbs.SweptDiskSolidContainer();
        const directrix = new Fbs.Directrix();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            hits.length = 0;
            this.geometry.sweptDiskSolids(geometryIndex, sds);

            for (let i = 0, l = sds.directricesLength(); i < l; i++) {
                sds.directrices(i, directrix);
                hits.push(...pickOnSegment(directrix, ray, Infinity));
                hits.push(...pickOnArc(directrix, ray, Infinity, get3DArcSectionsCount));
                hits.push(...pickOnPolyline(directrix, ray, Infinity));
            }

            return hits.filter(h => frustum.containsPoint(h.point));
        };
    })();

    pickPoint = (() => {
        const hits: HitPoint[] = [];
        const sds = new Fbs.SweptDiskSolidContainer();
        const directrix = new Fbs.Directrix();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            hits.length = 0;
            this.geometry.sweptDiskSolids(geometryIndex, sds);

            for (let i = 0, l = sds.directricesLength(); i < l; i++) {
                sds.directrices(i, directrix);

                forEachSegment(directrix, (start: Vector3, end: Vector3) => {
                    hits.push({ point: start.clone() } as HitPoint);
                    hits.push({ point: end.clone() } as HitPoint);
                });

                forEachArc(directrix, (arcStart: Vector3, arcMids: Vector3[], arcEnd: Vector3) => {
                    hits.push({ point: arcStart.clone() } as HitPoint);
                    hits.push({ point: arcEnd.clone() } as HitPoint);
                }, get3DArcSectionsCount);

                forEachPolyline(directrix, (polyPoint: Vector3, nextPolyPoint: Vector3) => {
                    hits.push({ point: polyPoint.clone() } as HitPoint);
                    hits.push({ point: nextPolyPoint.clone() } as HitPoint);
                });
            }

            return hits.filter(h => frustum.containsPoint(h.point));
        };
    })();
}

const IsConnected = (section1: Vector3, section2: Vector3) => {
    return section1.distanceToSquared(section2) < 0.0001 * 0.0001;
};

const isMatchType = (crossSectionType: Fbs.SegmentType, nextCrossSectionType: Fbs.SegmentType) => {
    if (nextCrossSectionType === Fbs.SegmentType.Arc) return true;
    return (nextCrossSectionType === Fbs.SegmentType.PolyLine || nextCrossSectionType === Fbs.SegmentType.LineSegment) && crossSectionType === Fbs.SegmentType.Arc;
};
