import {Trimble} from "../TrimBim_generated";
import Fbs = Trimble.Technology.TrimBim.Fbs;

export class LayerManager {
    private layerVisibilities: boolean[];
    private instance = new Fbs.GeometryInstance();
    private geometry: Fbs.ModelGeometry;
    private entities: Fbs.ModelEntities;

    constructor(private model: Fbs.Model) {
        this.geometry = model.geometry();
        this.entities = model.entities();
        this.layerVisibilities = new Array(this.geometry.layersLength());
    }

    getLayers(): string[] {
        const layers: string[] = [];
        for (let i = 0; i < this.geometry.layersLength(); i++)
            layers.push(this.geometry.layers(i));
        return layers;
    }

    findLayersByEntities(entityIds: number[]): string[] {
        const layers = new Set<string>();
        for (let instanceId = 0; instanceId < this.geometry.instancesLength(); instanceId++) {
            this.geometry.instances(instanceId, this.instance);
            if (entityIds.includes(this.instance.entityId())) layers.add(this.geometry.layers(this.instance.layerId()));
        }
        return Array.from(layers);
    }

    private getLayerIndex(layer: string): number {
        for (let i = 0; i < this.geometry.layersLength(); i++)
            if (this.geometry.layers(i) === layer) return i;
    }

    getLayerVisibility(layer: string): boolean {
        return this.layerVisibilities[this.getLayerIndex(layer)];
    }

    setLayerVisibility(layer: string, visible: boolean): void {
        this.layerVisibilities[this.getLayerIndex(layer)] = visible;
    }

    isVisibleByLayer(instanceId: number): boolean {
        const visibility = this.layerVisibilities[this.geometry.instances(instanceId, this.instance).layerId()];
        return visibility === true || visibility === undefined;
    }

    getLayerInstances(layer: string): number[] {
        const layerId = this.getLayerIndex(layer);
        const result: number[] = [];
        for (let i = 0; i < this.geometry.instancesLength(); i++)
            if (this.geometry.instances(i, this.instance).layerId() === layerId)
                result.push(i);
        return result;
    }
}
