export interface DisplayGroup {
    start: number;
    count: number;
}

export const mergeGroups = (input: DisplayGroup[]): DisplayGroup[] => {
    if (input.length === 0) return input;

    input.sort((a, b) => a.start - b.start);
    const result = [input[0]];

    for (let i = 1; i < input.length; i++) {
        const r = input[i];
        const last = result[result.length - 1];

        if (last.start + last.count < r.start)
            result.push(r);
        else if (last.start + last.count < r.start + r.count)
            last.count = r.start + r.count - last.start;
    }

    return result;
};

export const subtractGroups = (ranges: DisplayGroup[], subtracts: DisplayGroup[]): DisplayGroup[] => {
    if (ranges.length === 0 || subtracts.length === 0) return ranges;

    subtracts = mergeGroups(subtracts);

    for (let i = 0; i < subtracts.length; i++) {
        const subtract = subtracts[i];
        if (subtract.count === 0) continue;

        for (let j = 0; j < ranges.length; j++) {
            const range = ranges[j];
            const startInside = subtract.start > range.start && subtract.start < range.start + range.count;
            const endInside = subtract.start + subtract.count > range.start && subtract.start + subtract.count < range.start + range.count;
            if (startInside && endInside) {
                ranges.push({start: subtract.start + subtract.count, count: range.count + range.start - subtract.start - subtract.count});
                range.count = subtract.start - range.start;
            }
            else if (startInside) {
                range.count = subtract.start - range.start;
            }
            else if (endInside) {
                range.count = range.start + range.count - subtract.start - subtract.count;
                range.start = subtract.start + subtract.count;
            }
            // range is fully inside subtract
            else if (subtract.start <= range.start && subtract.start + subtract.count >= range.start + range.count) {
                ranges.splice(j, 1);
                j--;
            }
        }
    }

    return ranges;
};
