import { ModelStateManager } from "./QueryHandling/ModelStateManager";
import { Hit } from "./HitPoint";
import { GeometryManager } from "./GeometryManager";
import { MaterialManager } from "./MaterialManager";
import { InstanceManager } from "./InstanceManager";
import { ModelBuilderWorker } from "./QueryHandling/ModelBuilderWorker";
import { LodManager } from "./LodManager";
import { ClusterManager } from "./ClusterManager";
import { EntityManager } from "./EntityManager";
import {
    Entity,
    Hierarchy,
    Product
} from "./ThreadWorkerMessages";
import {
    AggregationOperator,
    HierarchyOptions,
    HierarchyType,
    Property,
    PropertyAggregation,
    PropertyDefinition,
    PropertySet,
    Query,
    TrimbimPickPriority
} from "../common";
import { parse } from "./QueryHandling/Parser";
import { find } from "./QueryHandling/find";
import { flatbuffers } from "flatbuffers";
import { ModelEntityState } from "./ModelEntityState";
import { Trimble } from "../TrimBim_generated";
import { Box3, Frustum, Plane, Ray, Vector3 } from "./ThreeMath";
import { IMetaMaterial } from "../Model/ModelBuilderTypes";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { PickHandler } from "./QueryHandling/PickHandler";
import {LayerManager} from "./LayerManager";
import {TrimbimProtocol} from "../Model/TrimbimProtocol";
import {SnapType} from "@technology/webgl-viewer/dist/esnext/common";

export class ThreadWorkerModel implements TrimbimProtocol {
    private modelStateManager: ModelStateManager;
    private state: ModelEntityState;
    private pickHandler: PickHandler;

    private model: Fbs.Model;
    private geometryManager: GeometryManager;
    private materialManager: MaterialManager;
    private instanceManager: InstanceManager;
    private modelBuilderWorker: ModelBuilderWorker;
    private lodManager: LodManager;
    private clusterManager: ClusterManager;
    private entityManager: EntityManager;
    private layerManager: LayerManager;

    constructor(private modelId: string, buffer: ArrayBuffer) {
        const buf = new flatbuffers.ByteBuffer(new Uint8Array(buffer));
        const model = Fbs.Model.getRootAsModel(buf);
        this.model = model;
        this.state = new ModelEntityState(model.entities().entitiesLength());

        this.instanceManager = new InstanceManager(model);

        this.lodManager = new LodManager();

        const modelGeometry = model.geometry();
        this.geometryManager = new GeometryManager(this.modelId, modelGeometry, this.instanceManager, this.lodManager);
        this.layerManager = new LayerManager(model);

        this.entityManager = new EntityManager(model.entities(), model.properties());

        this.materialManager = new MaterialManager(this.modelId);
        this.materialManager.addModelTextures(model);
        const materialIds = this.materialManager.addModelMaterials(model);

        this.clusterManager = new ClusterManager(this.modelId, model, this.instanceManager, this.geometryManager, this.lodManager, this.layerManager, this.state, materialIds);

        this.modelStateManager = new ModelStateManager(
            this.modelId,
            this.materialManager,
            this.clusterManager,
            this.instanceManager,
            this.lodManager,
            this.layerManager,
            this.state,
            this.geometryManager
        );
        this.modelBuilderWorker = new ModelBuilderWorker(
            this.modelId,
            this.clusterManager,
            this.instanceManager,
            this.geometryManager,
            this.lodManager,
            model
        );

        this.pickHandler = new PickHandler(
            this.model,
            this.instanceManager.boundingBoxes,
            this.instanceManager,
            this.geometryManager,
            this.layerManager,
            this.state
        );
    }

    pick(ray: Ray, frustum: Frustum, clipPlanes: Plane[]): Hit {
        return this.pickHandler.pick(ray, frustum, clipPlanes);
    }

    pickSnapped(ray: Ray, frustum: Frustum, snapTypes: SnapType[], clipPlanes: Plane[]): Hit[] {
        return this.pickHandler.pickSnapped(ray, frustum, snapTypes, clipPlanes);
    }

    getHierarchyTree(entityIds: number[], hierarchyType: HierarchyType): {parentIds: number[], childrenIds?: number[]} {
        return this.entityManager.getHierarchyTree(entityIds, hierarchyType);
    }

    frustumPick(frustum: Frustum, clipPlanes: Plane[]): number[] {
        return this.pickHandler.frustumPick(frustum, clipPlanes);
    }

    setSelection(entityIds: number[]): void {
        this.modelStateManager.setSelection(entityIds);
    }

    setHovered(entityIds: number[]): void {
        this.modelStateManager.setHovered(entityIds);
    }

    setMaterial(entityIds: number[], material: IMetaMaterial): void {
        this.modelStateManager.setMaterial(entityIds, material);
        this.modelBuilderWorker.forceUpdate();
    }

    getMaterials(entityIds: number[]): IMetaMaterial[] {
        return this.modelStateManager.getMaterials(entityIds);
    }

    setVisibility(entityIds: number[], visible: boolean): void {
        this.modelStateManager.setVisibility(entityIds, visible);
        this.modelBuilderWorker.forceUpdate();
    }

    setPickPriority(entityIds: number[], pickPriority: TrimbimPickPriority): void {
        this.modelStateManager.setPickPriority(entityIds, pickPriority);
    }

    getEntities(entityIds: number[]): Entity[] {
        return this.entityManager.getEntitiesFromIds(entityIds);
    }

    getLayers(): string[] {
        return this.layerManager.getLayers();
    }

    findLayersByEntities(entityIds: number[]): string[] {
        return this.layerManager.findLayersByEntities(entityIds);
    }

    getLayerVisibility(layer: string): boolean {
        return this.layerManager.getLayerVisibility(layer);
    }

    setLayerVisibility(layer: string, visible: boolean): void {
        this.modelStateManager.setLayerVisibility(layer, visible);
        this.modelBuilderWorker.forceUpdate();
    }

    getEntityParentId(entityId: number, hierarchyType: HierarchyType): number {
        if (hierarchyType === undefined) return entityId;

        const parentId = this.entityManager.getParents([entityId], hierarchyType)[0];
        return parentId !== undefined ? parentId : entityId;
    }

    getEntity(entityId: number): Entity {
        return this.entityManager.getEntity(entityId);
    }

    getEntityProduct(entityId: number): Product {
        const productId = this.entityManager.getProductId(entityId);
        return productId !== undefined ? this.entityManager.getProduct(productId) : undefined;
    }

    getEntityProperties(entityId: number): PropertySet[] {
        const propertySetIds = this.entityManager.getPropertySetIdsFromEntity(entityId);
        const properties = [];
        for (const propertySetId of propertySetIds) {
            properties.push(this.entityManager.getPropertySet(propertySetId));
        }
        return properties;
    }

    getHierarchy(hierarchyType: HierarchyType, options?: HierarchyOptions): Hierarchy {
        return this.entityManager.getHierarchy(hierarchyType, options);
    }

    setCamera(frustum: Frustum, position: Vector3): void {
        this.modelBuilderWorker.buildGeometryForCamera(frustum, position);
    }

    getBoundingBox(): Box3 {
        return this.instanceManager.worldBoundingbox;
    }

    getEntitiesBoundingBox(entityIds: number[]): Box3 {
        const box = new Box3();
        this.instanceManager.getAABBFromIds(entityIds, box);
        return box;
    }

    resetMaterial(entityIds: number[]): void {
        this.modelStateManager.resetMaterials(entityIds);
        this.modelBuilderWorker.forceUpdate();
    }

    clearSelection(): void {
        this.modelStateManager.clearSelection();
    }

    resetVisibility(): void {
        this.modelStateManager.resetVisibility();
        this.modelBuilderWorker.forceUpdate();
    }

    resetPickPriority(): void {
        this.modelStateManager.resetPickPriority();
    }

    getPropertySetNames(): string[] {
        return this.entityManager.getPropertySetNames();
    }

    getPropertyDefinitions(propertySetName: string): PropertyDefinition[] {
        return this.entityManager.getPropertyDefinitions(propertySetName);
    }

    getPropertyValues(propertyName: string): Array<string | number | flatbuffers.Long> {
        return this.entityManager.getPropertyValues(propertyName);
    }

    getEntitiesByFilter(conditions: any): number[] {
        const logicalNode = parse(conditions);
        return [...this.entityManager.filterProperties(logicalNode)];
    }

    guidsToEntityIds(guids: string[]): number[] {
        return this.entityManager.getEntityIdsFromGuids(guids);
    }

    query(request: Query): void {
        if (request.find)
            find(this.entityManager, this.instanceManager, request);
    }

    queryPage(streamId: number, page: number, pageSize: number): Array<Array<{[name: string]: (string | number);}>> {
        return this.entityManager.getPropertyStreamPage(streamId, page, pageSize);
    }

    getEntityIdsByState(visible?: boolean, pickPriority?: TrimbimPickPriority): number[] {
        const result: number[] = [];
        for (const entityId of this.instanceManager.entityToInstance.keys()) {
            if (
                (visible === undefined || this.state.isVisible(entityId) === visible) &&
                (pickPriority === undefined || this.state.getPickPriority(entityId) === pickPriority)
            ) result.push(entityId);
        }
        return result;
    }

    aggregateProperties(entityIds: number[], aggregations: PropertyAggregation[]): void {
        for (const entityId of entityIds) {
            const propSets = this.getEntityProperties(entityId);
            for (const a of aggregations) this.aggregate(a, propSets);
        }
    }

    private getProperty(propSets: PropertySet[], propertySetName: string, propertyName: string): Property {
        const set = propSets.find(s => s.name === propertySetName);
        return set ? set.properties.find(p => p.name === propertyName) : undefined;
    }

    private aggregate(aggregation: PropertyAggregation, propSets: PropertySet[]): void {
        if (aggregation.operator === AggregationOperator.SHARED) {
            if (aggregation.value === null) return; // value varies

            const property = this.getProperty(propSets, aggregation.propertySetName, aggregation.propertyName);
            if (property) {
                if (aggregation.value === undefined) {
                    aggregation.value = property.value;
                    aggregation.type = property.type;
                }
                else if (aggregation.value !== property.value) aggregation.value = null;
            } else {
                aggregation.value = null;
            }
        }
        else if (aggregation.operator === AggregationOperator.SUM) {
            const property = this.getProperty(propSets, aggregation.propertySetName, aggregation.propertyName);
            if (property) {
                if (aggregation.value === undefined) {
                    aggregation.value = 0;
                    aggregation.type = property.type;
                }
                (aggregation.value as number) += typeof property.value === "string" ? Number.parseFloat(property.value as string) : property.value as number;
            }
        }
    }

    dispose(): void {
        this.clusterManager.dispose();
        this.modelBuilderWorker.dispose();
    }
}
