import { GeometryData, GeometryBuilder, normalLength } from "./GeometryBuilder";
import { Trimble } from "../../TrimBim_generated";
import { HitPoint } from "../HitPoint";
import { Frustum, Plane, Ray, Triangle, Vector3 } from "../ThreeMath";
import { ObjectType } from "../../Model/ModelBuilderTypes";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import BRep = Trimble.Technology.TrimBim.Fbs.BRep;
import BRepWithUV = Trimble.Technology.TrimBim.Fbs.BRepWithUV;
import BRepWithNormal = Trimble.Technology.TrimBim.Fbs.BRepWithNormal;
import { TrimbimGeometryType } from "../../common";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";
// @ts-ignore - no declaration
import { Earcut } from "three/src/extras/Earcut";

interface BRepHole { vertices: number[]; uvs?: number[]; normals?: number[]; indices: number[] }

interface ICounters {
    index_count: number;
    vertex_count: number;
    vertex_length: number;
    uv_length: number;
    normal_count: number;
    normal_length: number;
}

enum polyType {
    triangle = 3,
    rectangle = 4
}

abstract class AbstractBRepGeometryBuilder extends GeometryBuilder {
    abstract geometryType: TrimbimGeometryType;
    readonly snapGeometryType = GeometryType.MESH;

    protected abstract getBRep(idx: number): BRep | BRepWithUV | BRepWithNormal;

    abstract prepareModelTypes(): void;

    private v0 = new Vector3();
    private v1 = new Vector3();
    private v2 = new Vector3();
    private n1 = new Vector3();

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const geometry = this.buffers[geometryIndex];

        if (!geometry.positions && buildIfEmpty) {
            const poly = this.getBRep(geometryIndex);
            this.build(poly, geometry);
        }

        return [geometry];
    }

    calculateBufferSizes = (() => {
        const innerWire = new Fbs.InnerWire();
        const outerWire = new Fbs.OuterWire();
        const MAX_SIZE = 1 << 16;       // InnerWire.outerWire is a ushort in the TrimBIM schema
        const indicesLength = new Uint16Array(MAX_SIZE);
        const verticesLength = new Uint16Array(MAX_SIZE);

        return (brep: BRep | BRepWithUV | BRepWithNormal): GeometryData => {
            let innerVertex_length = 0;

            for (let i = 0, length = brep.innerWiresLength(); i < length; i++) {
                const outerWireId = brep.innerWires(i, innerWire).outerWire();
                ++indicesLength[outerWireId];
                verticesLength[outerWireId] += innerWire.vertexIndicesLength() * 3;
                innerVertex_length += innerWire.vertexIndicesLength();
            }

            let all_outerVertices_length = 0;
            let indices_length = 0;

            for (let i = 0, outerLength = brep.outerWiresLength(); i < outerLength; i++) {
                const vertexIndicesLength = brep.outerWires(i, outerWire).vertexIndicesLength();
                all_outerVertices_length += vertexIndicesLength;

                if (indicesLength[i]) {
                    indices_length += 3 * (
                        vertexIndicesLength +
                        verticesLength[i] / 3 -
                        2 +
                        2 * indicesLength[i]
                    );
                } else {
                    indices_length += (vertexIndicesLength - 2) * 3;
                }
            }

            // Cleaning the hole buffer after use
            for (let i = 0, length = brep.innerWiresLength(); i < length; ++i) {
                const outerWireId = brep.innerWires(i, innerWire).outerWire();
                indicesLength[outerWireId] = 0;
                verticesLength[outerWireId] = 0;
            }

            const bufferGeometry: GeometryData = {
                objectType: ObjectType.MESH,
                indicesLength: indices_length,
                positionsLength: (innerVertex_length + all_outerVertices_length) * 3,
                normalsLength: (innerVertex_length + all_outerVertices_length) * 3,
                uvsLength: 0,
                pointSizesLength: 0
            };

            if (this.geometryType === TrimbimGeometryType.BRepWithUV) {
                bufferGeometry.uvsLength = (innerVertex_length + all_outerVertices_length) * 2;
            }

            return bufferGeometry;
        }
    })();

    private getVertexDataSize(geometryType: TrimbimGeometryType): number {
        if (geometryType === TrimbimGeometryType.BRepWithUV) {
            return 5;
        } else if (geometryType === TrimbimGeometryType.BRepWithNormals) {
            return 6;
        } else {
            return 3;
        }
    }

    private getBrepData(brep: BRep | BRepWithUV | BRepWithNormal, vertexDataSize: number): Float32Array {
        const brepOffset = brep.bb.__offset(brep.bb_pos, 8);
        return new Float32Array(
            brep.bb.bytes().buffer,
            brep.bb.__vector(brep.bb_pos + brepOffset),
            brep.verticesLength() * vertexDataSize
        );
    }

    build = (() => {
        const outerWire = new Fbs.OuterWire();

        return (brep: BRep | BRepWithUV | BRepWithNormal, bufferGeometry: GeometryData): void => {
            const counters = {
                vertex_count: 0,
                index_count: 0,
                vertex_length: 0,
                uv_length: 0,
                normal_length: 0,
                normal_count: 0
            };

            bufferGeometry.indices = new Uint16Array(bufferGeometry.indicesLength);
            bufferGeometry.positions = new Float32Array(bufferGeometry.positionsLength);
            bufferGeometry.normals = new Int16Array(bufferGeometry.normalsLength);
            bufferGeometry.uvs = new Float32Array(bufferGeometry.uvsLength);

            Object.seal(bufferGeometry);

            const data = this.getBrepData(brep, this.getVertexDataSize(this.geometryType));
            const holes = this.buildHoles(brep);

            for (let i = 0, length = brep.outerWiresLength(); i < length; i++) {
                brep.outerWires(i, outerWire);
                const vertexIndices = outerWire.vertexIndicesArray();

                if (
                    outerWire.vertexIndicesLength() === polyType.triangle &&
                    !holes.has(i)
                ) {
                    buildTriangle(vertexIndices, data, this.geometryType, bufferGeometry, counters);
                } else if (
                    outerWire.vertexIndicesLength() === polyType.rectangle &&
                    !holes.has(i)
                ) {
                    buildQuad(vertexIndices, data, i, this.geometryType, bufferGeometry, counters);
                } else {
                    buildPoly(vertexIndices, data, i, this.geometryType, bufferGeometry, holes, counters);
                }
            }

            if (this.geometryType !== TrimbimGeometryType.BRepWithNormals) {
                BRepGeometryBuilder.computeNormals(bufferGeometry);
            }
        }
    })();

    private buildHoles = (() => {
        const vertex = new Fbs.Vec3f();
        const vertexUv = new Fbs.PositionUV();
        const vertexNormal = new Fbs.PositionNormal();
        const innerWire = new Fbs.InnerWire();
        const holes = new Map<number, BRepHole>();

        return (brep: BRep | BRepWithUV | BRepWithNormal): Map<number, BRepHole> => {
            holes.clear();

            for (let i = 0, length = brep.innerWiresLength(); i < length; i++) {
                const outerWireId = brep.innerWires(i, innerWire).outerWire();

                if (holes.has(outerWireId)) {
                    const tmp = holes.get(outerWireId);

                    tmp.indices.push(tmp.vertices.length / 3);
                    holes.set(outerWireId, tmp);
                } else {
                    if (this.geometryType === TrimbimGeometryType.BRep) {
                        holes.set(outerWireId, { indices: [0], vertices: [] });
                    } else if (this.geometryType === TrimbimGeometryType.BRepWithNormals) {
                        holes.set(outerWireId, { indices: [0], vertices: [], normals: [] });
                    } else if (this.geometryType === TrimbimGeometryType.BRepWithUV) {
                        holes.set(outerWireId, { indices: [0], vertices: [], uvs: [] });
                    }
                }

                const hole = holes.get(outerWireId);
                for (let j = 0, innerlength = innerWire.vertexIndicesLength(); j < innerlength; j++) {
                    if (this.geometryType === TrimbimGeometryType.BRep && brep instanceof BRep) {
                        brep.vertices(innerWire.vertexIndices(j), vertex);
                        hole.vertices.push(vertex.x(), vertex.y(), vertex.z());
                    } else if (this.geometryType === TrimbimGeometryType.BRepWithUV && brep instanceof BRepWithUV) {
                        brep.vertices(innerWire.vertexIndices(j), vertexUv);
                        hole.vertices.push(
                            vertexUv.position().x(),
                            vertexUv.position().y(),
                            vertexUv.position().z()
                        );
                        hole.uvs.push(vertexUv.uv().u(), vertexUv.uv().v());
                    } else if (this.geometryType === TrimbimGeometryType.BRepWithNormals && brep instanceof BRepWithNormal) {
                        brep.vertices(innerWire.vertexIndices(j), vertexNormal);
                        hole.vertices.push(
                            vertexNormal.position().x(),
                            vertexNormal.position().y(),
                            vertexNormal.position().z()
                        );
                        hole.normals.push(
                            vertexNormal.normal().x() * normalLength,
                            vertexNormal.normal().y() * normalLength,
                            vertexNormal.normal().z() * normalLength
                        );
                    }
                }
            }

            return holes;
        }
    })();

    static computeNormals = (() => {
        const pA = new Vector3();
        const pB = new Vector3();
        const pC = new Vector3();
        const ab = new Vector3();
        const cb = new Vector3();
        let vA = 0;
        let vB = 0;
        let vC = 0;
        let normals: Float32Array;

        return (geometry: GeometryData) => {

            const indices = geometry.indices;
            const positions = geometry.positions;
            if (!normals || normals.length < geometry.normals.length)
                normals = new Float32Array(geometry.normals.length);
            else
                normals.fill(0);

            for (let i = 0, il = geometry.indices.length; i < il; i += 3) {
                vA = indices[i] * 3;
                vB = indices[i + 1] * 3;
                vC = indices[i + 2] * 3;

                pA.fromArray(positions, vA);
                pB.fromArray(positions, vB);
                pC.fromArray(positions, vC);

                cb.subVectors(pC, pB);
                ab.subVectors(pA, pB);
                cb.cross(ab);

                normals[ vA ] += cb.x;
                normals[ vA + 1 ] += cb.y;
                normals[ vA + 2 ] += cb.z;

                normals[ vB ] += cb.x;
                normals[ vB + 1 ] += cb.y;
                normals[ vB + 2 ] += cb.z;

                normals[ vC ] += cb.x;
                normals[ vC + 1 ] += cb.y;
                normals[ vC + 2 ] += cb.z;
            }

            for (let i = 0, il = geometry.normals.length; i < il; i += 3) {
                cb.fromArray(normals, i);
                cb.normalize();
                cb.multiplyScalar(normalLength);
                cb.toArray(geometry.normals, i);
            }
        };
    })();

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickFace(geometryIndex, ray);
    }

    pickFace = (() => {
        const plane = new Plane();
        const pointsInside = <HitPoint[]>[];
        const innerWiresLUT: Map<number, number[]> = new Map();

        const v0 = new Vector3();
        const v1 = new Vector3();
        const v2 = new Vector3();

        return (geometryIndex: number, ray: Ray): HitPoint[] => {
            const brep = this.getBRep(geometryIndex);
            pointsInside.length = 0;
            innerWiresLUT.clear();

            for (let i = 0; i < brep.innerWiresLength(); i++) {
                const index = brep.innerWires(i).outerWire();
                if (innerWiresLUT.has(index)) {
                    innerWiresLUT.get(index).push(i);
                } else {
                    innerWiresLUT.set(brep.innerWires(i).outerWire(), [i]);
                }
            }

            const indexOffset = this.getVertexDataSize(this.geometryType);
            const data = this.getBrepData(brep, indexOffset);

            for (let i = 0, length = brep.outerWiresLength(); i < length; i++) {
                const vertexIndices = brep.outerWires(i).vertexIndicesArray();

                this.v0.set(0, 0, 0);
                this.v1.set(0, 0, 0);
                this.v2.set(0, 0, 0);
                this.n1.set(0, 0, 0);

                const outerWireIndicesLength = vertexIndices.length;

                if (outerWireIndicesLength === 3) {
                    let idx0 = vertexIndices[0];
                    let idx1 = vertexIndices[1];
                    let idx2 = vertexIndices[2];

                    idx0 *= indexOffset;
                    idx1 *= indexOffset;
                    idx2 *= indexOffset;

                    v0.set(data[idx0], data[idx0 + 1], data[idx0 + 2]);
                    v1.set(data[idx1], data[idx1 + 1], data[idx1 + 2]);
                    v2.set(data[idx2], data[idx2 + 1], data[idx2 + 2]);

                    const result = this.intersectTriangle(ray, v0, v1, v2);

                    if (result) {
                        pointsInside.push(result);
                    }
                    continue;
                }

                const idx0 = vertexIndices[0] * indexOffset;

                this.v0.set(data[idx0], data[idx0 + 1], data[idx0 + 2]);

                getNormalFromTrimBIM(this.n1, data, vertexIndices, indexOffset);

                plane.setFromNormalAndCoplanarPoint(this.n1, this.v0);

                if (!ray.intersectPlane(plane, this.v1)) {
                    continue;
                }

                if (innerWiresLUT.has(i)) {
                    const innerWires = innerWiresLUT.get(i);

                    if (this.insideHole(this.v1, plane, innerWires, brep, data)) {
                        continue;
                    }
                }

                if (this.pointInsidePolygon(this.v1, data, vertexIndices, indexOffset, plane)) {
                    pointsInside.push({
                        point: this.v1.clone(),
                        normal: plane.normal.clone()
                    });
                }
            }

            return pointsInside;
        };
    })();

    pickLine = (() => {
        const outerWire = new Fbs.OuterWire();
        const vecA = new Vector3();
        const vecB = new Vector3();
        const point = new Vector3();
        const plane = new Plane();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            const brep = this.getBRep(geometryIndex);
            const result: HitPoint[] = [];

            for (let i = 0, length = brep.outerWiresLength(); i < length; i++) {
                brep.outerWires(i, outerWire);

                this.getVertex(brep, outerWire.vertexIndices(0), vecA);
                this.getVertex(brep, outerWire.vertexIndices(1), point);
                this.getVertex(brep, outerWire.vertexIndices(outerWire.vertexIndicesLength() - 1), vecB);
                plane.setFromCoplanarPoints(vecA, vecB, point);

                for (let j = 0, l = outerWire.vertexIndicesLength(); j < l; j++) {
                    this.getVertex(brep, outerWire.vertexIndices(j), vecA);
                    ray.distanceSqToSegment(vecA, vecB, undefined, point);
                    if (frustum.containsPoint(point)) {
                        result.push({ point: point.clone(), normal: plane.normal.clone(), snapLineStart: vecA.clone(), snapLineEnd: vecB.clone()})
                    }
                    vecB.copy(vecA);
                }
            }
            return result;
        };
    })();

    pickPoint = (() => {
        const position = new Vector3();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            const brep = this.getBRep(geometryIndex);
            const result: HitPoint[] = [];

            for (let i = 0, length = brep.verticesLength(); i < length; i++) {
                this.getVertex(brep, i, position);
                if (frustum.containsPoint(position)) {
                    result.push({ point: position.clone() })
                }
            }

            return result;
        };
    })();

    getVertex = (() => {
        const vertex = new Fbs.Vec3f();
        const vertexUv = new Fbs.PositionUV();
        const vertexNormal = new Fbs.PositionNormal();

        return (brep: BRep | BRepWithUV | BRepWithNormal, i: number, out: Vector3): void => {
            if (brep instanceof BRep)
                brep.vertices(i, vertex);
            else if (brep instanceof BRepWithNormal)
                brep.vertices(i, vertexNormal).position(vertex);
            else if (brep instanceof BRepWithUV)
                brep.vertices(i, vertexUv).position(vertex);
            out.set(vertex.x(), vertex.y(), vertex.z());
        };
    })();


    public insideHole = (() => {
        return (
            point: Vector3,
            plane: Plane,
            indices: number[],
            brep: BRep | BRepWithUV | BRepWithNormal,
            data: Float32Array
        ) => {
            let indexOffset: number;

            if (this.geometryType === TrimbimGeometryType.BRepWithUV) {
                indexOffset = 5;
            } else if (this.geometryType === TrimbimGeometryType.BRepWithNormals) {
                indexOffset = 6;
            } else {
                indexOffset = 3;
            }

            for (let j = 0, l = indices.length; j < l; j++) {
                const vertexIndices = brep.innerWires(indices[j]).vertexIndicesArray();

                if (this.pointInsidePolygon(point, data, vertexIndices, indexOffset, plane)) {
                    return true;
                }
            }

            return false;
        };
    })();

    public intersectTriangle = (() => {
        const triangle = new Triangle();
        const v0 = new Vector3();

        return (ray: Ray, a: Vector3, b: Vector3, c: Vector3): HitPoint => {
            triangle.a = a;
            triangle.b = b;
            triangle.c = c;

            if (ray.intersectTriangle(a, b, c, false, v0))
                return { point: v0.clone(), normal: triangle.getNormal(new Vector3()) };
        };
    })();

    public createOrthonormalBasis(refAxis: Vector3, axis0: Vector3, axis1: Vector3): void {
        if (Math.abs(refAxis.x) >= Math.abs(refAxis.y)) {
            // w.x or w.z is the largest magnitude component, swap them
            const invLength = 1.0 / Math.sqrt(refAxis.x * refAxis.x + refAxis.z * refAxis.z);
            axis0.set(-refAxis.z * invLength, 0.0, refAxis.x * invLength);
            axis1.set(
                refAxis.y * axis0.z,
                refAxis.z * axis0.x - refAxis.x * axis0.z,
                -refAxis.y * axis0.x
            );
        } else {
            // w.y or w.z is the largest magnitude component, swap them
            const invLength = 1.0 / Math.sqrt(refAxis.y * refAxis.y + refAxis.z * refAxis.z);
            axis0.set(0.0, refAxis.z * invLength, -refAxis.y * invLength);
            axis1.set(
                refAxis.y * axis0.z - refAxis.z * axis0.y,
                -refAxis.x * axis0.z,
                refAxis.x * axis0.y
            );
        }

        axis0.normalize();
        axis1.normalize();
    }

    private pointInsidePolygon = (() => {
        const axis1 = new Vector3();
        const axis0 = new Vector3();
        const vector3 = new Vector3();

        // Optimized version of https://wrf.ecse.rpi.edu//Research/Short_Notes/pnpoly.html
        return (
            point: Vector3,
            data: Float32Array,
            vertexIndices: Uint16Array,
            indexOffset: number,
            plane: Plane
        ): boolean => {
            this.createOrthonormalBasis(plane.normal, axis0, axis1);

            const count = vertexIndices.length;
            const lastIndex = vertexIndices[count - 1] * indexOffset;

            const pj = vector3
                .set(data[lastIndex], data[lastIndex + 1], data[lastIndex + 2])
                .sub(point);

            let jx = pj.dot(axis1);
            let jy = pj.dot(axis0);
            let c = false;

            for (let i = 0; i < count; i++) {
                const index = vertexIndices[i] * indexOffset;
                const pi = vector3.set(data[index], data[index + 1], data[index + 2]).sub(point);

                const ix = pi.dot(axis1);
                const iy = pi.dot(axis0);

                if (iy > 0 !== jy > 0) {
                    if (0 < ((jx - ix) * -iy) / (jy - iy) + ix) {
                        c = !c;
                    }
                }

                jx = ix;
                jy = iy;
            }
            return c;
        };
    })();
}

export class BRepGeometryBuilder extends AbstractBRepGeometryBuilder {
    geometryType = TrimbimGeometryType.BRep;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    protected getBRep = (() => {
        const bRep = new BRep();
        return (idx: number): BRep => {
            return this.geometry.bReps(idx, bRep);
        }
    })();

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.bRepsLength(); i < l; i++) {
            const poly = this.getBRep(i);
            this.buffers.push(this.calculateBufferSizes(poly));
        }
    }
}

export class BRepWithUVGeometryBuilder extends AbstractBRepGeometryBuilder {
    geometryType = TrimbimGeometryType.BRepWithUV;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    protected getBRep = (() => {
        const bRep = new BRepWithUV();
        return (idx: number): BRepWithUV => {
            return this.geometry.bRepWithUVs(idx, bRep);
        }
    })();

    prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.bRepWithUVsLength(); i < l; i++) {
            const poly = this.getBRep(i);
            this.buffers.push(this.calculateBufferSizes(poly));
        }
    }
}

