import { Trimble } from "./TrimBim_generated";
import Fbs = Trimble.Technology.TrimBim.Fbs;
export import HierarchyType = Fbs.HierarchyType;
export import PropertyType = Fbs.PropertyType;
export import TrimbimGeometryType = Fbs.GeometryType;
export import IdentifierType = Fbs.IdentifierType;
import {UUID} from "./UUID";
import {IColor} from "@technology/webgl-viewer/dist/esnext/common";

export interface TrimbimSettings {
    workerRootUrl?: string;
    // By default mesh textures are rendered fully opaque (performance reasons), set this to true if trb contains png textures with transparent pixels
    transparentMeshTextures?: boolean;
}

export interface EntityInfo {
    id: number;
    entity?: Entity;
    product?: Product;
    properties?: PropertySet[];
    children?: EntityInfo[];
}

export interface Entity {
    id: number;
    identifierType: IdentifierType;
    identifier: string | UUID | number[];
    class: string;
}

export interface Material {
    color?: IColor;
    opacity?: number;
}

export const enum TrimbimPickPriority {
    NORMAL = 0,
    HIGH = 1,
    NO_PICK = 2,
}

export interface HierarchyOptions {
    parentId?: number;
    product?: boolean;
    entity?: boolean;
    properties?: boolean;
}

export interface TextStyle {
    font: string;
    size: number;
    alignment: Fbs.AlignmentType;
    verticalAlign: number;
    horizontalAlign: number;
}

export interface IText {
    string: string,
    style?: TextStyle,
    checksum: number,
}

export interface Property {
    name: string;
    value: string | number;
    type: PropertyType;
}

export interface PropertyDefinition {
    name: string;
    type: PropertyType;
}

export interface PropertySet {
    name?: string;
    properties?: Property[];
}

export interface Product {
    name?: string;
    description: string;
    objectType: string;
}

export const enum AggregationOperator {
    SUM,
    SHARED
}

export interface PropertyAggregation {
    propertySetName: string;
    propertyName: string;
    operator: AggregationOperator;
    value?: string | number;
    type?: PropertyType;
}

export interface Query {
    find?: {
        // use output.find.callback to get result instead of output.find.entityIds
        stream?: boolean;

        // find by entity properties
        // $filter: {
        //     $and: [
        //         {'CalculatedGeometryValues.Area': {$gt: 0}},
        //         {'CalculatedGeometryValues.Area': {$lt: 100}},
        //     ]
        // },
        $filter?: any;

        // find children and parents of entities
        // $hierarchy: {
        //   $uniqueIdentifiers: ["19uM$S000K4J4oCparCJGu"], $scope: 'DescendantsAndSelf', $hierarchyType: 'ElementAssembly'
        // }
        $hierarchy?: any;

        // limit the result entities to specific entity propertySet
        // $selector: {
        //     'CalculatedGeometryValues': {}
        // }
        $selector?: any;

        // find entities belonging to Ifc products, filter product parameters (NOT IMPLEMENTED)
        // $product: {
        //   name: 'Insulation'
        // }
        $product?: any;

        // find entities by entity parameters
        // $entity: {
        //   class: 'IFCSLAB'
        // }
        $entity?: any;

        // find by entity instance definition
        // $definition: {geometryType: TrimbimGeometryType.AlignmentCurve}
        $definition?: any;
    };

    output?: {
        find?: {
            streamId?: number;
            length?: number;
            entityIds?: number[];
            callback?: (params: { page: number; pageSize: number }) => Promise<any[]>; // rename to getResult, used when find.stream === true
        };
    };
}

export const enum ClipPlaneEventName {
    Added = "clipplaneadded"
}
