import { LinePrimitiveBuilder } from "./LinePrimitiveBuilder";
import { Trimble } from "../../TrimBim_generated";
import { GeometryData, GeometryBuilder } from "./GeometryBuilder";
import { HitPoint } from "../HitPoint";
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { TextStyle, TrimbimGeometryType } from "../../common";
import { Frustum, Ray, Vector3 } from "../ThreeMath";
import { intersectLine } from "../Pickers";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { createIText } from "../Utils";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

export class GridGeometryBuilder extends GeometryBuilder {
    readonly geometryType = TrimbimGeometryType.GridLineContainer;
    readonly snapGeometryType = GeometryType.LINE;

    private labelBuffers: Map<string, GeometryData>[] = [];
    private intersections: Vector3[][] = [];
    private surfaceNormals = new Array<Vector3>();

    private readonly labelTextStyle = {     // NB: All required TextStyle members must be provided
        size: 16,
        font: "sans-serif",
        alignment: Fbs.AlignmentType.CenterBottom,
        horizontalAlign: 0,
        verticalAlign: -0.5,
    } as TextStyle;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const lineBuffer = this.buffers[geometryIndex];
        const labelBuffers = this.labelBuffers[geometryIndex];

        if (lineBuffer && !lineBuffer.positions && buildIfEmpty) {
            const grid = this.geometry.gridLineContainers(geometryIndex);
            this.buildGridLines(grid, lineBuffer);
            for (let j=0; j<grid.gridLineSegmentsLength(); ++j) {
                const labelString = grid.gridLineSegments(j).axisTag();
                const labelBuffer = labelBuffers.get(labelString);
                this.buildGridLabels(grid, labelBuffer, j);
            }
            this.surfaceNormals[geometryIndex] = new Vector3();
            this.intersections[geometryIndex] = this.buildIntersections(lineBuffer.positions, grid.gridLineSegmentsLength(), this.surfaceNormals[geometryIndex]);
        }

        // Reset pointers which were temporarily used by the builder
        for (const labelBuffer of labelBuffers.values()) {
            labelBuffer.positionPointer = 0;
            labelBuffer.uvPointer = 0;
        }

        return [lineBuffer, ...labelBuffers.values()];
    }

    public prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.gridLineContainersLength(); i < l; i++) {
            const grid = this.geometry.gridLineContainers(i);
            this.buffers.push(this.calculateBufferSizes(grid, ObjectType.LINE));
            const pointBuffers = new Map<string, GeometryData>();
            for (let j=0; j<grid.gridLineSegmentsLength(); ++j) {
                const labelString = grid.gridLineSegments(j).axisTag();
                if (!pointBuffers.has(labelString)) {
                    const label = this.calculateBufferSizes(grid, ObjectType.POINT);
                    label.text = createIText(grid.gridLineSegments(j).axisTag(), this.labelTextStyle);
                    pointBuffers.set(labelString, label);
                } else {
                    const label = pointBuffers.get(labelString);
                    label.positionsLength += 6;
                    label.uvsLength += 8;
                }
            }
            this.labelBuffers.push(pointBuffers);
        }
    }

    build(grid: Fbs.GridLineContainer, buffer: GeometryData, lineIndex?: number): void { }

    private buildGridLines(grid: Fbs.GridLineContainer, buffer: GeometryData): void {
        buffer.positions = new Float32Array(buffer.positionsLength);
        buffer.normals = new Int16Array(buffer.normalsLength);

        for (let i = 0, l = grid.gridLineSegmentsLength(); i < l; i++) {
            const segment = grid.gridLineSegments(i);
            const line = segment.lineSegment();
            LinePrimitiveBuilder.buildLine(line, buffer);
        }
    }

    private buildGridLabels(grid: Fbs.GridLineContainer, buffer: GeometryData, lineIndex: number): void {
        if (!buffer.positions) buffer.positions = new Float32Array(buffer.positionsLength);
        if (!buffer.uvs) buffer.uvs = new Float32Array(buffer.uvsLength);
        const segment = grid.gridLineSegments(lineIndex);
        const start = segment.lineSegment().start();
        const end = segment.lineSegment().end();
        buffer.positions.set([start.x(), start.y(), start.z(), end.x(), end.y(), end.z()], buffer.positionPointer);
        buffer.uvs.set([0,0,1,1,0,0,1,1], buffer.uvPointer);
        buffer.positionPointer += 6;
        buffer.uvPointer += 8;
    }

    private buildIntersections = (() => {
        const starts: Vector3[] = [];
        const ends: Vector3[] = [];
        const intersections: Vector3[] = [];
        const EPSILON = 0.0001;
        const start = new Vector3();
        const end = new Vector3();
        const v = new Vector3();
        const dir = new Vector3();
        const ray = new Ray();
        const point = new Vector3();
        const startToPoint = new Vector3();
        const endToPoint = new Vector3();

        return (vertices: Float32Array | Float64Array, length: number, outSurfaceNormal: Vector3): Vector3[] => {
            starts.length = 0;
            ends.length = 0;
            intersections.length = 0;
            for (let i=0; i<length; ++i) {
                start.set(vertices[i*6], vertices[i*6+1], vertices[i*6+2]);
                end.set(vertices[i*6+3], vertices[i*6+4], vertices[i*6+5]);
                v.copy(end).sub(start);
                dir.copy(v).normalize();
                ray.set(start, dir);
                for (let j=0; j<starts.length; ++j) {
                    const dist = ray.distanceSqToSegment(starts[j], ends[j], null, point);
                    if (dist < EPSILON) {
                        startToPoint.copy(point).sub(start);
                        endToPoint.copy(point).sub(end);
                        if (v.dot(startToPoint) >= -EPSILON && v.dot(endToPoint) <= EPSILON) {
                            intersections.push(point.clone());

                            // calculate grid surface normal, later will be used for surface picking
                            if (outSurfaceNormal.lengthSq() === 0)
                                outSurfaceNormal.crossVectors(dir, point.sub(starts[j]));
                        }
                    }
                }
                starts.push(start.clone());
                ends.push(end.clone());
            }
            return intersections.slice();
        };
    })();

    calculateBufferSizes(
        grid: Fbs.GridLineContainer,
        objectType?: ObjectType,
    ): GeometryData {
        if (objectType === ObjectType.POINT)
            return {
                objectType: ObjectType.POINT,
                indicesLength: 0,
                positionsLength: 6,
                normalsLength: 0,
                uvsLength: 8,
                pointSizesLength: 0,
                indexPointer: 0,
                positionPointer: 0,
            };
        else
            return {
                objectType: ObjectType.LINE,
                indicesLength: 0,
                positionsLength: grid.gridLineSegmentsLength() * 6,
                normalsLength: 0,
                uvsLength: 0,
                pointSizesLength: 0,
                indexPointer: 0,
                positionPointer: 0,
            };
    }

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickLine(geometryIndex, ray, frustum);
    }

    pickFace(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        if (this.surfaceNormals[geometryIndex].lengthSq() === 0) return;

        const points = this.pickLine(geometryIndex, ray, frustum);
        for (const p of points) p.normal = this.surfaceNormals[geometryIndex];
        return points;
    }

    pickLine = (() => {
        const glc = new Fbs.GridLineContainer();
        const start = new Vector3();
        const end = new Vector3();
        const hits = <HitPoint[]>[];

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            hits.length = 0;
            this.geometry.gridLineContainers(geometryIndex, glc);
            if (!this.buffers[geometryIndex].positions)
                this.getGeometries(geometryIndex, true);    // Build if empty
            const vs = this.buffers[geometryIndex].positions;
            for (let i=0, l=glc.gridLineSegmentsLength(); i<l; ++i) {
                start.set(vs[i*6], vs[i*6+1], vs[i*6+2]);
                end.set(vs[i*6+3], vs[i*6+4], vs[i*6+5]);
                intersectLine(hits, start, end, ray, Infinity);
            }
            return hits.filter(h => frustum.containsPoint(h.point));
        };
    })();

    pickPoint(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        if (!this.intersections[geometryIndex])
            this.getGeometries(geometryIndex, true);
        return this.intersections[geometryIndex]
            .filter(p => frustum.containsPoint(p))
            .map(p => ({ point: p.clone() }) as HitPoint);
    }
}
