import {
    AggregatePropertiesMessage,
    ICameraUpdateMessage,
    IFilterEntitiesMessage,
    IGetByStateMessage,
    IGetEntitiesMessage,
    IGetEntityInfoMessage,
    IGetHierarchyMessage,
    IGetHierarchyTreeMessage,
    IGetMaterialsMessage,
    IGetPropertyDefinitionsMessage,
    IGetPropertySetNamesMessage,
    IGetPropertyValuesMessage,
    IGuidsToEntityIdsMessage,
    IGetBoundingBoxesMessage,
    ILoadMessage,
    IMessage,
    IQueryMessage,
    IQueryPageMessage,
    IPickMessage,
    IUnloadMessage,
    MessageType,
    ICallMessage
} from "./ThreadWorkerMessages";
import { ThreadWorkerModel } from "./ThreadWorkerModel";
import { Frustum, Plane, Ray, Vector3 } from "./ThreeMath";

class ThreadWorker {
    private models: Map<string, ThreadWorkerModel> = new Map();
    private it = this as {[index: number]: any};

    constructor() {
        this.it[MessageType.LOAD] = (message: ILoadMessage) => {
            const model = new ThreadWorkerModel(message.modelId, message.buffer);
            this.models.set(message.modelId, model);

            message.buffer = undefined; // remove from response
            message.boundingBox = model.getBoundingBox();
        };

        this.it[MessageType.UNLOAD] = (message: IUnloadMessage) => {
            this.models.get(message.modelId).dispose();
            this.models.delete(message.modelId);
        };

        this.it[MessageType.PICK] = (message: IPickMessage) => {
            // snapped pick
            if (message.snapTypes) {
                message.hits = this.models.get(message.modelId).pickSnapped(this.restoreRay(message.ray), this.restoreFrustum(message.frustum), message.snapTypes, this.restorePlanes(message.clipPlanes));
            }

            // ray pick + frustum
            else if (message.ray) {
                const hit = this.models.get(message.modelId).pick(this.restoreRay(message.ray), this.restoreFrustum(message.frustum), this.restorePlanes(message.clipPlanes));
                if (hit) message.hits = [hit];
            }

            // frustum only pick
            else
                message.childrenEntityIds = this.models.get(message.modelId).frustumPick(this.restoreFrustum(message.frustum), this.restorePlanes(message.clipPlanes));
        };

        this.it[MessageType.ENTITY_GETENTITIESFROMIDS] = (message: IGetEntitiesMessage) => {
            message.entities = this.models.get(message.modelId).getEntities(message.entityIds);
            message.entityIds = undefined;
        };

        this.it[MessageType.ENTITY_GETINFO] = (message: IGetEntityInfoMessage) => {
            const model = this.models.get(message.modelId);
            const entityId = model.getEntityParentId(message.id, message.hierarchyType);

            message.entity = model.getEntity(entityId);
            message.product = model.getEntityProduct(message.id);
            message.properties = model.getEntityProperties(entityId);
        };

        this.it[MessageType.ENTITY_GETHIERARCHY] = (message: IGetHierarchyMessage) => {
            message.hierarchy = this.models.get(message.modelId).getHierarchy(message.hierarchyType, message.options);
        };

        this.it[MessageType.ENTITY_GETHIERARCHYTREE] = (message: IGetHierarchyTreeMessage) => {
            const tree = this.models.get(message.modelId).getHierarchyTree(message.entityIds, message.hierarchyType);
            message.parentIds = tree.parentIds;
            message.childrenIds = tree.childrenIds;
            message.entityIds = undefined;
        };

        this.it[MessageType.CAMERA_UPDATE] = (message: ICameraUpdateMessage) => {
            this.models.get(message.modelId).setCamera(this.restoreFrustum(message.frustum), this.restoreVector(message.position));
        };

        this.it[MessageType.GET_BOUNDINGBOXES] = (message: IGetBoundingBoxesMessage) => {
            message.boundingBoxes = [];
            for (const entityIds of message.entityIdGroups) {
                const box = this.models.get(message.modelId).getEntitiesBoundingBox(entityIds);
                message.boundingBoxes.push(box);
            }
            message.entityIdGroups = undefined;
        };

        this.it[MessageType.PROPERTY_GETSETNAMES] = (message: IGetPropertySetNamesMessage) => {
            message.names = this.models.get(message.modelId).getPropertySetNames();
        };

        this.it[MessageType.PROPERTY_GETPROPERTYDEFINITIONS] = (message: IGetPropertyDefinitionsMessage) => {
            message.properties = this.models.get(message.modelId).getPropertyDefinitions(message.setName);
        };

        this.it[MessageType.PROPERTY_GETPROPERTYVALUES] = (message: IGetPropertyValuesMessage) => {
            message.properties = this.models.get(message.modelId).getPropertyValues(message.propertyName);
        };

        this.it[MessageType.ENTITY_FILTER] = (message: IFilterEntitiesMessage) => {
            message.entities = this.models.get(message.modelId).getEntitiesByFilter(message.conditions);
        };

        this.it[MessageType.GUIDS_TO_ENTITYIDS] = (message: IGuidsToEntityIdsMessage) => {
            message.entityIds = this.models.get(message.modelId).guidsToEntityIds(message.guids);
        };

        this.it[MessageType.QUERY] = (message: IQueryMessage) => {
            this.models.get(message.modelId).query(message.query);
        };

        this.it[MessageType.QUERYPAGE] = (message: IQueryPageMessage) => {
            message.data = this.models.get(message.modelId).queryPage(message.streamId, message.page, message.pageSize);
        };

        this.it[MessageType.GET_BY_STATE] = (message: IGetByStateMessage) => {
            message.entityIds = this.models.get(message.modelId).getEntityIdsByState(message.visible, message.pickPriority);
        };

        this.it[MessageType.AGGREGATE_PROPERTIES] = (message: AggregatePropertiesMessage) => {
            this.models.get(message.modelId).aggregateProperties(message.entityIds, message.aggregations);
            message.entityIds = undefined;
        };

        this.it[MessageType.GET_MATERIALS] = (message: IGetMaterialsMessage) => {
            message.materials = this.models.get(message.modelId).getMaterials(message.entityIds);
            message.entityIds = undefined;
        };

        this.it[MessageType.CALL] = (message: ICallMessage) => {
            const model = this.models.get(message.modelId) as any;
            message.result = model[message.methodName].apply(model, message.args);
            message.args = undefined; // save memory (same message is sent back)
        };
    }

    private restoreVector(v: Vector3): Vector3 {
        return new Vector3().copy(v);
    }

    private restorePlanes(planes: Plane[]): Plane[] {
        if (!planes) return planes;
        return planes.map(p => new Plane(this.restoreVector(p.normal), p.constant));
    }

    private restoreFrustum(frustum: Frustum): Frustum {
        if (!frustum) return;
        const p = this.restorePlanes(frustum.planes);
        return new Frustum(p[0], p[1], p[2], p[3], p[4], p[5]);
    }

    private restoreRay(ray: Ray): Ray {
        return new Ray(this.restoreVector(ray.origin), this.restoreVector(ray.direction));
    }

    onMessage(e: MessageEvent): void {
        if (!e.data) return; // This might happen if worker is very busy. why??
        const message = e.data as IMessage;

        try {
            this.it[message.type](message);
            postMessage(message);
        } catch (error) {
            console.error(error);
            message.error = error.toString();
            postMessage(message);
        }
    }
}

declare var self: DedicatedWorkerGlobalScope;
const threadWorker = new ThreadWorker();
self.onmessage = (e: MessageEvent) => threadWorker.onMessage(e);

