import { UUID } from "../UUID";
import {
    IAdd,
    IRemove,
    IMetaMaterial,
    ISetBatchState, IUpdateFinished
} from "../Model/ModelBuilderTypes";
import { Plane, Box3, Frustum, Vector3, Ray } from "./ThreeMath";
import {IdentifierType, PropertyDefinition, PropertySet, Query, TrimbimPickPriority} from "../common";
import { flatbuffers } from "flatbuffers";
import { PropertyAggregation } from "../common";
import { Hit } from "./HitPoint";
import {Trimble} from "../TrimBim_generated";
import HierarchyType = Trimble.Technology.TrimBim.Fbs.HierarchyType;
import {SnapType} from "@technology/webgl-viewer/dist/esnext/common";

export interface IMessage {
    type: MessageType;
    messageId?: number; // the request/message/event correlation identifier
    error?: string; // output
    modelId: string;
}

export class Hierarchy {
    products?: Map<number, Product>;
    entities?: Map<number, Entity>;
    propertySets?: Map<number, PropertySet>;
    hierarchy: EntityInfo[];
}

export class EntityInfo {
    id: number;
    productId?: number;
    propertySetIds?: number[];
    children?: EntityInfo[];
}

export interface Product {
    name: string;
    description: string;
    objectType: string;
}

export class Entity {
    id: number;
    identifierType: IdentifierType;
    identifier: string | UUID | number[];
    class: string;
}

export interface ILoadMessage extends IMessage {
    type: MessageType.LOAD;
    buffer: ArrayBuffer;
    boundingBox?: Box3;
}

export interface IUnloadMessage extends IMessage {
    type: MessageType.UNLOAD;
}

export interface IGetEntitiesMessage extends IMessage {
    type: MessageType.ENTITY_GETENTITIESFROMIDS;
    entityIds: number[]; // input
    entities?: Entity[]; // output
}

export interface IGetEntityInfoMessage extends IMessage {
    type: MessageType.ENTITY_GETINFO;
    id: number; // input, entityId
    entity?: Entity; // output
    properties?: PropertySet[]; // output
    product?: Product; // output
    hierarchyType: HierarchyType;
}

export interface IGetPropertySetNamesMessage extends IMessage {
    type: MessageType.PROPERTY_GETSETNAMES;
    names?: string[]; // output
}

export interface IGetPropertyDefinitionsMessage extends IMessage {
    type: MessageType.PROPERTY_GETPROPERTYDEFINITIONS;
    setName: string; // input, setId
    properties?: PropertyDefinition[]; // output
}

export interface IGetPropertyValuesMessage extends IMessage {
    type: MessageType.PROPERTY_GETPROPERTYVALUES;
    propertyName: string;
    properties?: Array<string | number | flatbuffers.Long>; // output
}

export interface IFilterEntitiesMessage extends IMessage {
    type: MessageType.ENTITY_FILTER;
    conditions: any; // input
    entities?: number[]; // output
}

export interface IQueryPageMessage extends IMessage {
    type: MessageType.QUERYPAGE;
    streamId: number;
    page: number;
    pageSize: number;
    data?: Array<Array<{[name: string]: (string | number);}>>; // output
}

export interface IGuidsToEntityIdsMessage extends IMessage {
    type: MessageType.GUIDS_TO_ENTITYIDS;
    guids: string[];
    entityIds?: number[];
}

export interface IGetHierarchyMessage extends IMessage {
    type: MessageType.ENTITY_GETHIERARCHY;
    hierarchyType: HierarchyType; // input
    options?: any; // optional input: parentId, entity, product, properties
    hierarchy?: Hierarchy; // output
}

export interface IGetHierarchyTreeMessage extends IMessage {
    type: MessageType.ENTITY_GETHIERARCHYTREE;
    hierarchyType: HierarchyType;
    entityIds: number[];
    parentIds?: number[];
    childrenIds?: number[];
}

export interface ICameraUpdateMessage extends IMessage {
    type: MessageType.CAMERA_UPDATE;
    frustum: Frustum;
    position: Vector3;
}

export const enum StateType {
    MATERIAL,
    VISIBILITY,
    SELECTION,
    HOVER,
    PICK_PRIORITY
}

export interface IGetBoundingBoxesMessage extends IMessage {
    type: MessageType.GET_BOUNDINGBOXES;
    entityIdGroups: number[][];
    boundingBoxes?: Box3[];
}

export interface UpdateGeometryMessage extends IMessage {
    type: MessageType.UPDATE_GEOMETRY;
    actions: Array<IAdd | IRemove | ISetBatchState | IUpdateFinished>;
}

export interface IPickMessage extends IMessage {
    type: MessageType.PICK;
    ray: Ray;
    frustum: Frustum;
    snapTypes?: SnapType[];
    clipPlanes: Plane[];
    hits?: Hit[];
    childrenEntityIds?: number[];
}

export interface ITextureMessage extends IMessage {
    type: MessageType.UPDATE_TEXTURE;
    textureId: number;
    image: Int8Array;
}

export interface ITrimbimMaterialsMessage extends IMessage {
    type: MessageType.UPDATE_MATERIAL;
    materials: IMetaMaterial[];
}

export interface IGetMaterialsMessage extends IMessage {
    type: MessageType.GET_MATERIALS;
    entityIds: number[];
    materials?: IMetaMaterial[];
}

export interface IQueryMessage extends IMessage {
    type: MessageType.QUERY;
    query: Query;
}

export interface IGetByStateMessage extends IMessage {
    type: MessageType.GET_BY_STATE;
    visible?: boolean;
    pickPriority?: TrimbimPickPriority;
    entityIds?: number[];
}

export interface AggregatePropertiesMessage extends IMessage {
    type: MessageType.AGGREGATE_PROPERTIES;
    entityIds: number[];
    aggregations: PropertyAggregation[];
}

export interface ICallMessage extends IMessage {
    type: MessageType.CALL;
    methodName: string,
    args: any[];
    result: any;
}

export const enum MessageType {
    // TODO: most (all?) messages can be replaced with just CALL message
    CALL,

    UPDATE_GEOMETRY,
    UPDATE_TEXTURE,
    UPDATE_MATERIAL,
    LOAD,
    UNLOAD,
    GET_MATERIALS,
    CAMERA_UPDATE,
    GET_BOUNDINGBOXES,
    PICK,


    // TODO: rework the data query logic, all these messages can be optimized to 1-2 messge types
    QUERY,
    QUERYPAGE,
    GET_BY_STATE,
    PROPERTY_GETSETNAMES,
    PROPERTY_GETPROPERTYDEFINITIONS,
    ENTITY_FILTER,
    PROPERTY_GETPROPERTYVALUES,
    GUIDS_TO_ENTITYIDS,
    ENTITY_GETHIERARCHY,
    ENTITY_GETHIERARCHYTREE,
    ENTITY_GETENTITIESFROMIDS,
    ENTITY_GETINFO,
    AGGREGATE_PROPERTIES
}
