import {
    Mesh8GeometryBuilder,
    Mesh8TexturedGeometryBuilder,
    MeshGeometryBuilder,
    MeshTexturedGeometryBuilder
} from "./GeometryBuilders/MeshGeometryBuilder";
import {
    BRepGeometryBuilder,
    BRepWithNormalsGeometryBuilder,
    BRepWithUVGeometryBuilder
} from "./GeometryBuilders/BRepGeometryBuilder";
import { GraphicsGeometryBuilder } from "./GeometryBuilders/GraphicsGeometryBuilder";
import { SweptDiskSolidGeometryBuilder } from "./GeometryBuilders/SweptDiskSolidGeometryBuilder";
import { Trimble } from "../TrimBim_generated";
import { HitPoint } from "./HitPoint";
import { GeometryData, GeometryBuilder } from "./GeometryBuilders/GeometryBuilder";
import { TexturedBillboardGeometryBuilder, BillboardTextGeometryBuilder } from "./GeometryBuilders/BillboardGeometryBuilder";
import { TrimbimGeometryType } from "../common";
import { Frustum, Ray } from "./ThreeMath";
import { GridGeometryBuilder } from "./GeometryBuilders/GridGeometryBuilder";
import { AlignmentGeometryBuilder } from "./GeometryBuilders/AlignmentGeometryBuilder";
import { LodManager } from "./LodManager";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { InstanceManager } from "./InstanceManager";
import {GeometryType, SnapType} from "@technology/webgl-viewer/dist/esnext/common";

export class GeometryManager {
    private geometryBuilders: Map<TrimbimGeometryType, GeometryBuilder>;

    constructor(modelId: string, private geometry: Fbs.ModelGeometry, instanceManager: InstanceManager, private _lodManager: LodManager) {
        this.geometryBuilders = new Map();

        this.registerGeometryBuilder(new BRepGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new BRepWithNormalsGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new BRepWithUVGeometryBuilder(this.geometry));

        this.registerGeometryBuilder(new MeshGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new Mesh8GeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new MeshTexturedGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new Mesh8TexturedGeometryBuilder(this.geometry));

        this.registerGeometryBuilder(new GraphicsGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new SweptDiskSolidGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new GridGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new TexturedBillboardGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new BillboardTextGeometryBuilder(this.geometry));
        this.registerGeometryBuilder(new AlignmentGeometryBuilder(modelId, this.geometry, instanceManager, this._lodManager));
    }

    getEntityId = (() => {
        const instance = new Fbs.GeometryInstance();

        return (instanceId: number) => {
            this.geometry.instances(instanceId, instance);
            return instance.entityId();
        };
    })();

    public registerGeometryBuilder(handler: GeometryBuilder): void {
        this.geometryBuilders.set(handler.geometryType, handler);
    }

    public getGeometries(instanceId: number, generate: boolean): GeometryData[] {
        const instance = this.geometry.instances(instanceId);
        const definition = this.geometry.definitions(instance.definitionId());
        const type = definition.type() as any as TrimbimGeometryType;
        const geometryIndex: number = definition.index();

        const builder = this.geometryBuilders.get(type);
        if (!builder) {
            console.warn("Unsupported geometry type:", type);
            return [];
        }

        return builder.getGeometries(geometryIndex, generate);
    }

    getIsMesh(geometryType: TrimbimGeometryType): boolean {
        return this.geometryBuilders.has(geometryType) && this.geometryBuilders.get(geometryType).snapGeometryType === GeometryType.MESH;
    }

    public pick(definition: Fbs.GeometryDefinition, snapType: SnapType, ray: Ray, frustum: Frustum): HitPoint[] {
        const type: number = definition.type();
        const geometryIndex: number = definition.index();

        const gb = this.geometryBuilders.get(type);
        if (gb === undefined) return;
        const points = snapType === SnapType.POINT ? gb.pickPoint(geometryIndex, ray, frustum) :
                       snapType === SnapType.LINE ? gb.pickLine(geometryIndex, ray, frustum) :
                       snapType === SnapType.FACE ? gb.pickFace(geometryIndex, ray, frustum) :
                       gb.pick(geometryIndex, ray, frustum);
        if (points) {
            for (const p of points)
                p.snapGeometryType = gb.snapGeometryType;
        }
        return points;
    }
}
