import { EntityManager } from "../EntityManager";
import { parse, parseHierarchy } from "./Parser";
import { Query } from "../../common";
import { InstanceManager } from "../InstanceManager";

export const find = (entityManager: EntityManager, instanceManager: InstanceManager, request: Query) => {
    const results: any = {
        entityIds: new Set()
    };
    const hierarchy = request.find.$hierarchy ? parseHierarchy(request.find.$hierarchy) : undefined;

    const filter = request.find.$filter ? parse(request.find.$filter) : undefined;

    const product = request.find.$product ? parse(request.find.$product) : undefined;

    const entity = request.find.$entity ? parse(request.find.$entity) : undefined;

    const definition = request.find.$definition ? parse(request.find.$definition) : undefined;

    const selector = request.find.$selector ? parse(request.find.$selector) : undefined;

    if (filter) {
        results.entityIds = entityManager.filterProperties(filter);
    }

    if (product) {
        results.entityIds = new Set([
            ...entityManager.findProducts(product),
            ...results.entityIds
        ]);
    }

    if (entity) {
        results.entityIds = new Set([
            ...entityManager.filterEntities(entity),
            ...results.entityIds
        ]);
    }

    if (definition) {
        results.entityIds = new Set([
            ...instanceManager.filterDefinition(entityManager.getAllEntityIds(), definition),
            ...results.entityIds
        ]);
    }

    if (hierarchy) {
        results.entityIds = new Set([
            ...entityManager.queryHierarchy(hierarchy),
            ...results.entityIds
        ]);
    }

    if (!filter && !hierarchy && !product && !entity && !definition) {
        results.entityIds = entityManager.getAllEntityIds();
    }

    if (request.find.stream) {
        const { streamId, length } = entityManager.startPropertyStreaming(
            results.entityIds,
            selector
        );
        request.output = {
            find: {
                streamId,
                length
            }
        };

        return request;
    } else {
        if (!results.entityIds) results.entityIds = new Set();
        request.output = {
            find: {
                entityIds: [...results.entityIds]
            }
        };

        return request;
    }
};
