import { Operation, wrapperOperation } from "./Parser";

export interface IExpressionConstructor {
    token: string;
    operation?: Operation;
    isSelector?: boolean;
    wrapperOperation?: wrapperOperation;
    reducerSelector?: string;
    reducerName?: string;
}

export class LogicalNode {
    children: LogicalNode[];

    constructor(public expressions?: Expression[]) {
        this.children = [];
    }

    getIds(): Set<number> {
        return this.expressions[0].entityIds;
    }
}

export class AndWrapper extends LogicalNode {
    children: LogicalNode[];

    constructor(public expressions?: Expression[]) {
        super();
    }

    getIds(): Set<number> {
        const iterator = this.expressions.values();
        const firstExpression = iterator.next().value;

        if (!firstExpression) return new Set();

        const entityIds = firstExpression.entityIds;

        for (const expression of iterator) {
            for (const id of entityIds) {
                if (!expression.entityIds.has(id)) {
                    entityIds.delete(id);
                }
            }
        }
        return entityIds;
    }
}

export class OrWrapper extends LogicalNode {
    children: LogicalNode[];

    constructor(public expressions: Expression[]) {
        super();
    }

    getIds() {
        const result: Set<number> = new Set();

        for (const expression of this.expressions) {
            for (const id of expression.entityIds) {
                result.add(id);
            }
        }
        return result;
    }
}

export class Expression {
    propertyName: string;
    setName: string;
    parent: number;
    wrapperOperation: wrapperOperation;
    token: string;
    operations?: Operation[];
    isSelector: boolean;
    isOperation: boolean;
    isLogical: boolean;
    entityIds: Set<number>;
    type?: number;

    constructor({
        token,
        operation,
        isSelector,
        wrapperOperation
    }: IExpressionConstructor) {
        this.token = token;
        this.operations = operation ? [operation] : [];

        this.isSelector = !!isSelector;
        this.isOperation = !isSelector;
        this.isLogical = !!wrapperOperation;

        if (this.isSelector) {
            const [setName, propertyName] = token.split(".");
            this.setName = setName;
            this.propertyName = propertyName;
        }

        if (this.isLogical) {
            this.wrapperOperation = wrapperOperation;
        }

        this.entityIds = new Set();
    }

    addOperations(operation: Operation[]) {
        this.operations.push(...operation);
    }

    combineOperations(expressions: Expression[]) {
        if (!this.wrapperOperation)
            throw new Error("Error: Trying to combine without combine operation");

        const operations: Operation[] = [];
        for (const expression of expressions) {
            if (!expression.operations)
                throw new Error("Syntax Error, Expression without operation");
            operations.push(...expression.operations);
        }
        this.operations = [this.wrapperOperation(operations)];
    }

    combineExpressions(children: Expression[]) {
        if (this.isSelector) {
            for (const child of children) {
                if (child.isOperation) {
                    this.addOperations(child.operations);
                }
            }
        } else if (this.isOperation) {
            if (this.isLogical) {
                this.combineOperations(children);
            }
        }
    }
}
