import { Trimble } from "../TrimBim_generated";
import { Entity, EntityInfo, Hierarchy, Product } from "./ThreadWorkerMessages";
import { HierarchyOptions, HierarchyType, IdentifierType, PropertyDefinition, Property, PropertySet, PropertyType } from "../common";
import { UUID } from "../UUID";
import { HierarchyQuery, Operation } from "./QueryHandling/Parser";
import { Expression, LogicalNode } from "./QueryHandling/Expression";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { flatbuffers } from "flatbuffers";

export type IndexedExpressions = { expression: Expression; definitionIds: Map<number, number> };

export class EntityManager {
    private hierarchyIndex: Map<HierarchyType, Array<Set<number>>> = new Map();
    private productIndex: Array<number>;
    private propertyIndex: Array<Set<number>>;
    streams: {
        entityIds: number[];
        setIds: Set<number>;
    }[];

    constructor(private modelEntities: Fbs.ModelEntities, private properties: Fbs.ModelProperties) {
        this.streams = [];
    }

    getProductId(entityId: number): number {
        if (entityId === undefined) {
            throw new RangeError("Must provide entityId");
        }

        const index = this.loadProductIndex();
        return index[entityId];
    }

    getProduct(productId: number): Product {
        if (
            productId === undefined ||
            productId < 0 ||
            productId >= this.properties.productsLength()
        ) {
            throw new Error("Must provide a valid productId");
        }

        const p = this.properties.products(productId);
        const product: Product = {
            name: this.properties.names(p.name()),
            description: this.properties.descriptions(p.description()),
            objectType: this.properties.objectTypes(p.objectType())
        };

        return product;
    }

    getPropertySetIdsFromEntity(entityId: number): Set<number> {
        if (entityId === undefined) {
            throw new RangeError("Must provide entityId");
        }

        const index = this.loadPropertyIndex();
        return index[entityId];
    }

    getPropertySet(propSetId: number): PropertySet {
        if (propSetId === undefined)
            throw new RangeError("Must provide propSetId");

        const bindings = this.properties.propertySetBindings(propSetId);
        const definition = this.properties.definitions(bindings.definition());
        const properties = this.getSinglePropertyDefinitions(definition, bindings);

        return { name: this.properties.propertySetNames(definition.name()), properties } as PropertySet;
    }

    getPropertySetNames(): string[] {
        const result = new Array<string>(this.properties.propertySetNamesLength());

        for (let i = 0; i < result.length; i++) {
            result[i] = this.properties.propertySetNames(i);
        }

        return result;
    }

    getSetNameId(name: string): number {
        const length = this.properties.propertySetNamesLength();

        for (let i = 0; i < length; i++) {
            if (this.properties.propertySetNames(i) === name) {
                return i;
            }
        }
    }

    getPropertySetIdsFromName(name: string): Set<number> {
        const id = this.getSetNameId(name);

        let length = this.properties.definitionsLength();
        const definitionIds = new Set();
        for (let i = 0; i < length; i++) {
            const definition = this.properties.definitions(i);

            if (id === definition.name()) {
                definitionIds.add(i);
            }
        }

        length = this.properties.propertySetBindingsLength();

        const setIds = new Set<number>();
        for (let i = 0; i < length; i++) {
            const binding = this.properties.propertySetBindings(i);

            if (definitionIds.has(binding.definition())) {
                setIds.add(i);
            }
        }

        return setIds;
    }

    getDefinitionIdsFromNames(setName: string, propertyName?: string): Map<number, number> {
        const setNameId = this.getSetNameId(setName);
        const propertyNameIds = propertyName ? this.getPropertyNameIds(propertyName) : undefined;

        const length = this.properties.definitionsLength();
        const definitionIds = new Map();

        for (let i = 0; i < length; i++) {
            const definition = this.properties.definitions(i);

            if (setNameId === definition.name()) {
                if (propertyNameIds !== undefined) {
                    const l = definition.propertiesLength();
                    for (let j = 0; j < l; j++) {
                        const property = definition.properties(j);
                        if (propertyNameIds.has(property.name())) {
                            definitionIds.set(i, j);
                        }
                    }
                } else {
                    definitionIds.set(i, undefined);
                }
            }
        }

        return definitionIds;
    }

    getPropertyDefinitions(propertySetName: string): PropertyDefinition[] {
        if (propertySetName === undefined) {
            throw new Error("Must provide propSetName");
        }
        const propSetIds = this.getDefinitionIdsFromNames(propertySetName);
        if (propSetIds.size === 0) {
            throw new Error(`No property set name found matching ${propertySetName} `);
        }

        const result = new Map<string, PropertyDefinition>();
        for (const setId of propSetIds.keys()) {
            const setDefinition = this.properties.definitions(setId);
            for (let i = 0, l = setDefinition.propertiesLength(); i < l; i++) {
                const name = this.properties.propertyNames(setDefinition.properties(i).name());
                if (!result.has(name))
                    result.set(name, {
                        name: name,
                        type: setDefinition.properties(i).type() as unknown as PropertyType
                    });
            }
        }
        return Array.from(result.values());
    }

    getPropertyNameIds(name: string): Set<number> {
        const length = this.properties.propertyNamesLength();
        const indices = new Set<number>();
        for (let i = 0; i < length; i++) {
            if (name === this.properties.propertyNames(i)) {
                indices.add(i);
            }
        }

        return indices;
    }

    getPropertyValues(propertyName: string): Array<string | number | flatbuffers.Long> {
        if (propertyName === undefined) {
            throw new Error("Must provide propertyName");
        }

        const propertyIds = this.getPropertyNameIds(propertyName);

        if (propertyIds === undefined) {
            throw new Error(`PropertyName ${propertyName} does not exist`);
        }

        const result = new Set<string | number | flatbuffers.Long>();

        const bindingsCount = this.properties.propertySetBindingsLength();
        for (let i = 0; i < bindingsCount; i++) {
            const setBinding = this.properties.propertySetBindings(i);
            const setDefinition = this.properties.definitions(setBinding.definition());
            const propertyCount = setDefinition.propertiesLength();

            for (let propertyIdx = 0; propertyIdx < propertyCount; propertyIdx++) {
                const nameIndex = setDefinition.properties(propertyIdx).name();
                if (propertyIds.has(nameIndex)) {
                    const type = setDefinition.properties(propertyIdx).type();
                    result.add(this.getValue(type, setBinding.values(propertyIdx)));
                }
            }
        }

        return [...result];
    }

    queryHierarchy(object: any): Set<number> {
        const queries: HierarchyQuery[] = object["$AND"];
        const result: number[] = [];

        for (const query of queries) {
            const type = query.$HIERARCHYTYPE;
            switch (query.$SCOPE) {
                case "Descendants": {
                    if (query.$ENTITYID) {
                        for (const id of query.$ENTITYID) {
                            result.push(...this.getChildren(id, type));
                        }
                    }

                    if (query.$UNIQUEIDENTIFIERS) {
                        const ids = this.getEntityIdsFromGuids(query.$UNIQUEIDENTIFIERS);
                        for (const id of ids) {
                            result.push(...this.getChildren(id, type));
                        }
                    }
                    break;
                }
                case "DescendantsAndSelf": {
                    if (query.$ENTITYID) {
                        for (const id of query.$ENTITYID) {
                            result.push(...this.getChildren(id, type));
                            result.push(id);
                        }
                    }

                    if (query.$UNIQUEIDENTIFIERS) {
                        const ids = this.getEntityIdsFromGuids(query.$UNIQUEIDENTIFIERS);
                        for (const id of ids) {
                            result.push(...this.getChildren(id, type));
                            result.push(id);
                        }
                    }
                    break;
                }
                case "Root": {
                    if (query.$ENTITYID) {
                        for (const id of query.$ENTITYID) {
                            const parentId = this.getParents([id], type)[0];
                            result.push(parentId !== undefined ? parentId : id);
                        }
                    }

                    if (query.$UNIQUEIDENTIFIERS) {
                        const ids = this.getEntityIdsFromGuids(query.$UNIQUEIDENTIFIERS);
                        for (const id of ids) {
                            const parentId = this.getParents([id], type)[0];
                            result.push(parentId !== undefined ? parentId : id);
                        }
                    }
                    break;
                }
                default:
                    throw new Error(`Unknown scope ${query.$SCOPE}`);
            }
        }

        return new Set(result);
    }

    indexEntityQuery(expressions: Expression[]): IndexedExpressions[] {
        const all: IndexedExpressions[] = [];
        for (const expression of expressions) {
            const { propertyName, setName } = expression;
            const definitionIds = this.getDefinitionIdsFromNames(setName, propertyName);
            all.push({
                expression,
                definitionIds
            });
        }

        return all;
    }

    indexProductQuery(expressions: Expression[]): Map<string, Set<number>> {
        const productIndex: Map<string, Set<number>> = new Map();

        for (const expression of expressions) {
            const productProperty = expression.setName;
            let ids: Set<number>;

            if (productIndex.has(productProperty)) {
                ids = productIndex.get(productProperty);
            } else {
                ids = new Set();
                productIndex.set(productProperty, ids);
            }

            if (productProperty === "name") {
                for (let i = 0, l = this.properties.namesLength(); i < l; i++) {
                    const name = this.properties.names(i);
                    if (expression.operations[0](name))
                        ids.add(i);
                }
            }
        }

        return productIndex;
    }

    getAllEntityIds(): Set<number> {
        const l = this.modelEntities.entitiesLength();
        const allEntities = new Set<number>();

        for (let i = 0; i < l; i++) {
            allEntities.add(i);
        }

        return allEntities;
    }

    findProducts(node: LogicalNode): Set<number> {
        if (node.expressions.length === 0) {
            return this.getAllEntityIds();
        }

        const productBindingCandidates = new Set();
        const conditions = this.indexProductQuery(node.expressions);
        const productCount = this.properties.productsLength();

        for (let i = 0; i < productCount; i++) {
            const product = this.properties.products(i);

            for(const [condition, acceptedIds] of conditions) {

                if (condition === 'productType') {
                    if (acceptedIds.has(product.objectType())) {
                        productBindingCandidates.add(i);
                    }
                }

            }
        }

        const entityIds = new Set<number>();

        const productIndex = this.loadProductIndex();

        for (let i = 0, l = productIndex.length; i < l; i++) {
            if (productBindingCandidates.has(productIndex[i])) {
                entityIds.add(productIndex[i]);
            }
        }

        return entityIds;
    }

    filterEntities(node: LogicalNode): Set<number> {
        if (node.expressions.length === 0) {
            return this.getAllEntityIds();
        }

        const rootExpression = node.expressions[0];
        const validator = rootExpression.operations[0];
        const validClassIndexes = new Set();
        const entityIds = new Set<number>();

        if (rootExpression.token === 'class') {
            for (let i = 0, l = this.modelEntities.entityClassesLength(); i < l; i++) {
                const className = this.modelEntities.entityClasses(i);
                if (validator(className)) {
                    validClassIndexes.add(i);
                }
            }

            const entity = new Fbs.Entity();
            for (let i = 0, l = this.modelEntities.entitiesLength(); i < l; i++) {
                this.modelEntities.entities(i, entity);
                if (validClassIndexes.has(entity.classIndex())) {
                    entityIds.add(i);
                }
            }
        }

        return entityIds;
    }

    /**
     * Find identifiers of entities which satisfy conditions by checking property set definitions directly.
     */
    filterProperties(node: LogicalNode): Set<number> {
        if (node.expressions.length === 0) {
            return this.getAllEntityIds();
        }

        const addCandidate = (condition: IndexedExpressions, bindingId: number) => {
            if (candidatesBySetId.has(condition)) {
                candidatesBySetId.get(condition).push(bindingId);
            } else {
                candidatesBySetId.set(condition, [bindingId]);
            }
        };

        const conditions = this.indexEntityQuery(node.expressions);
        const bindingsCount = this.properties.propertySetBindingsLength();
        const candidatesBySetId: Map<IndexedExpressions, number[]> = new Map();

        for (let i = 0; i < bindingsCount; i++) {
            const setBinding = this.properties.propertySetBindings(i);
            const setDefinitionId = setBinding.definition();

            for (const condition of conditions) {
                if (condition.definitionIds.has(setDefinitionId)) {
                    const operation = condition.expression.operations[0];
                    const propertyDefinitionId = condition.definitionIds.get(setDefinitionId);

                    if (operation) {
                        if (
                            this.checkEntityConditions(
                                setDefinitionId,
                                propertyDefinitionId,
                                operation,
                                setBinding
                            )
                        ) {
                            addCandidate(condition, i);
                        }
                    } else {
                        addCandidate(condition, i);
                    }
                }
            }
        }

        for (const [candidates, ids] of candidatesBySetId) {
            candidates.expression.entityIds = this.getEntitiesFromBinding(ids);
        }

        return node.getIds();
    }

    startPropertyStreaming(entityIds: Set<number>, filter: LogicalNode) {
        const setIds = this.getPropertySetIdsFromName(filter.expressions[0].setName);
        this.streams.push({ entityIds: [...entityIds], setIds });
        return { streamId: 0, length: entityIds.size };
    }

    getPropertyStreamPage(streamId: number, page: number, pageSize: number): Array<Array<{[name: string]: (string | number);}>> {
        const { entityIds, setIds } = this.streams[streamId];

        const data = [];
        const startIndex = page * pageSize;
        const length = Math.min(startIndex + pageSize, entityIds.length);

        for (let i = startIndex; i < length; i++) {
            const entityId = entityIds[i];
            const entitySetIds = this.getPropertySetIdsFromEntity(entityId);
            const row: {[name: string]: (string | number);}[] = [];

            for (const propertySetId of entitySetIds) {
                if (setIds.has(propertySetId)) {
                    const properties = this.getPropertySet(propertySetId).properties;
                    const obj: {[name: string]: (string | number);} = {};
                    for (const prop of properties) {
                        obj[prop.name] = prop.value;
                    }
                    row.push(obj);
                }
            }
            data.push(row);
        }

        return data;
    }

    getParents(entityIds: number[], type: HierarchyType): number[] {
        const node = new Fbs.HierarchyNode();

        let input = new Map<number, number>();
        entityIds.forEach((v, i) => input.set(v, i));
        let input2 = new Map<number, number>();
        const parentIds = new Set<number>();

        while (input.size > 0) {
            for (let i = 0, l = this.modelEntities.hierarchiesLength(); i < l; i++) {
                this.modelEntities.hierarchies(i, node);
                if (node.type() !== type) continue;

                const index = input.get(node.childElementId());
                if (index !== undefined) {
                    const parentId = node.parentElementId();
                    parentIds.delete(index);
                    parentIds.add(parentId);
                    input2.set(parentId, index);
                }
            }
            input.clear();
            const tmp = input2;
            input2 = input;
            input = tmp;
        }

        return Array.from(parentIds);
    }

    getChildren(entityId: number, type: HierarchyType): Set<number> {
        const hierarchyIndex = this.loadHierarchyIndex(type);
        const children = hierarchyIndex[entityId];

        if (!children) return new Set();

        const ids = new Set([...children]);
        const childrensChildren = new Set<number>();

        for (const child of children) {
            const newChildren = this.getChildren(child, type);
            if (newChildren) newChildren.forEach(c => childrensChildren.add(c));
        }

        childrensChildren.forEach(c => ids.add(c));

        return ids;
    }

    getHierarchyTree(entityIds: number[], hierarchyType: HierarchyType): {parentIds: number[], childrenIds?: number[]} {
        const childrenIds = new Set<number>(entityIds);
        const parentIds = this.getParents(entityIds, hierarchyType);
        for (const parentId of parentIds) {
            const children = this.getChildren(parentId, hierarchyType);
            children.forEach(id => childrenIds.add(id));
        }
        return { parentIds: parentIds, childrenIds: [...childrenIds] };
    }

    private checkEntityConditions(
        setDefinitionId: number,
        propertyIdx: number,
        propertyCondition: Operation,
        setBinding: Fbs.PropertySet
    ): boolean {
        const setDefinition = this.properties.definitions(setDefinitionId);
        const type = setDefinition.properties(propertyIdx).type();
        const value = this.getValue(type, setBinding.values(propertyIdx));

        return propertyCondition(value);
    }

    /**
     * Extracts entity ids from an array of property set binding ids.
     */
    private getEntitiesFromBinding(bindingIds: number[]): Set<number> {
        const candidateSet = new Set<number>();

        for (const bindingId of bindingIds) {
            const binding = this.properties.propertySetBindings(bindingId);
            const entitiesLength = binding.entitiesLength();

            for (let i = 0; i < entitiesLength; i++) {
                candidateSet.add(binding.entities(i));
            }
        }

        return candidateSet;
    }

    // Fetches the whole hierarchy at once
    // Possible options:
    //  - if parentId is passed then subtree starting from this node is returned
    //  - if "entity" property is passed then Entity information is fetched
    //  - if "product" property is passed then Product information is fetched
    //  - if "properties" property is passed then PropetySet[] information is fetched
    getHierarchy(type: HierarchyType, options?: HierarchyOptions): Hierarchy {
        if (type === undefined) {
            throw new RangeError("Must provide hierarchy type");
        }

        const result = new Hierarchy();

        // normalize arguments before passing them to internal method
        if (!(options === null || options === undefined)) {
            if (options.entity === true) {
                result.entities = new Map();
            }
            if (options.product === true) {
                result.products = new Map();
            }
            if (options.properties === true) {
                result.propertySets = new Map();
            }
        }

        result.hierarchy = this.getHierarchyInternal(
            type,
            options === undefined ? undefined : options.parentId,
            result
        );
        return result;
    }

    /**
     * Converts static ids to runtime ids that are used for modifying the model.
     * @param  {string[]} guids
     * @returns {number[]}
     */
    getEntityIdsFromGuids(guids: string[]): number[] {
        const uuids = new Map();
        const tmpUuid = new UUID({});
        let index = 0;
        for (const id of guids) {
            if (id.length === 22) {
                uuids.set(id, index);
            } else if (id.length === 36) {
                // This is probably not good enough...
                tmpUuid.setMsGuid(id);
                uuids.set(tmpUuid.ifcGuid, index);
            } else {
                console.warn("Only IFC GUID and MS GUID is currently supported");
            }

            index++;
        }

        const tmpGuid = new Fbs.Guid();
        const tmpEntity = new Fbs.Entity();
        const matchingEntityIds = new Array(guids.length);
        let foundIds = 0;

        for (let i = 0, l = this.modelEntities.entitiesLength(); i < l; i++) {
            this.modelEntities.entities(i, tmpEntity);
            if (tmpEntity.type() === Fbs.IdentifierType.Guid) {
                this.modelEntities.guidIdentifiers(tmpEntity.identifierIndex(), tmpGuid);
                tmpUuid.setFromBufferData(tmpGuid.bb.bytes().buffer, tmpGuid.bb_pos);
                const id = tmpUuid.ifcGuid;
                if (uuids.has(id)) {
                    matchingEntityIds[uuids.get(id)] = i;
                    foundIds++;
                    if (foundIds === guids.length) return matchingEntityIds;
                }
            }
        }

        if (matchingEntityIds.length !== guids.length) {
            console.warn("Not all ids could be converted");
        }

        return matchingEntityIds;
    }

    private getIdentifier = (() => {
        const tmpGuid = new Fbs.Guid();
        return (identifierType: IdentifierType, e: Fbs.Entity) => {
            switch (identifierType) {
                case IdentifierType.Guid:
                    const guid = this.modelEntities.guidIdentifiers(e.identifierIndex(), tmpGuid);
                    return createUUID(guid);
                case IdentifierType.String:
                    return this.modelEntities.stringIdentifiers(e.identifierIndex());
                case IdentifierType.SpatialHash:
                    return [
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).x(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).y(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).z(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d0(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d1(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d2(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d3(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d4(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d5(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d6(),
                        this.modelEntities.spatialHashIdentifiers(e.identifierIndex()).d7()
                    ];
                case IdentifierType.DwgHandle:
                    return [
                        this.modelEntities.dwgHandleIdentifiers(e.identifierIndex()).high,
                        this.modelEntities.dwgHandleIdentifiers(e.identifierIndex()).low
                    ];
                case IdentifierType.None:
                    return;
                default:
                    console.warn("Unknown identifier", identifierType);
                    return;
            }
        };
    })();

    getEntity(entityId: number): Entity {
        if (entityId < 0 || entityId > this.modelEntities.entitiesLength())
            return;

        const entity = new Entity();

        const e = this.modelEntities.entities(entityId);
        const identifierType = e.type();

        entity.id = entityId;
        entity.identifierType = identifierType;
        entity.identifier = this.getIdentifier(entity.identifierType, e);

        entity.class = this.modelEntities.entityClasses(e.classIndex());

        return entity;
    }

    getEntitiesFromIds(entityIds: number[]): Entity[] {
        const entities: Entity[] = Array(entityIds.length);
        for (let i = 0, l = entityIds.length; i < l; i++)
            entities[i] = this.getEntity(entityIds[i]);
        return entities;
    }

    private getHierarchyInternal(
        type: HierarchyType,
        parentId: number,
        catalog: Hierarchy
    ): EntityInfo[] {
        const index = this.loadHierarchyIndex(type);

        let entities: EntityInfo[] = [];

        if (parentId === undefined) {
            // search for root nodes by finding all entities that don't have parents
            const children = new Set<number>();
            for (const values of index) {
                if (!values) continue;
                for (const value of values) {
                    children.add(value);
                }
            }

            for (let key = 0, length = index.length; key < length; key++) {
                if (!index[key]) continue;

                if (!children.has(key)) {
                    const info: EntityInfo = {
                        id: key,
                        productId: this.getProductId(key),
                        propertySetIds: [...this.getPropertySetIdsFromEntity(key)]
                    };
                    if (catalog.entities !== undefined && !catalog.entities.has(info.id)) {
                        catalog.entities.set(info.id, this.getEntity(info.id));
                    }
                    if (
                        catalog.products !== undefined &&
                        info.productId &&
                        !catalog.products.has(info.productId)
                    ) {
                        catalog.products.set(info.productId, this.getProduct(info.productId));
                    }
                    if (catalog.propertySets !== undefined) {
                        for (var propertySetId of info.propertySetIds) {
                            if (!catalog.propertySets.has(propertySetId)) {
                                catalog.propertySets.set(
                                    propertySetId,
                                    this.getPropertySet(propertySetId)
                                );
                            }
                        }
                    }

                    info.children = this.getHierarchyInternal(type, key, catalog);
                    entities.push(info);
                }
            }
        } else {
            const childrenIds = index[parentId];
            if (childrenIds !== undefined) {
                entities = Array<EntityInfo>(childrenIds.size);
                let i = 0;
                for (let childId of childrenIds) {
                    const info: EntityInfo = {
                        id: childId,
                        productId: this.getProductId(childId),
                        propertySetIds: [...this.getPropertySetIdsFromEntity(childId)]
                    };
                    if (catalog.entities !== undefined && !catalog.entities.has(info.id)) {
                        catalog.entities.set(info.id, this.getEntity(info.id));
                    }
                    if (catalog.products !== undefined && !catalog.products.has(info.productId)) {
                        catalog.products.set(info.productId, this.getProduct(info.productId));
                    }
                    if (catalog.propertySets !== undefined) {
                        for (var propertySetId of info.propertySetIds) {
                            if (!catalog.propertySets.has(propertySetId)) {
                                catalog.propertySets.set(
                                    propertySetId,
                                    this.getPropertySet(propertySetId)
                                );
                            }
                        }
                    }

                    info.children = this.getHierarchyInternal(type, childId, catalog);
                    entities[i++] = info;
                }
            }
        }

        return entities;
    }

    private loadProductIndex(): Array<number> {
        if (this.productIndex === undefined) {
            const entitiesLength = this.modelEntities.entitiesLength();
            this.productIndex = new Array<number>(entitiesLength);

            for (let i = 0, l = this.properties.productBindingsLength(); i < l; i++) {
                const bindings = this.properties.productBindings(i);
                this.productIndex[bindings.entityId()] = bindings.productId();
            }
        }

        return this.productIndex;
    }

    private loadPropertyIndex(): Set<number>[] {
        if (this.propertyIndex === undefined) {
            const entitiesLength = this.modelEntities.entitiesLength();
            const index: Map<number, Set<number>> = new Map();

            for (let i = 0, l = this.properties.propertySetBindingsLength(); i < l; i++) {
                const bindings = this.properties.propertySetBindings(i);

                for (let j = 0, l = bindings.entitiesLength(); j < l; j++) {
                    const entityId = bindings.entities(j);

                    if (index.has(entityId)) {
                        index.get(entityId).add(i);
                    } else {
                        index.set(entityId, new Set([i]));
                    }
                }
            }

            this.propertyIndex = new Array<Set<number>>(entitiesLength);
            for (let i = 0; i < entitiesLength; i++) {
                if (index.has(i)) {
                    this.propertyIndex[i] = index.get(i);
                } else {
                    this.propertyIndex[i] = new Set();
                }
            }
        }

        return this.propertyIndex;
    }

    private loadHierarchyIndex(type: HierarchyType): Array<Set<number>> {
        let index = this.hierarchyIndex.get(type);
        if (index === undefined) {
            const entitiesLength = this.modelEntities.entitiesLength();
            index = new Array<Set<number>>(entitiesLength);

            for (let i = 0, l = this.modelEntities.hierarchiesLength(); i < l; i++) {
                const link = this.modelEntities.hierarchies(i);
                const hierarchyType: number = link.type();

                if (hierarchyType === type) {
                    var node = index[link.parentElementId()];
                    if (node === undefined) {
                        node = new Set();
                        index[link.parentElementId()] = node;
                    }

                    node.add(link.childElementId());
                }
            }

            this.hierarchyIndex.set(type, index);
        }

        return index;
    }

    private getSinglePropertyDefinitions(
        def: Fbs.PropertySetDefinition,
        bindings: Fbs.PropertySet
    ): Property[] {
        const properties = new Array<Property>(def.propertiesLength());

        for (let i = 0, l = def.propertiesLength(); i < l; i++) {
            const singlePropertyDefinition = def.properties(i);

            const name = this.properties.propertyNames(singlePropertyDefinition.name());
            const value = this.getValue(singlePropertyDefinition.type(), bindings.values(i));
            const type = <number>singlePropertyDefinition.type();

            properties[i] = { name: name, value: value as number, type: type };
        }

        return properties;
    }

    private getValue(type: any, index: number): string | number | flatbuffers.Long {
        switch (type) {
            case PropertyType.LengthMeasure:
                return this.properties.lengthMeasures(index);

            case PropertyType.AreaMeasure:
                return this.properties.areaMeasures(index);

            case PropertyType.VolumeMeasure:
                return this.properties.volumeMeasures(index);

            case PropertyType.MassMeasure:
                return this.properties.massMeasures(index);

            case PropertyType.AngleMeasure:
                return this.properties.angleMeasures(index);

            case PropertyType.StringValue:
                return this.properties.stringValues(index);

            case PropertyType.IntValue:
                return index;

            case PropertyType.DoubleValue:
                return this.properties.doubleValues(index);

            case PropertyType.DateTime:
                return this.properties.dateTimeValues(index);

            case PropertyType.Logical:
                return index;

            case PropertyType.Boolean:
                return index;
        }
    }
}

const createUUID = (guid: Fbs.Guid) =>
    new UUID({data: guid.bb.bytes().slice(guid.bb_pos, guid.bb_pos + 16)});
