/// <summary>
/// The quantized axis aligned bounding box node with data id.
/// </summary>
export class QuantizedAxisAlignedBox3TreeNode {
    private view: DataView;

    constructor(array: Float64Array, offset: number) {
        this.quantizedAabb = new QuantizedAxisAlignedBox3(array, offset);
    }

    /// <summary>
    /// The quantized axis aligned bounding box.
    /// </summary>
    private quantizedAabb: QuantizedAxisAlignedBox3;

    /// <summary>
    /// If negative, containing the escape width.
    /// If positive, the data provided.
    /// </summary>
    private widthOrValue: number;

    /// <summary>
    /// Determines whether this is data node.
    /// </summary>
    /// <returns>
    ///   <c>true</c> if this node contains data; otherwise, <c>false</c> and contains a branch.
    /// </returns>
    public IsDataNode(): boolean {
        return this.widthOrValue >= 0;
    }

    /// <summary>
    /// Gets the quantized axis aligned bounding box.
    /// </summary>
    /// <returns>the quantized axis aligned bounding box</returns>
    public GetQuantizedAabb(): QuantizedAxisAlignedBox3 {
        return this.quantizedAabb;
    }

    /// <summary>
    /// Gets the width of the branch. (The count of nodes inside this branches subtree).
    /// </summary>
    /// <returns>The count of nodes inside this branch.</returns>
    public GetBranchNodeWidth(): number {
        // if (this.widthOrValue > 0)
        // {
        //     debugger;
        //     return 0;
        // }
        return -this.widthOrValue;
    }

    /// <summary>
    /// Gets the data value.
    /// </summary>
    /// <returns>Returns the id for the node.</returns>
    public GetDataNodeValue(): number {
        // if (this.widthOrValue < 0)
        // {
        //     debugger;
        //     return 0;
        // }
        return this.widthOrValue;
    }

    /// <summary>
    /// Sets the node to be a branch node.
    /// </summary>
    /// <param name="width">The width of the branch.</param>
    /// <param name="aabb">The quantized axis aligned bounding box for the brach</param>
    public SetAsBranch(width: number, aabb: QuantizedAxisAlignedBox3): void {
        this.widthOrValue = -width;
        this.quantizedAabb.copy(aabb);
    }

    public SetBranch(width: number): void {
        this.widthOrValue = -width;
    }

    /// <summary>
    /// Sets the data value.
    /// </summary>
    /// <param name="dataValue">The data value.</param>
    /// <param name="aabb">The quantized axis aligned bounding box for the brach</param>
    public SetAsDataValue(dataValue: number, aabb: QuantizedAxisAlignedBox3): void {
        this.widthOrValue = dataValue;
        this.quantizedAabb.copy(aabb);
    }

    public SetDataValue(dataValue: number): void {
        this.widthOrValue = dataValue;
    }

    /// <summary>
    /// Tests the quantized box overlap.
    /// </summary>
    /// <param name="aabb">The quantized axis aligned bounding box to compare with.</param>
    /// <returns>
    /// Returns true, is boxes overlap.
    /// </returns>
    public TestQuantizedBoxOverlap(aabb: QuantizedAxisAlignedBox3): boolean {
        if (
            this.quantizedAabb.MinX > aabb.MaxX ||
            this.quantizedAabb.MaxX < aabb.MinX ||
            this.quantizedAabb.MinY > aabb.MaxY ||
            this.quantizedAabb.MaxY < aabb.MinY ||
            this.quantizedAabb.MinZ > aabb.MaxZ ||
            this.quantizedAabb.MaxZ < aabb.MinZ
        ) {
            return false;
        }

        return true;
    }
}
