export class QuantizedAxisAlignedBox3 {
    /// <summary>
    /// Initializes a new instance of the <see cref="QuantizedAxisAlignedBox3"/> struct.
    /// </summary>
    /// <param name="minX">The min X.</param>
    /// <param name="maxX">The max X.</param>
    /// <param name="minY">The min Y.</param>
    /// <param name="maxY">The max Y.</param>
    /// <param name="minZ">The min Z.</param>
    /// <param name="maxZ">The max Z.</param>
    constructor(public aabb: Float64Array = new Float64Array(6), public offset: number = 0) {}

    public get MinX(): number {
        return this.aabb[this.offset];
    }

    public get MaxX(): number {
        return this.aabb[this.offset + 1];
    }

    public get MinY(): number {
        return this.aabb[this.offset + 2];
    }

    public get MaxY(): number {
        return this.aabb[this.offset + 3];
    }

    public get MinZ(): number {
        return this.aabb[this.offset + 4];
    }

    public get MaxZ(): number {
        return this.aabb[this.offset + 5];
    }

    copy(other: QuantizedAxisAlignedBox3): void {
        this.aabb[this.offset] = other.MinX;
        this.aabb[this.offset + 1] = other.MaxX;
        this.aabb[this.offset + 2] = other.MinY;
        this.aabb[this.offset + 3] = other.MaxY;
        this.aabb[this.offset + 4] = other.MinZ;
        this.aabb[this.offset + 5] = other.MaxZ;
    }

    /// <summary>
    /// Creates the merged QuantizedAabb with the other QuantizedAabb.
    /// </summary>
    /// <param name="other">The other QuantizedAabb.</param>
    /// <returns>The merged QuantizedAabb.</returns>
    public CreateMergedWith(first: QuantizedAxisAlignedBox3, second: QuantizedAxisAlignedBox3): void {
        (this.aabb[this.offset] = Math.min(first.MinX, second.MinX)),
            (this.aabb[this.offset + 1] = Math.max(first.MaxX, second.MaxX)),
            (this.aabb[this.offset + 2] = Math.min(first.MinY, second.MinY)),
            (this.aabb[this.offset + 3] = Math.max(first.MaxY, second.MaxY)),
            (this.aabb[this.offset + 4] = Math.min(first.MinZ, second.MinZ)),
            (this.aabb[this.offset + 5] = Math.max(first.MaxZ, second.MaxZ));
    }
}
