import { QuantizedAxisAlignedBox3 } from "./QuantizedAxisAlignedBox3";
import { QuantizedAxisAlignedBox3TreeNode } from "./QuantizedAxisAlignedBox3TreeNode";

import { Ray, Box3, Plane, Vector3, Frustum } from "../ThreeMath";
import { Utils } from "../Utils";

/// The struct representing the input item.
export class InputBox {
    constructor(public index: number, public Data: number) {}
}

/// Definition of items in the visitor walker.
interface VisitorStackItem {
    /// The parent id.
    BranchNodeIndex: number;

    /// The depth of the node (level)
    Depth: number;

    /// The branch node end index.
    BranchNodeEndIndex: number;

    /// The count of subnodes containing data.
    DataNodeCount: number;

    /// The count of subnodes containing branch.
    BranchNodeCount: number;
}

/// Quantized axis aligned bounding box tree
export class QuantizedAxisAlignedBox3Tree {
    /// global bounding box.
    public globalAabb: Box3;

    /// The quantized nodes presenting the tree.
    private quantizedAabbTreeNodes: QuantizedAxisAlignedBox3TreeNode[];

    private leftBoundingBoxes: QuantizedAxisAlignedBox3[];
    private rightBoundingBoxes: QuantizedAxisAlignedBox3[];

    /// Initializes a new instance of the <see cref="QuantizedAxisAlignedBox3Tree"/> class.
    /// <param name="inputBoxes">The primitive boxes.</param>
    constructor(private boundingBoxes: Float64Array, inputBoxes: InputBox[]) {
        this.globalAabb = this.MergeBoxes(inputBoxes, 0, inputBoxes.length);
        const nodeCount = inputBoxes.length * 2 - 1;
        this.quantizedAabbTreeNodes = [];

        const buffer = new Float64Array(nodeCount * 6);
        for (let i = 0; i < nodeCount; i++) {
            this.quantizedAabbTreeNodes.push(new QuantizedAxisAlignedBox3TreeNode(buffer, i * 6));
        }

        const subTreeBoundingBox = new QuantizedAxisAlignedBox3();

        this.leftBoundingBoxes = [];
        this.rightBoundingBoxes = [];

        for (let i = 0; i < 32; i++) {
            this.leftBoundingBoxes.push(new QuantizedAxisAlignedBox3());
            this.rightBoundingBoxes.push(new QuantizedAxisAlignedBox3());
        }

        this.BuildSubTree(0, 0, inputBoxes, 0, inputBoxes.length, subTreeBoundingBox);
    }

    /// Gets the total count of data (leaf) nodes.
    public DataNodeCount(): number {
        return (this.quantizedAabbTreeNodes.length + 1) / 2;
    }

    /// Query items inside the given box.
    /// <param name="box">The box.</param>
    /// <returns>Results the list of entities that are possible inside the given box.</returns>
    public BoxQuery(box: Box3): number[] {
        const collidedResults: number[] = [];
        const quantizedAabb = new QuantizedAxisAlignedBox3();

        this.Quantize(box, quantizedAabb);
        let currentIndex = 0;

        while (currentIndex < this.quantizedAabbTreeNodes.length) {
            const overlaps: boolean = this.TestQuantizedBoxOverlap(currentIndex, quantizedAabb);
            const isDataNode: boolean = this.IsDataNode(currentIndex);

            if (isDataNode && overlaps) {
                // is overlapped data (leaf) node
                collidedResults.push(this.GetNodeDataValue(currentIndex));
                currentIndex++;
            } else if (isDataNode || overlaps) {
                // is data (leaf) node or overlapping branch node.
                currentIndex++;
            } else {
                // branch node does not overlap -> skip
                currentIndex += this.GetBranchNodeWidth(currentIndex);
            }
        }

        return collidedResults;
    }

    public QueryRay(ray: Ray, clipPlanes: Plane[]): number[] {
        const intersect = (box: Box3) => ray.intersectsBox(box);
        const visible = clipPlanes && clipPlanes.length > 0 ?
            (box: Box3) => Utils.planesIntersectBox(clipPlanes, box) : () => true;
        return this.Query(intersect, () => false, visible);
    }

    public QueryFrustum(frustum: Frustum, clipPlanes: Plane[]): number[] {
        const planes = [...frustum.planes];
        if (clipPlanes) planes.push(...clipPlanes);
        const intersect = (box: Box3) => Utils.planesIntersectBox(planes, box);
        const contain = (box: Box3) => Utils.planesContainBox(planes, box);
        return this.Query(intersect, contain, () => true);
    }

    private Query(intersectBox: (box: Box3) => boolean, containBox: (box: Box3) => boolean, visibleBox: (box: Box3) => boolean): number[] {
        const collidedResults: number[] = [];
        let curIndex = 0;
        const numNodes = this.quantizedAabbTreeNodes.length;

        while (curIndex < numNodes) {
            const bound = this.GetAabb(curIndex);
            const isDataNode = this.IsDataNode(curIndex);
            const contains = containBox(bound);
            const intersects = contains || intersectBox(bound);

            if (isDataNode && intersects && visibleBox(bound)) {
                // is overlapped data (leaf) node
                collidedResults.push(this.GetNodeDataValue(curIndex));
            }

            if (intersects || isDataNode) {
                // is data (leaf) node or overlapping branch node.
                curIndex++;

                if (contains && !isDataNode) {
                    // contain whole branch -> add all data nodes
                    const endIndex = curIndex + this.GetBranchNodeWidth(curIndex);
                    for (; curIndex < endIndex; curIndex++) {
                        if (this.IsDataNode(curIndex) && visibleBox(bound))
                            collidedResults.push(this.GetNodeDataValue(curIndex));
                    }
                }
            } else {
                // branch node does not overlap -> skip
                curIndex += this.GetBranchNodeWidth(curIndex);
            }
        }

        return collidedResults;
    }

    /// Calculates the average node depth.
    /// <returns>The average node depth.</returns>
    public CalculateAverageNodeDepth(): number {
        let totalDepth = 0;

        const parentStack: number[] = [];

        for (
            let currentIndex = 0;
            currentIndex < this.quantizedAabbTreeNodes.length;
            currentIndex++
        ) {
            // if we're in the right of an node.
            while (
                parentStack.length !== 0 &&
                parentStack[parentStack.length - 1] === currentIndex
            ) {
                parentStack.pop();
            }

            if (this.IsDataNode(currentIndex)) {
                totalDepth += parentStack.length;
            } else {
                // is node; push the node-right index to stack.
                const nodeWidth = this.GetBranchNodeWidth(currentIndex);
                parentStack.push(currentIndex + nodeWidth);
            }
        }

        return totalDepth / this.DataNodeCount();
    }

    private getBox3FromArray(array: Float64Array, target: Box3, index: number): void {
        target.min.fromArray(array as any, index * 6);
        target.max.fromArray(array as any, index * 6 + 3);
    }

    /// QSort like in-place split by pivot using X component.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left.</param>
    /// <param name="right">The right.</param>
    /// <param name="pivotValue">The pivot value.</param>
    /// <returns>The split index, for which the split is effective.</returns>
    private InPlaceSplitX = (() => {
        const vec3 = new Vector3();
        const box = new Box3();

        return (
            inputBoxes: InputBox[],
            left: number,
            right: number,
            pivotValue: number
        ): number => {
            let pivotIndex = left;

            // only larger than pivotValue are stored before the pivotIndex..
            for (let i = left; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                if (box.getCenter(vec3).x > pivotValue) {
                    this.SwapInputBoxes(inputBoxes, i, pivotIndex);
                    pivotIndex++;
                }
            }

            return pivotIndex;
        };
    })();

    /// QSort like in-place split by pivot using Y component.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left.</param>
    /// <param name="right">The right.</param>
    /// <param name="pivotValue">The pivot value.</param>
    /// <returns>The split index, for which the split is effective.</returns>
    private InPlaceSplitY = (() => {
        const vec3 = new Vector3();
        const box = new Box3();

        return (
            inputBoxes: InputBox[],
            left: number,
            right: number,
            pivotValue: number
        ): number => {
            let pivotIndex = left;

            // only larger than pivotValue are stored before the pivotIndex..
            for (let i = left; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                if (box.getCenter(vec3).y > pivotValue) {
                    this.SwapInputBoxes(inputBoxes, i, pivotIndex);
                    pivotIndex++;
                }
            }

            return pivotIndex;
        };
    })();

    /// QSort like in-place split by pivot using Z component.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left.</param>
    /// <param name="right">The right.</param>
    /// <param name="pivotValue">The pivot value.</param>
    /// <returns>The split index, for which the split is effective.</returns>
    private InPlaceSplitZ = (() => {
        const vec3 = new Vector3();
        const box = new Box3();

        return (
            inputBoxes: InputBox[],
            left: number,
            right: number,
            pivotValue: number
        ): number => {
            let pivotIndex = left;

            // only larger than pivotValue are stored before the pivotIndex..
            for (let i = left; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                if (box.getCenter(vec3).z > pivotValue) {
                    this.SwapInputBoxes(inputBoxes, i, pivotIndex);
                    pivotIndex++;
                }
            }

            return pivotIndex;
        };
    })();

    /// Calculates the mean the of center points in the given range.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left.</param>
    /// <param name="right">The right.</param>
    /// <returns>Returns the mean of the center boxes in the given range.</returns>
    private CalculateMeanOfCenterPoints = (() => {
        const vec3 = new Vector3();
        const box = new Box3();

        return (inputBoxes: InputBox[], left: number, right: number, target: Vector3) => {
            this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[left].index);
            box.getCenter(target);
            for (let i = left + 1; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                target.add(box.getCenter(vec3));
            }

            const numIndices = right - left;
            return target.divideScalar(numIndices);
        };
    })();

    /// Merges the boxes in the given range.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left.</param>
    /// <param name="right">The right.</param>
    /// <returns>Returns the Aabb that is merged with the boxes in the given range.</returns>
    private MergeBoxes = (() => {
        const box = new Box3();

        return (inputBoxes: InputBox[], left: number, right: number) => {
            this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[left].index);
            const mergedAabb = box.clone();
            for (let i = left + 1; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                mergedAabb.union(box);
            }

            return mergedAabb;
        };
    })();

    /// Swaps the boxes.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="itemA">The itemA.</param>
    /// <param name="itemB">Index of the store.</param>
    private SwapInputBoxes(inputBoxes: InputBox[], itemA: number, itemB: number): void {
        const tmp = inputBoxes[itemA];
        inputBoxes[itemA] = inputBoxes[itemB];
        inputBoxes[itemB] = tmp;
    }

    /// Tests if the quantized box overlap.
    /// <param name="nodeIndex">Index of the node.</param>
    /// <param name="quantizedAabb">The quantized axis aligned bounding box to compare to.</param>
    /// <returns>
    /// Returns true, if the quantized boxes do overlap.
    /// </returns>
    private TestQuantizedBoxOverlap(
        nodeIndex: number,
        quantizedAabb: QuantizedAxisAlignedBox3
    ): boolean {
        return this.quantizedAabbTreeNodes[nodeIndex].TestQuantizedBoxOverlap(quantizedAabb);
    }

    /// Determines whether the specified nodeIndex is data (leaf) node.
    /// <param name="nodeIndex">The nodeIndex.</param>
    /// <returns>
    ///   <c>true</c> if the specified nodeIndex is data node; otherwise, <c>false</c>.
    /// </returns>
    private IsDataNode(nodeIndex: number): boolean {
        return this.quantizedAabbTreeNodes[nodeIndex].IsDataNode();
    }

    /// Gets the axis aligned bounding box.
    /// <param name="nodeIndex">Index of the node.</param>
    /// <returns>the axis aligned bounding box</returns>
    public GetAabb(nodeIndex: number): Box3 {
        const quantizedAabb = this.quantizedAabbTreeNodes[nodeIndex].GetQuantizedAabb();
        return this.Unquantize(quantizedAabb);
    }

    /// Creates the data (leaf) node.
    /// <param name="nodeIndex">Index of the node.</param>
    /// <param name="quantizedAabb">The quantized axis aligned bounding box.</param>
    /// <param name="dataValue">The dataValue.</param>
    private CreateDataNode(
        nodeIndex: number,
        quantizedAabb: QuantizedAxisAlignedBox3,
        dataValue: number
    ): void {
        this.quantizedAabbTreeNodes[nodeIndex].SetAsDataValue(dataValue, quantizedAabb);
    }

    private setAsDataNode(nodeIndex: number, dataValue: number): void {
        this.quantizedAabbTreeNodes[nodeIndex].SetDataValue(dataValue);
    }

    /// Creates the branch node.
    /// <param name="nodeIndex">Index of the node.</param>
    /// <param name="quantizedAabb">The quantized axis aligned bounding box.</param>
    /// <param name="width">The width (count of subnodes) of the branch.</param>
    private CreateBranchNode(
        nodeIndex: number,
        quantizedAabb: QuantizedAxisAlignedBox3,
        width: number
    ): void {
        this.quantizedAabbTreeNodes[nodeIndex].SetAsBranch(width, quantizedAabb);
    }

    /// Gets the value from a data node.
    /// <param name="nodeIndex">Index of the node.</param>
    /// <returns>The data value for the node.</returns>
    private GetNodeDataValue(nodeIndex: number): number {
        return this.quantizedAabbTreeNodes[nodeIndex].GetDataNodeValue();
    }

    /// Gets the width of the branch node (count of subnodes).
    /// <param name="nodeIndex">Index of the node.</param>
    /// <returns>The width for the node (nodeCount of nodes in the subtree)</returns>
    private GetBranchNodeWidth(nodeIndex: number): number {
        return this.quantizedAabbTreeNodes[nodeIndex].GetBranchNodeWidth();
    }

    /// Quantizes the specified axis aligned bounding box.
    /// <param name="aabb">The axis aligned box.</param>
    /// <returns>
    /// The quantized axis aligned bounding box.
    /// </returns>
    private Quantize = (() => {
        const minClampedPoint = new Vector3();
        const maxClampedPoint = new Vector3();

        return (aabb: Box3, target: QuantizedAxisAlignedBox3): void => {
            minClampedPoint.subVectors(aabb.min, this.globalAabb.min);
            maxClampedPoint.subVectors(aabb.max, this.globalAabb.min);

            target.aabb[target.offset] = minClampedPoint.x;
            target.aabb[target.offset + 1] = maxClampedPoint.x;
            target.aabb[target.offset + 2] = minClampedPoint.y;
            target.aabb[target.offset + 3] = maxClampedPoint.y;
            target.aabb[target.offset + 4] = minClampedPoint.z;
            target.aabb[target.offset + 5] = maxClampedPoint.z;
        };
    })();

    /// Inverse quantizes the specified value.
    /// <param name="value">The quantized value.</param>
    /// <returns>Returns the unquantized value.</returns>
    private Unquantize(value: QuantizedAxisAlignedBox3): Box3 {
        const min = new Vector3(
            value.MinX + this.globalAabb.min.x,
            value.MinY + this.globalAabb.min.y,
            value.MinZ + this.globalAabb.min.z
        );
        const max = new Vector3(
            value.MaxX + this.globalAabb.min.x,
            value.MaxY + this.globalAabb.min.y,
            value.MaxZ + this.globalAabb.min.z
        );

        // const center = new Vector3().addVectors(max, min).divideScalar(2.0);
        // const extent = new Vector3().subVectors(, min).divideScalar(2.0);
        return new Box3(min, max);
    }

    /// Builds the sub tree.
    /// <param name="outputIndex">The next (free) index.</param>
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left index.</param>
    /// <param name="right">The right index.</param>
    /// <param name="subTreeBoundingBox">The calculated sub tree bounding box.</param>
    /// <returns>
    /// Returns the count of nodes in the subtree (both data (leaf) and branch nodes).
    /// </returns>
    private BuildSubTree = (() => {
        const box = new Box3();

        return (
            treeDepth: number,
            outputIndex: number,
            inputBoxes: InputBox[],
            left: number,
            right: number,
            subTreeBoundingBox: QuantizedAxisAlignedBox3
        ): number => {
            if (right - left === 1) {
                // if only one then it's data (leaf) node.
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[left].index);
                this.Quantize(box, subTreeBoundingBox);
                this.CreateDataNode(outputIndex, subTreeBoundingBox, inputBoxes[left].Data);
                return 1;
            }

            if (right - left === 2) {
                // create one branch node and two data (leaf) nodes.
                const quantizedAabb1 = this.quantizedAabbTreeNodes[
                    outputIndex + 1
                ].GetQuantizedAabb();
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[left].index);
                this.Quantize(box, quantizedAabb1);
                this.setAsDataNode(outputIndex + 1, inputBoxes[left].Data);

                const quantizedAabb2 = this.quantizedAabbTreeNodes[
                    outputIndex + 2
                ].GetQuantizedAabb();
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[left + 1].index);
                this.Quantize(box, quantizedAabb2);
                this.setAsDataNode(outputIndex + 2, inputBoxes[left + 1].Data);

                subTreeBoundingBox.CreateMergedWith(quantizedAabb1, quantizedAabb2);
                this.CreateBranchNode(outputIndex, subTreeBoundingBox, 3);
                return 3;
            }

            const splitIndex = this.SplitByOneAxis(inputBoxes, left, right);
            const leftWidth = this.BuildSubTree(
                treeDepth + 1,
                outputIndex + 1,
                inputBoxes,
                left,
                splitIndex,
                this.leftBoundingBoxes[treeDepth]
            );
            const rightWidth = this.BuildSubTree(
                treeDepth + 1,
                outputIndex + leftWidth + 1,
                inputBoxes,
                splitIndex,
                right,
                this.rightBoundingBoxes[treeDepth]
            );

            subTreeBoundingBox.CreateMergedWith(
                this.leftBoundingBoxes[treeDepth],
                this.rightBoundingBoxes[treeDepth]
            );
            const branchWidth = leftWidth + rightWidth + 1;
            this.CreateBranchNode(outputIndex, subTreeBoundingBox, branchWidth);
            return branchWidth;
        };
    })();

    /// Splits the input primitives by one axis.
    /// Variance is used to detect which axis to split by,
    /// The mean (average) center point by that axis is used as pivot to split.
    /// Always: right > left.
    /// <param name="inputBoxes">The primitive boxes.</param>
    /// <param name="left">The left index.</param>
    /// <param name="right">The right index.</param>
    /// <returns>Returns the index where the split was done.</returns>
    SplitByOneAxis = (() => {
        const varianceSum = new Vector3();
        const delta = new Vector3();
        const meanOfCenterPoints = new Vector3();
        const box = new Box3();

        return (inputBoxes: InputBox[], left: number, right: number): number => {
            this.CalculateMeanOfCenterPoints(inputBoxes, left, right, meanOfCenterPoints);
            varianceSum.set(0, 0, 0);

            for (let i = left; i < right; i++) {
                this.getBox3FromArray(this.boundingBoxes, box, inputBoxes[i].index);
                box.getCenter(delta).sub(meanOfCenterPoints);
                const deltaSquared = delta.multiply(delta);
                varianceSum.add(deltaSquared);
            }

            // split by axis with largest variance.
            let splitIndex;
            if (varianceSum.x > varianceSum.y) {
                if (varianceSum.x > varianceSum.z) {
                    // Y is not largest.
                    splitIndex = this.InPlaceSplitX(inputBoxes, left, right, meanOfCenterPoints.x);
                } else {
                    splitIndex = this.InPlaceSplitZ(inputBoxes, left, right, meanOfCenterPoints.z);
                }
            } else if (varianceSum.y > varianceSum.z) {
                splitIndex = this.InPlaceSplitY(inputBoxes, left, right, meanOfCenterPoints.y);
            } else {
                splitIndex = this.InPlaceSplitZ(inputBoxes, left, right, meanOfCenterPoints.z);
            }

            const numIndices = right - left;
            const rangeBalancedIndices = numIndices / 3;

            if (splitIndex <= left + rangeBalancedIndices) {
                // if split index is too close to left, use middle position.
                splitIndex = (left + right) / 2;
            } else if (splitIndex >= right - 1 - rangeBalancedIndices) {
                // if split index is too close to right, use middle position.
                splitIndex = (left + right) / 2;
            }

            return Math.round(splitIndex);
        };
    })();
}
