import { InputBox, QuantizedAxisAlignedBox3Tree } from "./QuantizedAxisAlignedBox3Tree";
import { InstanceManager } from "../InstanceManager";
import { GeometryManager } from "../GeometryManager";
import { Frustum, Matrix4, Plane, Ray, Box3 } from "../ThreeMath";
import { Trimble } from "../../TrimBim_generated";
import { ModelEntityState } from "../ModelEntityState";
import { TrimbimPickPriority } from "../../common";
import { Utils } from "../Utils";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { Hit } from "../HitPoint";
import {LayerManager} from "../LayerManager";
import {SnapType} from "@technology/webgl-viewer/dist/esnext/common";

export class PickHandler {
    _geometry: Fbs.ModelGeometry;
    searchTree: QuantizedAxisAlignedBox3Tree;

    private instance = new Fbs.GeometryInstance();
    private definition = new Fbs.GeometryDefinition();

    constructor(
        model: Fbs.Model,
        private _boundingBoxes: Float64Array,
        private _instanceManager: InstanceManager,
        private _geometryManager: GeometryManager,
        private _layerManager: LayerManager,
        private _state: ModelEntityState
    ) {
        this._geometry = model.geometry();

        const inputBoxes = [];

        for (let i = 0; i < this._boundingBoxes.length / 6; i++) {
            inputBoxes.push(new InputBox(i, i));
        }

        this.searchTree = new QuantizedAxisAlignedBox3Tree(this._boundingBoxes, inputBoxes);

        inputBoxes.length = 0;
    }

    private filterPickableInstances(instanceIds: number[]): number[] {
        return instanceIds.filter(instanceId => {
            const entityId = this._geometryManager.getEntityId(instanceId);
            return this._state.isVisible(entityId) &&
                this._state.getPickPriority(entityId) !== TrimbimPickPriority.NO_PICK &&
                this._layerManager.isVisibleByLayer(instanceId);
        });
    }

    frustumPick(frustum: Frustum, clipPlanes: Plane[]): number[] {
        const instanceIds = this.filterPickableInstances(this.searchTree.QueryFrustum(frustum, clipPlanes));
        return instanceIds.map(id => this._geometryManager.getEntityId(id));
    }

    pick(ray: Ray, frustum: Frustum, clipPlanes: Plane[]): Hit {
        const frustumPickIds = this.pickBoundingBoxes(frustum, clipPlanes);
        if (frustumPickIds.length === 0)
            return;

        const rayPickIds = this.pickBoundingBoxes(ray, clipPlanes);

        const hits: Hit[] = [];
        for (const instanceId of frustumPickIds) {
            this._geometry.instances(instanceId, this.instance);
            this._geometry.definitions(this.instance.definitionId(), this.definition);
            const isMesh = this._geometryManager.getIsMesh(this.definition.type());

            // do not perform geometry picking if mesh bounds are not ray picked (early optimization)
            if (isMesh && !rayPickIds.includes(instanceId))
                continue;

            hits.push(...this.pickGeometry(instanceId, this.definition, this.instance, ray, frustum, undefined, clipPlanes));
        }

        if (hits.length === 0)
            return;

        const highPriorityHits = hits.filter(h => this._state.getPickPriority(h.entityId) === TrimbimPickPriority.HIGH);
        return highPriorityHits.length > 0 ? this.findClosestHit(highPriorityHits) : this.findClosestHit(hits);
    }

    pickSnapped(ray: Ray, frustum: Frustum, snapTypes: SnapType[], clipPlanes: Plane[]): Hit[] {
        const hits: Hit[] = [];
        const mainHit = this.pick(ray, frustum, clipPlanes);
        if (!mainHit)
            return this.pickSnappedEdges(ray, frustum, snapTypes, clipPlanes);

        this._geometry.instances(mainHit.instanceId, this.instance);
        this._geometry.definitions(this.instance.definitionId(), this.definition);
        for (const snapType of snapTypes) {
            hits.push(...this.pickGeometry(mainHit.instanceId, this.definition, this.instance, ray, frustum, snapType, clipPlanes));
        }
        return hits;
    }

    private pickSnappedEdges(ray: Ray, frustum: Frustum, snapTypes: SnapType[], clipPlanes: Plane[]): Hit[] {
        const hits: Hit[] = [];
        if (snapTypes.includes(SnapType.LINE) || snapTypes.includes(SnapType.POINT)) {
            const frustumPickIds = this.pickBoundingBoxes(frustum, clipPlanes);
            for (const instanceId of frustumPickIds) {
                this._geometry.instances(instanceId, this.instance);
                this._geometry.definitions(this.instance.definitionId(), this.definition);
                for (const snapType of snapTypes) {
                    if (snapType !== SnapType.FACE)
                        hits.push(...this.pickGeometry(instanceId, this.definition, this.instance, ray, frustum, snapType, clipPlanes));
                }
            }
        }
        return hits;
    }

    private pickBoundingBoxes(shape: Ray | Frustum, clipPlanes: Plane[]): number[] {
        const instanceIds = shape instanceof Ray ?
            this.searchTree.QueryRay(shape, clipPlanes) :
            this.searchTree.QueryFrustum(shape, clipPlanes);
        return this.filterPickableInstances(instanceIds);
    }

    private findClosestHit(hits: Hit[]): Hit {
        return hits.reduce((h1, h2) => h1.distance < h2.distance ? h1 : h2);
    }

    private pickGeometry = (() => {
        const localRay = new Ray();
        const localFrustum = new Frustum();
        const transform = new Matrix4();
        const inverseTransform = new Matrix4();

        return (instanceId: number, definition: Fbs.GeometryDefinition, instance: Fbs.GeometryInstance, ray: Ray, frustum: Frustum, snapType: SnapType, clipPlanes: Plane[]): Hit[] => {
            this._instanceManager.getTransform(instance, transform);
            inverseTransform.getInverse(transform);

            localRay.copy(ray).applyMatrix4(inverseTransform);
            Utils.transformFrustum(frustum, inverseTransform, localFrustum);

            let hitsPoints = this._geometryManager.pick(definition, snapType, localRay, localFrustum);

            if (hitsPoints && clipPlanes && clipPlanes.length > 0) {
                const transformedPlanes = this.transformClipPlanes(clipPlanes, inverseTransform);
                hitsPoints = hitsPoints.filter(hit => Utils.insideNonIntersectingPlanes(hit.point, transformedPlanes));
            }

            const hits: Hit[] = [];
            if (hitsPoints) {
                hitsPoints.forEach(h => {
                    const hit = h as Hit;
                    hit.point.applyMatrix4(transform);
                    if (hit.normal) hit.normal.transformDirection(transform);
                    hit.instanceId = instanceId;
                    hit.entityId = this._geometryManager.getEntityId(hit.instanceId);
                    hit.pickPriority = this._state.getPickPriority(hit.entityId);
                    hit.distance = ray.origin.distanceTo(hit.point);
                    hit.snapType = snapType;
                    if (hit.snapLineStart) hit.snapLineStart.applyMatrix4(transform);
                    if (hit.snapLineEnd) hit.snapLineEnd.applyMatrix4(transform);
                    hits.push(hit);
                });
            }
            return hits;
        };
    })();

    private transformClipPlanes(clipPlanes: Plane[], m: Matrix4): Plane[] {
        if (clipPlanes) {
            return clipPlanes.map(plane => {
                const newPlane = new Plane().copy(plane);
                return newPlane.applyMatrix4(m);
            });
        } else {
            return [];
        }
    }
}

export const getBoundingBox = (id: number, box: Box3, boundingBoxes: Float64Array) => {
    box.min.set(boundingBoxes[id * 6], boundingBoxes[id * 6 + 1], boundingBoxes[id * 6 + 2]);
    box.max.set(boundingBoxes[id * 6 + 3], boundingBoxes[id * 6 + 4], boundingBoxes[id * 6 + 5]);
};
