import { Trimble } from "../TrimBim_generated";
import { Hit, HitPoint } from "./HitPoint";
import { Vector3, Matrix4, Ray } from "./ThreeMath";

function getArcSectionsCount(arc: Trimble.Technology.TrimBim.Fbs.Arc3f, sectionsPerRadianForUnitRadius: number, min: number): number {
    // amount of arc sections depends on arc radius, so small arcs are approximated, larger one are more precise
    // sectionsPerRadianForUnitRadius is used by line graphic geometry, as it can afford to have more vertices
    const count = Math.round(arc.angle() * arc.radius() * sectionsPerRadianForUnitRadius);
    return Math.min(Math.max(count, min), 30);
}

export function get3DArcSectionsCount(arc: Trimble.Technology.TrimBim.Fbs.Arc3f): number {
   return getArcSectionsCount(arc, 5, 4);
}

export function get2DArcSectionsCount(arc: Trimble.Technology.TrimBim.Fbs.Arc3f): number {
    // higher min value required for better representation of 2D autocad dwg markups
    return getArcSectionsCount(arc, 10, 8);
}

export const forEachSegment = (() => {
    const start: Vector3 = new Vector3();
    const end: Vector3 = new Vector3();
    const tmp = new Trimble.Technology.TrimBim.Fbs.Vec3f();
    const segment = new Trimble.Technology.TrimBim.Fbs.Line3f();

    return (
        lines: Trimble.Technology.TrimBim.Fbs.GraphicsContainer | Trimble.Technology.TrimBim.Fbs.Directrix,
        f: (start: Vector3, end: Vector3) => void
    ): void => {
        const lineSegmentsLength = lines.lineSegmentsLength();
        for (let i = 0; i < lineSegmentsLength; i++) {
            lines.lineSegments(i, segment);
            segment.start(tmp);
            start.set(tmp.x(), tmp.y(), tmp.z());
            segment.end(tmp);
            end.set(tmp.x(), tmp.y(), tmp.z());
            f(start, end);
        }
    };
})();

export const forEachArc = (() => {
    const arcStart = new Vector3();
    const arcEnd = new Vector3();
    const arcMids: Vector3[] = [];
    const arcCenter = new Vector3();
    const arcAxis = new Vector3();
    const arc = new Trimble.Technology.TrimBim.Fbs.Arc3f();

    return (
        directrix: Trimble.Technology.TrimBim.Fbs.Directrix | Trimble.Technology.TrimBim.Fbs.GraphicsContainer,
        f: (arcStart: Vector3, arcMids: Vector3[], arcEnd: Vector3) => void,
        getArcSectionsCount: (arc: Trimble.Technology.TrimBim.Fbs.Arc3f) => number
    ): void => {
        const arcsLength = directrix.arcsLength();
        for (let i = 0; i < arcsLength; i++) {
            directrix.arcs(i, arc);
            const arcCrossSectionCount = getArcSectionsCount(arc);

            arcCenter.set(arc.center().x(), arc.center().y(), arc.center().z());
            arcAxis.set(arc.axis().x(), arc.axis().y(), arc.axis().z());
            arcStart.set(arc.refDirection().x(), arc.refDirection().y(), arc.refDirection().z());

            arcMids.length = arcCrossSectionCount - 2;
            for (let j = 0; j < arcMids.length; j++) {
                arcMids[j] = new Vector3();
                arcMids[j].copy(arcStart);
                arcMids[j].applyAxisAngle(arcAxis, arc.angle() * (j + 1) / (arcCrossSectionCount - 1));
                arcMids[j].multiplyScalar(arc.radius());
                arcMids[j].add(arcCenter);
            }

            arcEnd.copy(arcStart);
            arcEnd.applyAxisAngle(arcAxis, arc.angle());
            arcEnd.multiplyScalar(arc.radius());
            arcEnd.add(arcCenter);

            arcStart.multiplyScalar(arc.radius());
            arcStart.add(arcCenter);

            f(arcStart, arcMids, arcEnd);
        }
    };
})();

export const forEachPolyline = (() => {
    const polyPoint = new Vector3();
    const nextPolyPoint = new Vector3();
    const polyline = new Trimble.Technology.TrimBim.Fbs.Polyline3f();

    return (
        directrix: Trimble.Technology.TrimBim.Fbs.Directrix,
        f: (polyPoint: Vector3, nextPolyPoint: Vector3) => void
    ): void => {
        const polylinesLength = directrix.polyLinesLength();
        for (let i = 0; i < polylinesLength; i++) {
            directrix.polyLines(i, polyline);
            for (let j = 0, innerLength = polyline.pointsLength() - 1; j < innerLength; j++) {
                polyPoint.set(polyline.points(j).x(), polyline.points(j).y(), polyline.points(j).z());
                nextPolyPoint.set(polyline.points(j + 1).x(), polyline.points(j + 1).y(), polyline.points(j + 1).z());
                f(polyPoint, nextPolyPoint);
            }
        }
    };
})();

export const pickOnSegment = (() => {
    const intersections = <Hit[]>[];

    return (
        lines: Trimble.Technology.TrimBim.Fbs.GraphicsContainer | Trimble.Technology.TrimBim.Fbs.Directrix,
        ray: Ray, precision = 1
    ): Hit[] => {
        intersections.length = 0;

        forEachSegment(lines, (start: Vector3, end: Vector3) => {
            intersectLine(intersections, start, end, ray, precision);
        });

        return intersections;
    };
})();

export const pickOnArc = (() => {
    const intersections = <Hit[]>[];
    return (
        directrix: Trimble.Technology.TrimBim.Fbs.Directrix | Trimble.Technology.TrimBim.Fbs.GraphicsContainer,
        ray: Ray,
        precision = 1,
        getArcSectionsCount: (arc: Trimble.Technology.TrimBim.Fbs.Arc3f) => number
    ): Hit[] => {
        intersections.length = 0;
        forEachArc(directrix, (arcStart: Vector3, arcMids: Vector3[], arcEnd: Vector3) => {
            intersectLine(intersections, arcStart, arcMids[0], ray, precision);
            arcMids.forEach((mid, index) => {
               if(index > 0) intersectLine(intersections,  arcMids[index - 1], mid, ray, precision);
            });
            intersectLine(intersections, arcMids[arcMids.length - 1], arcEnd, ray, precision);
        }, getArcSectionsCount);
        return intersections;
    };
})();

export const pickOnPolyline = (() => {
    const intersections = <Hit[]>[];

    return (
        directrix: Trimble.Technology.TrimBim.Fbs.Directrix,
        ray: Ray,
        precision = 1
    ): Hit[] => {
        intersections.length = 0;
        forEachPolyline(directrix, (polyPoint: Vector3, nextPolyPoint: Vector3) => {
            intersectLine(intersections, polyPoint, nextPolyPoint, ray, precision);
        });
        return intersections;
    };
})();

export const pickOnLineString = (() => {
    const intersections = <HitPoint[]>[];

    const start = new Vector3();
    const end = new Vector3();

    return (lineStringVertices: Float32Array | Float64Array, ray: Ray): HitPoint[] => {
        if (lineStringVertices) {
            intersections.length = 0;

            const vertexCount = lineStringVertices.length / 3;

            for (let i = 1; i < vertexCount; i++) {
                end.set(
                    lineStringVertices[i * 3],
                    lineStringVertices[i * 3 + 1],
                    lineStringVertices[i * 3 + 2]
                );
                start.set(
                    lineStringVertices[(i - 1) * 3],
                    lineStringVertices[(i - 1) * 3 + 1],
                    lineStringVertices[(i - 1) * 3 + 2]
                );
                intersectLine(intersections, start, end, ray, Infinity);
            }

            return intersections;
        }
    };
})();

export const intersectLine = (() => {
    const interRay: Vector3 = new Vector3();
    const interSegment: Vector3 = new Vector3();

    return (intersections: HitPoint[], start: Vector3, end: Vector3, ray: Ray, precision: number): number => {
        const distSq = ray.distanceSqToSegment(start, end, interRay, interSegment);
        addHit(intersections, start, end, ray, precision, distSq, interSegment);
        return distSq;
    };
})();

const addHit = (intersections: HitPoint[], start: Vector3, end: Vector3, ray: Ray, precision: number, distSq: number, point: Vector3): HitPoint => {
    if (distSq > precision)
        return;
    intersections.push({
        point: point.clone(),
        snapLineStart: start.clone(),
        snapLineEnd: end.clone()
    });
};

/**
 * Intersects the given ray with a cylinder with its axis from start to end, with the given radius.
 */
export const intersectCylinder = (() => {
    const zAxis = new Vector3();
    const yAxis = new Vector3();
    const xAxis = new Vector3();
    const m = new Matrix4();
    const mInv = new Matrix4();
    const rayTr = new Ray();
    const point = new Vector3();
    const normal = new Vector3();
    const scaleFactor = new Vector3();
    const points: HitPoint[] = [];

    /**
     * Finds an (arbitrary) unit vector perpendicular to source, and sets it to target.
     */
    const getPerpendicular = (() => {
        const zz = new Vector3(0, 0, 1);
        const xx = new Vector3(1, 0, 0);
        return (source: Vector3, target: Vector3) => {
            if (Math.abs(source.dot(zz)) > 0.9)
                target.crossVectors(source, xx).normalize();
            else target.crossVectors(source, zz).normalize();
        };
    })();

    const makeHit = (t: number, length: number, radius: number) => {
        point.copy(rayTr.direction).normalize().multiplyScalar(t).add(rayTr.origin);
        if (point.z >= 0 && point.z <= length / radius) {
            normal.set(point.x, point.y, 0).applyMatrix4(mInv).normalize();
            point.applyMatrix4(m);
            points.push({
                point: point.clone(),
                normal: normal.clone(),
            } as HitPoint);
        }
    };

    return (start: Vector3, end: Vector3, ray: Ray, radius: number): HitPoint[] => {
        const length = end.distanceTo(start);
        zAxis.copy(end).sub(start).normalize();
        getPerpendicular(zAxis, xAxis);
        yAxis.crossVectors(zAxis, xAxis);
        m.identity().makeBasis(xAxis, yAxis, zAxis);
        m.setPosition(start);
        m.scale(scaleFactor.set(radius, radius, radius));
        mInv.getInverse(m);
        rayTr.copy(ray).applyMatrix4(mInv);

        const a = rayTr.direction.x * rayTr.direction.x + rayTr.direction.y * rayTr.direction.y;
        const b = 2 * rayTr.origin.x * rayTr.direction.x + 2 * rayTr.origin.y * rayTr.direction.y;
        const c = rayTr.origin.x * rayTr.origin.x + rayTr.origin.y * rayTr.origin.y - 1;
        if (4 * a * c > b * b) return [];                // No intersection

        const t1 = (-b + Math.sqrt(b * b - 4 * a * c)) / (2 * a);
        const t2 = (-b - Math.sqrt(b * b - 4 * a * c)) / (2 * a);

        points.length = 0;
        mInv.transpose();
        makeHit(t1, length, radius);
        makeHit(t2, length, radius);
        return points;
    };
})();
