import { Trimble } from "../../TrimBim_generated";
import { GeometryData } from "./GeometryBuilder";
import { Vector3 } from "../ThreeMath";

import Fbs = Trimble.Technology.TrimBim.Fbs;
import { get2DArcSectionsCount } from "../Pickers";

export class LinePrimitiveBuilder {
    public static buildSegments(
        lines: Fbs.GraphicsContainer | Fbs.Directrix,
        buffer: GeometryData
    ): GeometryData {
        const lineSegmentsLength = lines.lineSegmentsLength();

        for (let i = 0; i < lineSegmentsLength; i++) {
            const segment = lines.lineSegments(i);

            LinePrimitiveBuilder.buildLine(segment, buffer);
        }

        return buffer;
    }

    public static buildLine(segment: Fbs.Line3d | Fbs.Line3f, buffer: GeometryData): void {
        const start = segment.start();
        const end = segment.end();

        buffer.positions[buffer.positionPointer] = start.x();
        buffer.positions[buffer.positionPointer + 1] = start.y();
        buffer.positions[buffer.positionPointer + 2] = start.z();

        buffer.positions[buffer.positionPointer + 3] = end.x();
        buffer.positions[buffer.positionPointer + 4] = end.y();
        buffer.positions[buffer.positionPointer + 5] = end.z();

        buffer.positionPointer += 6;
    }

    public static getTotalArcSectionsCount(lines: Fbs.GraphicsContainer | Fbs.Directrix): number {
        let crossSectionCount = 0;
        const arc = new Trimble.Technology.TrimBim.Fbs.Arc3f();
        for (let i = 0; i < lines.arcsLength(); i++) {
            lines.arcs(i, arc);
            crossSectionCount += get2DArcSectionsCount(arc);
        }
        return crossSectionCount;
    }

    public static buildArcs(lines: Fbs.GraphicsContainer | Fbs.Directrix, buffer: GeometryData): GeometryData {
        const arc = new Trimble.Technology.TrimBim.Fbs.Arc3f();
        let arcCrossSections: Vector3[] = [];

        for (let i = 0, l = lines.arcsLength(); i < l; i++) {
            lines.arcs(i, arc);
            const crossSectionCount = get2DArcSectionsCount(arc);
            arcCrossSections = createCrossSections(arc, crossSectionCount);

            buffer.positions[buffer.positionPointer] = arcCrossSections[0].x;
            buffer.positions[buffer.positionPointer + 1] = arcCrossSections[0].y;
            buffer.positions[buffer.positionPointer + 2] = arcCrossSections[0].z;
            buffer.positionPointer += 3;

            for (let j = 1; j < crossSectionCount - 1; j++) {
                buffer.positions[buffer.positionPointer] = arcCrossSections[j].x;
                buffer.positions[buffer.positionPointer + 1] = arcCrossSections[j].y;
                buffer.positions[buffer.positionPointer + 2] = arcCrossSections[j].z;
                buffer.positions[buffer.positionPointer + 3] = arcCrossSections[j].x;
                buffer.positions[buffer.positionPointer + 4] = arcCrossSections[j].y;
                buffer.positions[buffer.positionPointer + 5] = arcCrossSections[j].z;
                buffer.positionPointer += 6;
            }

            buffer.positions[buffer.positionPointer + 0] = arcCrossSections[crossSectionCount - 1].x;
            buffer.positions[buffer.positionPointer + 1] = arcCrossSections[crossSectionCount - 1].y;
            buffer.positions[buffer.positionPointer + 2] = arcCrossSections[crossSectionCount - 1].z;
            buffer.positionPointer += 3;
        }
        return buffer;
    }
}

export function createCrossSections(arc: Fbs.Arc3f, arcCrossSectionCount: number): Vector3[] {
    const arcCenter = new Vector3();
    const arcAxis = new Vector3();
    const arcStart = new Vector3();
    const arcEnd = new Vector3();
    const arcMids: Vector3[] = [];
    const arcCrossSections: Vector3[] = [];

    arcCenter.set(arc.center().x(), arc.center().y(), arc.center().z());
    arcAxis.set(arc.axis().x(), arc.axis().y(), arc.axis().z());
    arcStart.set(arc.refDirection().x(), arc.refDirection().y(), arc.refDirection().z());

    for (let j = 0; j < arcCrossSectionCount - 2; j++) {
        arcMids[j] = new Vector3();
        arcMids[j].copy(arcStart);
        arcMids[j].applyAxisAngle(arcAxis, arc.angle() * (j + 1) / (arcCrossSectionCount - 1));
        arcMids[j].multiplyScalar(arc.radius());
        arcMids[j].add(arcCenter);

    }

    arcEnd.copy(arcStart);
    arcEnd.applyAxisAngle(arcAxis, arc.angle());
    arcEnd.multiplyScalar(arc.radius());
    arcEnd.add(arcCenter);

    arcStart.multiplyScalar(arc.radius());
    arcStart.add(arcCenter);

    arcCrossSections.push(arcStart);
    arcCrossSections.push(...arcMids);
    arcCrossSections.push(arcEnd);

    return arcCrossSections;
}

