import { StateType } from "./ThreadWorkerMessages";
import { TrimbimPickPriority } from "../common";

export class ModelEntityState {
    protected readonly pickPriorityOffset = 2;
    protected readonly pickPriorityMask = 3 << this.pickPriorityOffset;

    protected state: Uint8Array;

    constructor(geometryCount: number) {
        this.state = new Uint8Array(geometryCount);
        this.state.fill(StateType.VISIBILITY);
    }

    isVisible(entityId: number): boolean {
        return this.is(entityId, StateType.VISIBILITY);
    }

    isSelected(entityId: number): boolean {
        return this.is(entityId, StateType.SELECTION);
    }

    private is(entityId: number, stateType: StateType): boolean {
        return !!(this.state[entityId] & stateType);
    }

    getPickPriority(entityId: number): TrimbimPickPriority {
        return (this.state[entityId] & this.pickPriorityMask) >> this.pickPriorityOffset;
    }

    setVisibility(visible: boolean, entityIds: ArrayLike<number>): void {
        if (entityIds) {
            for (let i = 0, l = entityIds.length; i < l; i++) {
                const mask = this.state[entityIds[i]];
                this.state[entityIds[i]] = visible ? mask | StateType.VISIBILITY : mask & ~StateType.VISIBILITY;
            }
        }
        else {
            for (let entityId = 0, l = this.state.length; entityId < l; entityId++) {
                const mask = this.state[entityId];
                this.state[entityId] = visible ? mask | StateType.VISIBILITY : mask & ~StateType.VISIBILITY;
            }
        }
    }

    resetVisibility(): void {
        for (let i = 0, l = this.state.length; i < l; i++) {
            this.state[i] = this.state[i] | StateType.VISIBILITY;
        }
    }

    setPickPriority(pickPriority: TrimbimPickPriority, entityIds: ArrayLike<number>): void {
        const priorityValue = pickPriority << this.pickPriorityOffset;
        if (entityIds) {
            for (let i = 0, l = entityIds.length; i < l; i++) {
                const mask = this.state[entityIds[i]];
                this.state[entityIds[i]] = mask & ~this.pickPriorityMask | priorityValue;
            }
        }
        else {
            for (let entityId = 0, l = this.state.length; entityId < l; entityId++) {
                const mask = this.state[entityId];
                this.state[entityId] = mask & ~this.pickPriorityMask | priorityValue;
            }
        }
    }


    resetPickPriority(): void {
        for (let i = 0, l = this.state.length; i < l; i++) {
            this.state[i] = this.state[i] & ~this.pickPriorityMask;
        }
    }

    setSelection(selected: boolean, entityIds: ArrayLike<number>): void {
        for (let i = 0, l = entityIds.length; i < l; i++) {
            const mask = this.state[entityIds[i]];
            this.state[entityIds[i]] = selected ? mask | StateType.SELECTION : mask & ~StateType.SELECTION;
        }
    }

    clearSelection(): void {
        for (let i = 0, l = this.state.length; i < l; i++) {
            this.state[i] = this.state[i] & ~StateType.SELECTION;
        }
    }
}
