import { HitPoint } from "../HitPoint";
import { Frustum, Ray } from "../ThreeMath";
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { IText, TrimbimGeometryType } from "../../common";
import { BatchPrototype } from "../BatchCluster";
import { Trimble } from "../../TrimBim_generated";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

export const normalLength = 32767;

export abstract class GeometryBuilder {
    readonly geometryType: TrimbimGeometryType;
    readonly snapGeometryType: GeometryType;

    protected buffers: GeometryData[] = [];

    constructor(protected geometry: Fbs.ModelGeometry) { }

    protected abstract prepareModelTypes(): void;
    protected abstract calculateBufferSizes(geometryDefinition: any, objectType?: ObjectType): GeometryData;
    protected abstract build(geometryDefinition: any, bufferGeometry: GeometryData): void;

    abstract getGeometries(geometryIndex: number, buildIfEmpty: boolean): GeometryData[];
    abstract pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[];

    abstract pickFace(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[];
    abstract pickLine(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[];
    abstract pickPoint(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[];
}

export interface GeometryData {
    objectType: ObjectType;
    indices?: Uint16Array | Uint8Array | Uint32Array;
    positions?: Float32Array | Float64Array;
    normals?: Int16Array;
    uvs?: Float32Array;
    pointSizes?: Float32Array;
    indicesLength: number;
    positionsLength: number;
    normalsLength: number;
    uvsLength: number;
    pointSizesLength: number;
    materialId?: number;
    indexPointer?: number;
    positionPointer?: number;
    normalPointer?: number;
    uvPointer?: number;
    pointSizePointer?: number;
    text?: IText,
}

export interface ObjectBatchGeometryContainer {
    geometry: GeometryData;
    instanceId?: number;
    start: number;
    batch: BatchPrototype;
}
