import { BatchPrototype } from "./BatchCluster";
import { BatchCluster, GeometryCluster, PointCluster } from "./BatchCluster";
import { InstanceManager } from "./InstanceManager";
import { GeometryManager } from "./GeometryManager";

import { Trimble } from "../TrimBim_generated";
import { GeometryData, ObjectBatchGeometryContainer } from "./GeometryBuilders/GeometryBuilder";
import { ObjectType } from "../Model/ModelBuilderTypes";
import { ModelEntityState } from "./ModelEntityState";
import {
    DisplayGroup,
    mergeGroups,
    subtractGroups
} from "@technology/webgl-viewer/dist/esnext/Rendering/DisplayGroup";
import { LodManager } from "./LodManager";
import { GeometryUpdateMessenger } from "./GeometryUpdateMessenger";
import {LayerManager} from "./LayerManager";

export class ClusterManager {
    private grid_size: number;
    clusterLUT: Map<string, GeometryCluster>;
    private _instanceIdToClusters: Map<number, Set<GeometryCluster>>;
    private _instanceIdToBatches: Map<number, Set<BatchPrototype>>;
    messenger: GeometryUpdateMessenger;

    constructor(
        private modelId: string,
        private _model: Trimble.Technology.TrimBim.Fbs.Model,
        private _im: InstanceManager,
        private _geom: GeometryManager,
        private _lodManager: LodManager,
        private _layerManager: LayerManager,
        private _modelState: ModelEntityState,
        private materialIds: number[]
    ) {
        this.clusterLUT = new Map();
        this._instanceIdToClusters = new Map();
        this._instanceIdToBatches = new Map();
        this.messenger = new GeometryUpdateMessenger(modelId);

        const distance = _im.worldBoundingbox.min.distanceTo(_im.worldBoundingbox.max);

        this.grid_size = this._lodManager.getClusterSize(distance);

        for (let i = 0, length = _im.boundingBoxes.length / 6; i < length; i++) {
            const geometries = _geom.getGeometries(i, false);
            for (const geometry of geometries) {
                const clusterId = this.generateClusterId(geometry, i);
                const cluster = this.getCluster(clusterId);

                expandClusterBoundingBox(cluster, this._im.boundingBoxes, i);
                const objectSize = this.getSize(i);
                const prototype = cluster.insertObject(geometry, i, objectSize);
                this.addInstanceToBatchMapping(i, prototype);

                if (!this._instanceIdToClusters.has(i)) this._instanceIdToClusters.set(i, new Set());
                this._instanceIdToClusters.get(i).add(cluster);
            }
        }
    }

    private addInstanceToBatchMapping(instanceId: number, batch: BatchPrototype): void {
        if (!this._instanceIdToBatches.has(instanceId))
            this._instanceIdToBatches.set(instanceId, new Set());
        this._instanceIdToBatches.get(instanceId).add(batch);
    }

    /**
     * Changes the material for the ids with a non-destructive change
     * @param  {number} materialId
     * @param  {number[]} instanceIds
     */
    public setMaterial(instanceIds: number[], materialId: number): void {
        instanceIds.sort((a, b) => a - b);

        const removed: Map<GeometryCluster, Set<number>> = new Map();
        const added: Set<GeometryCluster> = new Set();

        // Create new clusters
        for (const id of instanceIds) {

            const geometries = this._geom.getGeometries(id, false);
            const oldClusters = this._instanceIdToClusters.get(id);

            for (const oldCluster of oldClusters) {
                if (oldCluster.materialId === materialId) continue;

                removed.has(oldCluster)
                    ? removed.get(oldCluster).add(id)
                    : removed.set(oldCluster, new Set<number>().add(id));

                const newClusterId = oldCluster.id.slice();
                newClusterId[3] = materialId;
                const cluster = this.getCluster(newClusterId);

                oldClusters.delete(oldCluster);
                oldClusters.add(cluster);

                expandClusterBoundingBox(cluster, this._im.boundingBoxes, id);

                this.populateCluster(cluster, id, geometries);

                added.add(cluster);
            }
        }

        added.forEach(cluster => cluster.removeGeometry());

        // Update old clusters
        this.removeInstanceIdsFromClusters(removed);
    }

    public getMaterialId(instanceId: number): number {
        return this._instanceIdToClusters.get(instanceId).values().next().value.materialId;
    }

    getInstanceIds(): number[] {
        return Array.from(this._instanceIdToClusters.keys());
    }

    public calculateRanges(locations: Array<{ geometry: GeometryData; start: number; batch: BatchPrototype }>): DisplayGroup[] {
        const ranges: DisplayGroup[] = [];
        for (const { geometry, start } of locations) {
            const count = geometry.indicesLength !== 0
                ? geometry.indicesLength
                : (geometry.positionsLength / 3);

            ranges.push({ start, count });
        }
        return ranges;
    }

    resetMaterials(instanceIds: number[]): void {
        instanceIds.sort((a, b) => a - b);

        const removed: Map<GeometryCluster, Set<number>> = new Map();
        const added: Set<GeometryCluster> = new Set();

        // Create new clusters
        for (const instanceId of instanceIds) {
            const geometries = this._geom.getGeometries(instanceId, false);
            const clusters = this._instanceIdToClusters.get(instanceId);
            for (const currentCluster of clusters) {
                for (const geometry of geometries) {
                    if (!currentCluster.belongsToCluster(geometry)) continue;
                    if (geometry.positionsLength === 0) continue;

                    const clusterId = this.generateClusterId(geometry, instanceId);
                    const newCluster = this.getCluster(clusterId);
                    if (currentCluster === newCluster) continue;
                    expandClusterBoundingBox(newCluster, this._im.boundingBoxes, instanceId);

                    if (removed.has(currentCluster)) {
                        removed.get(currentCluster).add(instanceId);
                    } else {
                        removed.set(currentCluster, new Set<number>().add(instanceId));
                    }

                    const objectSize = this.getSize(instanceId);
                    const prototype = newCluster.insertObject(geometry, instanceId, objectSize);

                    this.restoreInstanceState(instanceId, prototype, geometry);

                    clusters.delete(currentCluster);
                    clusters.add(newCluster);
                    this.addInstanceToBatchMapping(instanceId, prototype);
                    added.add(newCluster);
                }
            }
        }

        added.forEach(cluster => cluster.removeGeometry());

        // Update old clusters
        this.removeInstanceIdsFromClusters(removed);
    }

    private restoreInstanceState(instanceId: number, batch: BatchPrototype, geometry: GeometryData): void {
        const entityId = this._geom.getEntityId(instanceId);
        if (this._modelState.isSelected(entityId) || !this._modelState.isVisible(entityId) || !this._layerManager.isVisibleByLayer(instanceId)) {
            const locationIndex = batch.instances.indexOf(instanceId);
            const start = batch.indexOffset[locationIndex];
            const location = { batch, geometry, start };

            if (this._modelState.isSelected(entityId))
                batch.state.selected = mergeGroups(batch.state.selected.concat(this.calculateRanges([location])));
            if (!this._modelState.isVisible(entityId) || !this._layerManager.isVisibleByLayer(instanceId))
                batch.state.visible = subtractGroups(batch.state.visible, this.calculateRanges([location]));
        }
    }

    private removeInstanceIdsFromClusters(removeActions: Map<GeometryCluster, Set<number>>): void {
        for (const [cluster, idsToRemove] of removeActions.entries()) {

            const currentIds = cluster.getInstanceIds();
            currentIds.sort((a, b) => a - b);

            cluster.removeGeometry();
            cluster.clearBatches();
            for (const instanceId of currentIds) {
                if (!idsToRemove.has(instanceId)) {
                    const geometries = this._geom.getGeometries(instanceId, false);
                    this.populateCluster(cluster, instanceId, geometries);
                }
            }
        }
    }

    private populateCluster(cluster: GeometryCluster, instanceId: number, geometries: GeometryData[]): void {
        const objectSize = this.getSize(instanceId);
        for (const geometry of geometries) {
            if (cluster.belongsToCluster(geometry)) {
                const prototype = cluster.insertObject(geometry, instanceId, objectSize);
                this.restoreInstanceState(instanceId, prototype, geometry);
                this.addInstanceToBatchMapping(instanceId, prototype);
            }
        }
    }

    private generateClusterId = (() => {
        const aabb = new Float64Array(6);
        let x = 0.0;
        let y = 0.0;
        let z = 0.0;

        const modelGeometry = this._model.geometry();
        const instance = new Trimble.Technology.TrimBim.Fbs.GeometryInstance();

        return (geometry: GeometryData, instanceId: number): Int32Array => {
            const clusterId = new Int32Array(6);
            modelGeometry.instances(instanceId, instance);
            const materialId = isNaN(geometry.materialId) ? this.materialIds[instance.surfaceMaterialId()] : geometry.materialId;

            if ( this._im.boundingBoxes ) {
                x = ( 0.5*( this._im.boundingBoxes[ instanceId*6     ] + this._im.boundingBoxes[ instanceId*6 + 3 ] ) );
                y = ( 0.5*( this._im.boundingBoxes[ instanceId*6 + 1 ] + this._im.boundingBoxes[ instanceId*6 + 4 ] ) );
                z = ( 0.5*( this._im.boundingBoxes[ instanceId*6 + 2 ] + this._im.boundingBoxes[ instanceId*6 + 5 ] ) );
            } else {
                this._im.getInstanceAABB( instanceId, aabb );

                x = ( 0.5*( aabb[ 0 ] + aabb[ 3 ] ) );
                y = ( 0.5*( aabb[ 1 ] + aabb[ 4 ] ) );
                z = ( 0.5*( aabb[ 2 ] + aabb[ 5 ] ) );
            }

            const x_diff = x % this.grid_size;
            const y_diff = y % this.grid_size;
            const z_diff = z % this.grid_size;

            clusterId[ 0 ] = x - x_diff;
            clusterId[ 1 ] = y - y_diff;
            clusterId[ 2 ] = z - z_diff;
            clusterId[ 3 ] = materialId;
            clusterId[ 4 ] = geometry.objectType;
            clusterId[ 5 ] = geometry.text ? geometry.text.checksum : 0;

            return clusterId;
        }
    })();

    public getBatchGeometryByInstanceIds(instanceIds: number[]): Map<number, ObjectBatchGeometryContainer[]> {
        const batches: Map<number, ObjectBatchGeometryContainer[]> = new Map();

        for (const id of instanceIds) {
            const geometries = this._geom.getGeometries(id, false);
            const instanceBatches = this._instanceIdToBatches.get(id);
            for (const batch of instanceBatches) {
                const locationIndex = batch.instances.indexOf(id);

                if (locationIndex === -1)
                    throw new Error("Inconsistency error: selection not found in batch.");

                const start = batch.indexOffset[locationIndex];

                for (const geometry of geometries) {
                    if (this.belongsToBatchPrototype(geometry, batch)) {
                        const location = { geometry: geometry, start: start, batch: batch } as ObjectBatchGeometryContainer;
                        batches.has(batch.id)
                            ? batches.get(batch.id).push(location)
                            : batches.set(batch.id, [location]);
                    }
                }
            }
        }

        return batches;
    }

    private belongsToBatchPrototype(geometry: GeometryData, prototype: BatchPrototype): boolean {
        if (!geometry.text && !prototype.text) return true;
        if (!geometry.text || !prototype.text) return false;
        return geometry.text.checksum === prototype.text.checksum;
    }

    private getSize = (() => {
        const aabb = new Float64Array(6);
        return( i: number ) => {
            let x = 0;
            let y = 0;
            let z = 0;
            if( this._im.boundingBoxes ) {
                x = this._im.boundingBoxes[ i*6 + 3 ] - this._im.boundingBoxes[ i*6     ];
                y = this._im.boundingBoxes[ i*6 + 4 ] - this._im.boundingBoxes[ i*6 + 1 ];
                z = this._im.boundingBoxes[ i*6 + 5 ] - this._im.boundingBoxes[ i*6 + 2 ];
            } else {
                this._im.getInstanceAABB( i, aabb );
                x = aabb[ 3 ] - aabb[ 0 ];
                y = aabb[ 4 ] - aabb[ 1 ];
                z = aabb[ 5 ] - aabb[ 2 ];
            }
            return Math.sqrt( x*x + y*y + z*z );
        }
    })();

    private getCluster(id: Int32Array): GeometryCluster {
        const stringId = `${id[0]},${id[1]},${id[2]},${id[3]},${id[4]},${id[5]}`;

        if (this.clusterLUT.has(stringId)) {
            return this.clusterLUT.get(stringId);
        } else {
            const cluster = this.createCluster(id);
            this.clusterLUT.set(stringId, cluster);

            return cluster;
        }
    }

    private createCluster(id: Int32Array): GeometryCluster {
        if (id[4] === ObjectType.POINT ||id[4] === ObjectType.ATTENUATED_POINT) {
            return new PointCluster(this.modelId, id, this._im, this._geom, this.messenger, this._lodManager);
        } else {
            return new GeometryCluster(this.modelId, id, this._im, this._geom, this.messenger, this._lodManager);
        }
    }

    dispose(): void {
        this.messenger.dispose();
    }
}

export function expandClusterBoundingBox(cluster: BatchCluster, boundingBoxes: Float64Array | number[], i: number ): BatchCluster {
    const min = [0.0, 0.0, 0.0];
    const max = [0.0, 0.0, 0.0];

    min[0] = boundingBoxes[ i*6     ];
    min[1] = boundingBoxes[ i*6 + 1 ];
    min[2] = boundingBoxes[ i*6 + 2 ];

    max[0] = boundingBoxes[ i*6 + 3 ];
    max[1] = boundingBoxes[ i*6 + 4 ];
    max[2] = boundingBoxes[ i*6 + 5 ];

    if (cluster.min === undefined || cluster.max === undefined ) {
        cluster.min = new Float64Array(min);
        cluster.max = new Float64Array(max);
    } else {
        cluster.min[0] = cluster.min[0] < min[0] ? cluster.min[0] : min[0];
        cluster.min[1] = cluster.min[1] < min[1] ? cluster.min[1] : min[1];
        cluster.min[2] = cluster.min[2] < min[2] ? cluster.min[2] : min[2];

        cluster.max[0] = cluster.max[0] > max[0] ? cluster.max[0] : max[0];
        cluster.max[1] = cluster.max[1] > max[1] ? cluster.max[1] : max[1];
        cluster.max[2] = cluster.max[2] > max[2] ? cluster.max[2] : max[2];
    }
    return cluster;
}
