import isMobile from "ismobilejs";

export class LodManager {
    private _isMobile: boolean;
    private lodObjectSizes: Map<number, number>;

    constructor() {
        this.lodObjectSizes = new Map(); // Lod level to size
        this.lodObjectSizes.set(0, 0);
        this.lodObjectSizes.set(1, 0.5);
        this.lodObjectSizes.set(2, 2.0);
    }

    isMobile(): boolean {
        if (this._isMobile === undefined) this._isMobile = isMobile(navigator).any;
        return this._isMobile;
    }

    getLevelsCount(): number {
        return this.lodObjectSizes.size;
    }

    getMinObjectSize(lodLevel: number): number {
        return this.lodObjectSizes.get(lodLevel);
    }

    getLevel(distance: number): number {
        if (distance < (this.isMobile() ? 5 : 30))
            return 0;
        else if (distance < (this.isMobile() ? 50 : 200))
            return 1;
        else
            return 2;
    }

    getClusterSize(modelSize: number): number {
        if (modelSize < 1500) {
            return 25;
        } else {
            return 400;
        }
    }

    getAlignmentClusterSize(): number {
        return 400;
    }
}
