import { GeometryBuilder, GeometryData } from "./GeometryBuilder";
import { LinePrimitiveBuilder } from "./LinePrimitiveBuilder";
import { Trimble } from "../../TrimBim_generated";
import { HitPoint } from "../HitPoint";
import {Frustum, Ray, Vector3} from "../ThreeMath";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { ObjectType } from "../../Model/ModelBuilderTypes";
import { TrimbimGeometryType } from "../../common";
import {
    pickOnSegment,
    pickOnArc,
    get2DArcSectionsCount,
    forEachSegment,
    forEachArc,
    get3DArcSectionsCount
} from "../Pickers";
import {GeometryType} from "@technology/webgl-viewer/dist/esnext/common";

export class GraphicsGeometryBuilder extends GeometryBuilder {
    readonly geometryType = TrimbimGeometryType.GraphicsContainer;
    readonly snapGeometryType = GeometryType.LINE;

    constructor(geometry: Trimble.Technology.TrimBim.Fbs.ModelGeometry) {
        super(geometry);
        this.prepareModelTypes();
    }

    getGeometries(geometryIndex: number, buildIfEmpty: boolean = true): GeometryData[] {
        const geometry = this.buffers[geometryIndex];

        if (!geometry.positions && buildIfEmpty) {
            const line = this.geometry.graphicsContainers(geometryIndex);
            this.build(line, geometry);
        }

        return [geometry];
    }

    public prepareModelTypes(): void {
        for (let i = 0, l = this.geometry.graphicsContainersLength(); i < l; i++) {
            const line = this.geometry.graphicsContainers(i);
            this.buffers.push(this.calculateBufferSizes(line));
        }
    }

    calculateBufferSizes(lines: Fbs.GraphicsContainer): GeometryData {
        const lineSegmentsLength = lines.lineSegmentsLength();
        const arcsLength = lines.arcsLength();
        const totalLength = lineSegmentsLength * 6 + (LinePrimitiveBuilder.getTotalArcSectionsCount(lines) - 2 * arcsLength) * 6 + 6 * arcsLength;

        return {
            objectType: ObjectType.LINE,
            indicesLength: 0,
            positionsLength: totalLength,
            normalsLength: 0,
            uvsLength: 0,
            pointSizesLength: 0,
            indexPointer: 0,
            positionPointer: 0
        };
    }

    build(
        lines: Fbs.GraphicsContainer,
        buffer: GeometryData
    ): void {
        buffer.positions = new Float32Array(buffer.positionsLength);
        buffer.normals = new Int16Array(buffer.normalsLength);

        LinePrimitiveBuilder.buildSegments(lines, buffer);
        LinePrimitiveBuilder.buildArcs(lines, buffer);
    }

    pick(geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] {
        return this.pickLine(geometryIndex, ray, frustum);
    }

    pickFace(geometryIndex: number, ray: Ray): HitPoint[] {
        return;
    }

    pickLine = (() => {
        const hits: HitPoint[] = [];
        const graphics = new Fbs.GraphicsContainer();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            hits.length = 0;
            this.geometry.graphicsContainers(geometryIndex, graphics);

            hits.push(...pickOnSegment(graphics, ray, Infinity));
            hits.push(...pickOnArc(graphics, ray, Infinity, get2DArcSectionsCount));

            return hits.filter(h => frustum.containsPoint(h.point));
        };
    })();

    pickPoint = (() => {
        const hits: HitPoint[] = [];
        const graphics = new Fbs.GraphicsContainer();

        return (geometryIndex: number, ray: Ray, frustum: Frustum): HitPoint[] => {
            hits.length = 0;
            this.geometry.graphicsContainers(geometryIndex, graphics);

            forEachSegment(graphics, (start: Vector3, end: Vector3) => {
                hits.push({ point: start.clone() } as HitPoint);
                hits.push({ point: end.clone() } as HitPoint);
            });

            forEachArc(graphics, (arcStart: Vector3, arcMids: Vector3[], arcEnd: Vector3) => {
                hits.push({ point: arcStart.clone() } as HitPoint);
                hits.push({ point: arcEnd.clone() } as HitPoint);
            }, get3DArcSectionsCount);

            return hits.filter(h => frustum.containsPoint(h.point));
        };
    })();
}
