import { ITextureMessage, ITrimbimMaterialsMessage, MessageType } from "./ThreadWorkerMessages";
import { Trimble } from "../TrimBim_generated";
import Fbs = Trimble.Technology.TrimBim.Fbs;
import { IMetaMaterial } from "../Model/ModelBuilderTypes";
import TextureType = Trimble.Technology.TrimBim.Fbs.TextureType;

declare var self: DedicatedWorkerGlobalScope;

export class MaterialManager {
    private readonly materials: IMetaMaterial[];
    private tmpTexture = new Trimble.Technology.TrimBim.Fbs.Texture();

    constructor(
        private modelId: string,
        private postMessage: (message: any, transfer?: Transferable[]) => void = self.postMessage.bind(self)
    ) {
        this.materials = [];
    }

    addModelTextures(model: Trimble.Technology.TrimBim.Fbs.Model): void {
        const geometry = model.geometry();
        for (let i = 0; i < geometry.texturesLength(); i++) {
            const tex = geometry.textures(i, this.tmpTexture);
            const image = new Int8Array(tex.bytesLength());
            image.set(tex.bytesArray());
            this.postMessage({ type: MessageType.UPDATE_TEXTURE, modelId: this.modelId, textureId: i, image: image } as ITextureMessage, [image.buffer]);
        }
    }

    addModelMaterials(model: Trimble.Technology.TrimBim.Fbs.Model): number[] {
        const materials: IMetaMaterial[] = [];
        const geometry = model.geometry();
        const length = geometry.surfaceMaterialsLength();

        for (let i = 0; i < length; i++) {
            const matDef = geometry.surfaceMaterials(i);
            let textureTransparent = false;

            let textureId = matDef.textureId();
            if (textureId === 4294967295) // Unsigned -1
                textureId = undefined;

            if (textureId !== undefined) {
                const tex = geometry.textures(textureId, this.tmpTexture);
                textureTransparent = tex.type() === TextureType.Png;
            }

            const material: IMetaMaterial = {
                color: matDef.b() | (matDef.g() << 8) | (matDef.r() << 16),
                side: matDef.sidedness(),
                lineStyle: matDef.lineStyle(),
                textureId: textureId,
                opacity: matDef.a() / 255,
                transparent: textureTransparent || matDef.a() < 255
            };

            materials.push(material);
        }
        return this.addMaterials(materials);
    }

    addMaterials(materials: IMetaMaterial[]): number[] {
        const ids: number[] = [];
        const newMaterials: IMetaMaterial[] = [];
        mainLoop: for (const material of materials) {
            for (let i = 0, l = this.materials.length; i < l; i++) {
                if (this.equal(material, this.materials[i])) {
                    ids.push(i); continue mainLoop;
                }
            }
            this.materials.push(material);
            newMaterials.push(material);
            ids.push(this.materials.length - 1);
        }
        this.postMessage({ type: MessageType.UPDATE_MATERIAL, modelId: this.modelId, materials: newMaterials } as ITrimbimMaterialsMessage);
        return ids;
    }

    private equal(m1: IMetaMaterial, m2: IMetaMaterial): boolean {
        return m1.color === m2.color &&
            this.eq(m1.opacity, m2.opacity, 1.0) &&
            this.eq(m1.side, m2.side, Fbs.Sidedness.Single) &&
            this.eq(m1.lineStyle, m2.lineStyle, Fbs.LineStyle.Normal) &&
            m1.textureId === m2.textureId;
    }

    private eq(v1: any, v2: any, def: any): boolean {
        return v1 === v2 ||
            v1 === undefined && v2 === def ||
            v1 === def && v2 === undefined;
    }

    getMaterial(id: number): IMetaMaterial {
        return this.materials[id];
    }
}
