import { ActionType, IAdd, IRemove, ISetBatchState, IUpdateFinished } from "../Model/ModelBuilderTypes";
import { MessageType, UpdateGeometryMessage } from "./ThreadWorkerMessages";

export class GeometryUpdateMessenger  {
    private actions: Array<IAdd | IRemove | ISetBatchState | IUpdateFinished> = [];
    private intervalId: any;

    constructor(private modelId: string) {
        this.intervalId = setInterval(() => this.update(), 50);
    }

    send(action: IAdd | IRemove | ISetBatchState | IUpdateFinished): void {
        this.actions.push(action);
        if (this.actions.length > 20) this.update();
    }

    private update(): void {
        if (this.actions.length === 0) return;

        const message = {
            type: MessageType.UPDATE_GEOMETRY,
            modelId: this.modelId,
            actions: this.actions
        } as UpdateGeometryMessage;
        this.actions = [];

        const transfer = [];
        for (const action of message.actions) {
            if (action.actionType === ActionType.ADD) {
                if (action.positions) transfer.push(action.positions.buffer);
                if (action.indices) transfer.push(action.indices.buffer);
                if (action.normals) transfer.push(action.normals.buffer);
                if (action.uvs) transfer.push(action.uvs.buffer);
                if (action.pointSizes) transfer.push(action.pointSizes.buffer);
            }
        }

        postMessage(message, transfer);
    }

    removePendingUpdateFinishedAction(): void {
        this.actions = this.actions.filter(a => a.actionType !== ActionType.UPDATE_FINISHED);
    }

    dispose(): void {
        clearInterval(this.intervalId);
    }
}
