import { MaterialManager } from "../MaterialManager";
import { ClusterManager } from "../ClusterManager";
import { MessageType, UpdateGeometryMessage } from "../ThreadWorkerMessages";
import { InstanceManager } from "../InstanceManager";
import { ActionType, IMetaMaterial, ISetBatchState } from "../../Model/ModelBuilderTypes";
import { mergeGroups, subtractGroups } from "@technology/webgl-viewer/dist/esnext/Rendering/DisplayGroup";
import { ModelEntityState } from "../ModelEntityState";
import { TrimbimPickPriority } from "../../common";
import { LodManager } from "../LodManager";
import { BatchPrototype } from "../BatchCluster";
import { LayerManager } from "../LayerManager";
import {GeometryManager} from "../GeometryManager";

export class ModelStateManager {
    private hoveredInstanceIds: number[];

    constructor(
        private modelId: string,
        private materialManager: MaterialManager,
        private clusterManager: ClusterManager,
        private instanceManager: InstanceManager,
        private lodManager: LodManager,
        private layerManager: LayerManager,
        private state: ModelEntityState,
        private geometryManager: GeometryManager,
    ) {
    }

    public resetMaterials(entityIds: number[]): void {
        const instanceIds = entityIds ? this.entityIdsToInstanceIds(entityIds) : this.clusterManager.getInstanceIds();
        this.clusterManager.resetMaterials(instanceIds);
    }

    getMaterials(entityIds: number[]): IMetaMaterial[] {
        return entityIds.map(entityId => {
            const instanceId = this.entityIdsToInstanceIds([entityId])[0];
            if (instanceId === undefined) return undefined;
            const materialId = this.clusterManager.getMaterialId(instanceId);
            return materialId !== undefined ? this.materialManager.getMaterial(materialId) : undefined;
        })
    }

    public setMaterial(entityIds: number[], material: IMetaMaterial): void {
        const instanceIds = entityIds ? this.entityIdsToInstanceIds(entityIds) : this.clusterManager.getInstanceIds();

        // copy some parameters from original materials
        const newMaterials: IMetaMaterial[] = [];
        for (const instanceId of instanceIds) {
            const oldMatId = this.clusterManager.getMaterialId(instanceId);
            const oldMat = this.materialManager.getMaterial(oldMatId);
            const newMat = Object.assign({}, material) as IMetaMaterial;
            if (newMat.color === undefined) newMat.color = oldMat.color;
            if (newMat.opacity === undefined) newMat.opacity = oldMat.opacity;
            if (newMat.transparent === undefined) newMat.transparent = oldMat.transparent;
            if (newMat.side === undefined) newMat.side = oldMat.side;
            if (newMat.textureId === undefined) newMat.textureId = oldMat.textureId;
            newMaterials.push(newMat);
        }
        const materialIds = this.materialManager.addMaterials(newMaterials);

        const groups = instanceIds.reduce((map: Map<number, number[]>, instanceId: number, i: number) => {
            const matId = materialIds[i];
            if (!map.has(matId)) map.set(matId, []);
            map.get(matId).push(instanceId);
            return map;
        }, new Map<number, number[]>());
        groups.forEach((iids: number[], matId: number) => this.clusterManager.setMaterial(iids, matId));
    }

    public clearSelection(): void {
        this.state.clearSelection();

        for (const cluster of this.clusterManager.clusterLUT.values()) {
            for (let lodLevel = 0; lodLevel < this.lodManager.getLevelsCount(); lodLevel++) {
                for (const batch of cluster.getBatches(lodLevel)) {
                    if (batch.state.selected.length > 0) {
                        batch.state.selected = [];
                        this.postSetStateMessage(batch);
                    }
                }
            }
        }
    }

    private postSetStateMessage(batch: BatchPrototype): void {
        const stateUpdate: UpdateGeometryMessage = {
            type: MessageType.UPDATE_GEOMETRY,
            modelId: this.modelId,
            actions: [{
                actionType: ActionType.SET_BATCH_STATE,
                modelId: this.modelId,
                batchId: batch.id,
                state: batch.state
            } as ISetBatchState]
        };
        postMessage(stateUpdate);
    }

    public resetVisibility(): void {
        this.state.resetVisibility();

        for (const cluster of this.clusterManager.clusterLUT.values()) {
            for (let lodLevel = 0; lodLevel < this.lodManager.getLevelsCount(); lodLevel++) {
                for (const batch of cluster.getBatches(lodLevel)) {
                    if (batch.state.visible.length === 1 && batch.state.visible[0].start === 0 && batch.state.visible[0].count === Infinity)
                        continue;
                    batch.state.visible = [{ start: 0, count: Infinity }];
                    this.postSetStateMessage(batch);
                }
            }
        }
    }

    public setSelection(entityIds: number[]): void {
        // TODO: make a diff
        this.clearSelection();
        this.state.setSelection(true, entityIds);

        const instanceIds = this.entityIdsToInstanceIds(entityIds);
        const batchMap = this.clusterManager.getBatchGeometryByInstanceIds(instanceIds);
        for (const [batchId, locations] of batchMap) {
            const batch = locations[0].batch;
            batch.state.selected = mergeGroups(this.clusterManager.calculateRanges(locations));
            this.postSetStateMessage(batch);
        }
    }

    public setHovered(entityIds: number[]): void {
        if (!entityIds) {
            this.clearHovered();
            return;
        }

        const instanceIds = this.entityIdsToInstanceIds(entityIds);
        this.hoveredInstanceIds = instanceIds;
        const batchMap = this.clusterManager.getBatchGeometryByInstanceIds(instanceIds);
        for (const [batchId, locations] of batchMap) {
            const batch = locations[0].batch;
            batch.state.hovered = mergeGroups(this.clusterManager.calculateRanges(locations));
            this.postSetStateMessage(batch);
        }
    }

    private clearHovered(): void {
        if (!this.hoveredInstanceIds) return;

        const batchMap = this.clusterManager.getBatchGeometryByInstanceIds(this.hoveredInstanceIds);
        for (const [batchId, locations] of batchMap) {
            const batch = locations[0].batch;
            batch.state.hovered = undefined;
            this.postSetStateMessage(batch);
        }
        this.hoveredInstanceIds = undefined;
    }

    setVisibility(entityIds: number[], visible: boolean): void {
        this.state.setVisibility(visible, entityIds);
        let instanceIds = entityIds ? this.entityIdsToInstanceIds(entityIds) : this.clusterManager.getInstanceIds();
        if (visible) instanceIds = instanceIds.filter(id => this.layerManager.isVisibleByLayer(id));
        this.applyVisibility(instanceIds, visible);
    }

    setLayerVisibility(layer: string, visible: boolean): void {
        this.layerManager.setLayerVisibility(layer, visible);
        const instanceIds = visible ? this.layerManager.getLayerInstances(layer).filter(id =>
            this.state.isVisible(this.geometryManager.getEntityId(id))
        ) : this.layerManager.getLayerInstances(layer);
        this.applyVisibility(instanceIds, visible);
    }

    private applyVisibility(instanceIds: number[], visible: boolean): void {
        const batchMap = this.clusterManager.getBatchGeometryByInstanceIds(instanceIds);
        for (const [batchId, locations] of batchMap) {
            const batch = locations[0].batch;

            const ranges = this.clusterManager.calculateRanges(locations);
            if (visible)
                batch.state.visible = mergeGroups(batch.state.visible.concat(ranges));
            else
                batch.state.visible = subtractGroups(batch.state.visible, ranges);
            this.postSetStateMessage(batch);
        }
    }

    public setPickPriority(entityIds: number[], pickPriority: TrimbimPickPriority): void {
        this.state.setPickPriority(pickPriority, entityIds);
    }

    public resetPickPriority(): void {
        this.state.resetPickPriority();
    }

    private entityIdsToInstanceIds(entityIds: number[]): number[] {
        const instanceIds = [];
        for (let i = 0, l = entityIds.length; i < l; i++) {
            if (this.instanceManager.entityToInstance.has(entityIds[i]))
                instanceIds.push(...this.instanceManager.entityToInstance.get(entityIds[i]));
        }
        return instanceIds;
    }
}
